@import 'src/styles/includes';

.tageditor {
  max-width: 80%;
  margin-top: var(--s8);

  textarea:focus,
  input:focus {
    outline: none;
  }

  .title {
    color: var(--project-color-main);
    font-weight: var(--font-weight-medium);
    margin-bottom: var(--s1);
  }

  .description {
    color: var(--input-color);
    font-size: var(--font-size-s);
  }

  .instructions {
    color: var(--input-color);
    font-size: var(--font-size-s);
    padding-top: 0.25rem;
  }

  .icon-yellow,
  .icon-white,
  .icon {
    margin: 0 var(--s2) 0 0;
  }

  .input-field-container {
    border-radius: var(--border-radius);
    border-width: var(--s1px);
    color: var(--input-color);
    display: flex;
    flex-direction: column;
    margin-bottom: var(--s5);
    margin-top: var(--s4);
    min-width: rem(276);
    position: relative;
    width: 50%;

    ::placeholder {
      color: var(--input-color-placeholder) !important;
      opacity: 1;
    }

    .active ~ .multi-select-container {
      border-color: var(--tageditor-border-color);
      border-top-width: 0;
      top: var(--s12);

      .ui-checkbox {
        align-items: flex-start;
        margin-bottom: 0;
        padding: var(--s3);

        &:hover {
          background: var(--tageditor-checked-background);
          cursor: pointer;
        }

        .checkbox ~ .checkbox-label-text {
          padding-left: var(--s3);
          padding-top: var(--s2px);
        }
      }

      .ui-checkbox-checked {
        background: var(--tageditor-checked-background);
      }
    }

    .multi-select-container {
      .scrollable {
        @include scrollbars(var(--s1));
        padding-top: var(--s3);
        overflow-y: auto;
      }

      .multi-select-action-button-container {
        border-top-color: var(--tageditor-border-color);
        border-top-width: var(--s1px);
      }

      .ui-button {
        font-size: var(--font-size-s);
        line-height: var(--lineheight-xs);
        font-weight: var(--font-weight-medium);
        padding: var(--s2);
      }
    }

    @include query($md) {
      &[data-tags-right='true'] {
        display: inline-block;
      }
    }

    &.focus {
      border-color: var(--tageditor-border-color-active);
      outline: none;
    }

    .filter-search-icon {
      position: absolute;
      left: var(--s3);
      height: 100%;
    }

    .input-field {
      background: var(--tageditor-background-color);
      border: var(--s1px) solid var(--tageditor-border-color);
      border-radius: var(--border-radius);
      color: var(--input-color);
      font-size: var(--font-size-regular);
      font-weight: var(--font-weight-regular);
      height: var(--s12);
      line-height: var(--input-lineheight);
      padding-left: var(--s10);
      width: 100%;

      &.active {
        border-width: var(--s2px);
      }

      &:-webkit-autofill {
        caret-color: var(--input-color);
        margin-bottom: 0;
        margin-top: 0;
        padding-bottom: 0;
      }

      &.focus-visible {
        box-shadow: none;
      }

      &.active,
      &:focus {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        border-color: var(--tageditor-border-color-active);
      }
    }

    &.focus .text-input {
      border: var(--border-size-standard) solid var(--button-primary-color);
    }

    .has-icon-left {
      bottom: var(--s9);
      height: var(--s10);
      left: var(--s3);
      pointer-events: none;
      position: absolute;
      width: var(--s10);
      z-index: 4;
    }

    .has-icon-left + .text-input {
      padding-left: var(--s10);
    }
  }

  .a-form-error {
    color: var(--color-error);
    line-height: 1.5;
    background-color: transparent;
    display: block;
  }

  .pills {
    @include query($md) {
      &[data-tags-right='true'] {
        display: inline-block;
        padding-left: var(--s3);
      }
    }

    .clearable-display-pill {
      color: var(--color-black);

      button {
        &:active,
        &:focus,
        &:hover {
          background-color: transparent;
        }
      }
    }
  }
}
