@import 'src/styles/includes';

.page-container {
  background-color: var(--background-primary);
  border-right: var(--border-standard);
}

.form-content-wrapper {
  @include query($sm, 'max') {
    h1 {
      font-size: var(--font-size-xl);
    }

    .tageditor {
      max-width: 100%;

      .input-field-container {
        width: 100%;
      }
    }
  }

  .input-container {
    margin-bottom: var(--s7);

    .edit-icon {
      height: 100%;
      margin-top: 0;
    }
  }
}

.list-table-subrow {
  .title-cell {
    .library-item-checkbox[type='checkbox'] ~ label {
      align-items: center;
      display: flex;
      justify-content: flex-start;
      margin-bottom: 0;
      margin-right: var(--s4);
      padding: 0;
    }
  }
}
