@import 'src/styles/includes';

[data-appbanner='open'] main {
  margin-top: var(--app-banner-height-offset);

  .app-banner-content {
    left: 0;
    top: 0;

    .button-link {
      color: var(--color-black);
    }
  }
}

.disclaimer-modal {
  .disclaimer-modal-title {
    text-align: center;
    padding: var(--s9) var(--s9) 0 var(--s9);
  }

  .disclaimer-modal-body {
    max-height: 50vh;
    overflow-y: auto;
    padding: 0 var(--s12) var(--s12) var(--s12);
  }
}

.structured-analysis-view {
  @extend %max-content-width;

  .structured-table.scrollable {
    cursor: default;
  }
}
