@import '../../../styles/includes';

.project-role-user-pill {
  padding: var(--s2) 0;
  border-radius: var(--s4);
  margin-right: var(--s3);
  margin-bottom: var(--s3);
  font-size: var(--font-size-s);
  font-weight: var(--font-weight-medium);
  white-space: nowrap;
  display: grid;
  grid-template-columns: 30px 1fr 40px;
  row-gap: var(--s1);
  align-items: flex-end;
  line-height: var(--font-size-s);
  color: var(--color-black);
  margin-left: 0;

  &:not(.pwc-user) {
    color: var(--color-black);
  }

  &.pending {
    color: var(--color-black);
    background-color: var(--color-grey-light);

    &.pending-approval {
      background-color: var(--color-grey);
    }

    &.pending-approved {
      background-color: var(--input-alert-color);
    }
  }

  &.with-toggle {
    grid-template-columns: 30px var(--s9) 1fr 40px;

    .toggle-container {
      margin-bottom: -3px;

      .toggle-checkbox {
        margin: 0;
      }

      input:checked + label .toggle-background {
        background: var(--toggle-background);
        border-color: var(--toggle-background);

        &::before {
          left: 14px;
        }
      }

      .toggle-background {
        height: calc(var(--s4) - 2px);
        width: var(--s6);
        &::before {
          height: 10px;
          width: 10px;
          margin-top: 2px;
        }
      }
    }
  }

  & > span {
    overflow: hidden;
    text-overflow: ellipsis;
  }

  button {
    border: 0;
    background-color: transparent;
    line-height: var(--font-size-s);
    align-self: center;
  }
}
