.library-items-list-data {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  justify-content: flex-start;
  background-color: var(--background-default);
  padding: var(--s6);
  height: 100%;

  &__button {
    margin: 0 0 var(--s4) 0;
    display: flex;
    align-items: center;

    &__text {
      margin-left: var(--s3);
    }
  }

  &__droppable-container {
    min-height: 50%;
    display: flex;
    flex: 1 1 auto;
    overflow: hidden;
    &__list {
      padding: var(--s2px) calc(var(--s2) + var(--s2px)) 0 0;
      display: flex;
      flex-direction: column;
      flex: 1 1 auto;
      max-width: 100%;
      overflow-y: scroll;

      &::-webkit-scrollbar {
        width: calc(var(--s1) + var(--s2px));
      }

      &::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 calc(var(--s1) + var(--s2px))
          rgba(0, 0, 0, 0.3);
        box-shadow: inset 0 0 calc(var(--s1) + var(--s2px)) rgba(0, 0, 0, 0.3);
      }

      &::-webkit-scrollbar-thumb {
        background-color: var(--color-grey-medium);
        border-radius: var(--s3px);
      }
    }
  }

  .library-item-card {
    min-width: 100%;
  }
}
