@import 'src/styles/includes';

.files {
  h6 {
    margin-bottom: var(--s4);
  }
  .file {
    position: relative;
    background-color: var(--color-grey-darker);
    border-radius: var(--s2px);
    padding: var(--s3) var(--s13);
    padding-left: var(--s3);
    border: var(--s1px) solid var(--color-grey-medium);

    .mini-loader-contain .mini-loader-holder svg {
      right: var(--s10);
      height: var(--s6);
      top: var(--s3);
    }

    svg {
      position: absolute;
      top: 0;
      right: var(--s3);
    }

    svg.icon.icon-error.fill {
      path.fill {
        fill: var(--icon-red-error);
      }
      path.fill.variant {
        fill: var(--color-grey-darker);
      }
    }

    svg.action-icon.outline.icon-orange-theme.with-hover:hover {
        path.fill {
          fill: var(--color-orange-theme);
        }

        path.outline {
          fill: transparent;
        }
    }

    svg.action-icon.outline.icon-orange-theme path.fill {
      outline: var(--color-orange-theme);
    }

    .icon-error {
      left: var(--s3);
    }
  }

  .file-upload-error {
    padding-left: var(--s13);
    border: var(--s2px) solid var(--icon-red-error);
  }

  .action-icon {
    cursor: pointer;
  }
}