@import 'src/styles/includes';

.edit-layout {
  display: grid;
  grid-template-columns: auto minmax(
      calc(var(--s25) * 2),
      calc((var(--s25) * 3) + var(--s11) + var(--s1px))
    );
  @include query($md, max) {
    grid-template-columns: minmax(calc((var(--s25) * 4) + var(--s5)), auto) minmax(
        calc(var(--s25) * 2),
        calc((var(--s25) * 3) + var(--s11) + var(--s1px))
      );
  }

  &--collapsed-column {
    grid-template-columns: auto minmax(64px, 5%);
  }

  &__left {
    flex: 1;
  }

  &__right {
    border-left-color: var(--border-primary);
    border-left-width: var(--s1px);
    border-left-style: solid;

    & .ui-tooltip.ui-tooltip-top .ui-tooltip-tip {
      bottom: calc(100% + var(--s2) + var(--s1px));
    }
  }

  &--mobile {
    display: flex;
    flex-direction: column;
    grid-template-columns: unset;

    .edit-layout__left {
      .left-panel {
        margin: 0;
      }
    }

    .edit-layout__bottom {
      padding: var(--content-spacing) var(--container-spacing);
    }
  }
}
