@import 'src/styles/includes';

.project-user-form {
  .project-user-add-header {
    max-width: 600px;

    h3 {
      font-weight: var(--font-weight-normal);
      font-size: var(--font-size-l);
    }

    & a.orange-link {
      color: var(--color-orange-theme);
      text-decoration: underline;
    }
  }

  .project-user-grid-headings {
    display: grid;
    //    grid-template-columns: 1fr 33% 20%;
    grid-template-columns: 296px 1fr;
    column-gap: var(--s6);
    grid-auto-flow: row;
    align-items: center;
    color: var(--project-color-secondary);
    padding: var(--s4) 0;
    // padding-top: var(--s4);
    // margin-bottom: var(--s4);

    @media screen and (max-width: 767px) {
      display: none;
    }
  }

  button {
    white-space: nowrap;
  }
}
