@import 'src/styles/includes';

.loader-container {
  max-height: var(--s15);
}

.no-reports-span {
  padding-left: var(--s1);
}

.no-results-header {
  display: flex;
  flex-direction: row;
  margin: var(--s4) 0px;

  p {
    display: inline-flex;
    align-items: center;
    margin-bottom: 0px;
    margin-left: var(--s4);
    font-size: var(--font-size-l);
    color: var(--headings-primary);
  }

  .no-results-title {
    font-weight: var(--font-weight-bold);
  }
}

.no-results-body {
  font-size: var(--font-size-regular);
  color: var(--breadcrumb-text);

  a {
    color: var(--color-orange-theme);
    text-decoration: underline;
  }

  .ui-button.button-link {
    color: var(--color-orange-theme);
    text-decoration: none;
  }

  .refresh-button {
    align-items: center;
    display: flex;
    .icon {
      margin-right: var(--s3);
    }
  }
}
