@import 'src/styles/includes';

.project-overview {
  @extend %max-content-width;

  background-color: var(--color-page-background);
  padding: var(--content-spacing) var(--container-spacing) var(--s12);

  h2 {
    color: var(--project-color-main);
    display: inline-block;
    font-size: var(--font-size-xl);
  }

  .project-container {
    .project-panel-columns {
      display: flex;

      @include query($sm, max) {
        flex-direction: column;
      }

      > div {
        flex: 1 1 50%;
      }
    }

    .user-details-container {
      display: flex;
    }

    @include query($sm, max) {
      .project-title {
        font-size: var(--font-size-xl);
        line-height: var(--lineheight-xl);
      }
    }
  }
}

.project-overview-module-container {
  .project-overview-module {
    @include query($md, max) {
      display: flex;
      flex-direction: column;
    }
  }
}

.project-overview-subpanel-container {
  display: grid;
  grid-gap: var(--s6);

  @include query(1024) {
    grid-template-columns: repeat(3, 1fr);
  }
}

.project-overview-subpanel {
  display: flex;
  align-content: center;
  justify-content: space-between;
  border-radius: 2px;
  background-color: var(--background-primary);
  box-shadow: var(--table-shadow);
  padding: var(--content-spacing);

  .nowrap {
    white-space: nowrap;
  }

  .title {
    color: var(--card-link-color);
    font-size: var(--font-size-l);
    font-weight: var(--font-weight-bold);
    line-height: var(--lineheight-l);

    @include query($md) {
      @include query($lg, 'max') {
        font-size: var(--font-size-regular);
      }
    }

    @include query($md, 'max') {
      font-size: var(--font-size-l);
    }

    @include query($xs, max) {
      font-size: var(--font-size-regular);
    }
  }

  .addbutton {
    float: right;
    white-space: nowrap;

    @include query($md) {
      @include query($lg, 'max') {
        transform: scale(0.8);
      }
    }
  }

  .add-link:hover svg > path {
    fill: var(--color-orange);
  }

  .title-icon {
    margin-right: var(--s3);
    padding-bottom: var(--s2);

    @include query($md, 'max') {
    }
  }
}
