@import 'src/styles/includes';

.project-create {
  h1 {
    margin-bottom: var(--s12);
  }
  .separator {
    min-height: 0;
    margin: 0 0 var(--s5) 0;
  }

  .stepindicator-container {
    width: 100%;
    max-width: 700px;
    margin-top: 0;
    margin-bottom: var(--s12);
  }

  .step-1-form {
    width: 100%;
    max-width: 700px;

    .select-container {
      width: calc(100% + var(--s5));

      select {
        width: calc(100% - var(--s5));
        height: 60px;
      }
    }
  }

  .text-input-fieldset .text-input-container.basic label {
    padding: var(--s2) 0 var(--s1) 0;
    margin-bottom: 0;
  }

  .clearable-display-pill span {
    color: var(--color-black);
  }

  .a-form-error {
    margin-bottom: var(--s2);
  }

  .link-projects-list {
    display: flex;
    justify-content: flex-end;
  }
}
