@import 'src/styles/includes';

.hypothesis-main-content {
  max-width: $content-width;

  .section-name {
    color: var(--project-color-secondary);
    margin-top: var(--s8);
  }

  .client-view-conclusion {
    border: 0.5px solid var(--color-orange);
    border-left: 5px solid var(--color-orange);
    padding: 5px 20px;
    margin-top: 20px;
    background-color: var(--background-disabled);
  }

  .key-focus-area {
    color: var(--project-color-main);
    font-size: var(--font-size-xl);
    margin-top: var(--s4);
    margin-bottom: var(--s1);
  }

  .page-last-updated {
    margin-bottom: var(--s3);
  }
}

.hypothesis-top-bar-container {
  border-bottom: var(--border-standard);
  padding: 0 var(--container-spacing);

  .hypothesis-top-bar {
    align-items: center;
    display: flex;
    flex-direction: row;
    height: var(--s16);
    justify-content: space-between;

    @include query($xs, 'max') {
      align-items: flex-start;
      flex-direction: column;
      padding-left: 0;

      .hypothesis-pills {
        flex-wrap: wrap;
        margin-bottom: var(--s4);

        .display-pill {
          color: var(--text-primary);
          margin-bottom: 0.2rem;
          margin-top: 0.2rem;
        }
      }

      > div:first-of-type {
        margin-bottom: var(--s4);
      }
    }

    @include query($md) {
      height: var(--s16);
      padding-right: 0;
    }

    & > div {
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }

    & > div:not(:first-of-type) > button {
      margin: 0 var(--s2);
    }

    & > div:not(:first-of-type) > button:last-of-type {
      margin-right: 0;
    }

    @include query(854, 'max') {
      & > div:not(:first-of-type) > button {
        margin-right: var(--s2);
      }
    }
  }
}
