@import 'src/styles/includes';

.analyses-and-insights-details {
  grid-column-start: 2;
  grid-column-end: 4;

  @include query($md, max) {
    display: none;
  }

  .analyses-and-insights-table {
    display: flex;
    flex-direction: column;
    width: 100%;

    .analyses-and-insights-table-header {
      display: flex;
      flex-direction: column;
    }

    .analyses-and-insights-table-body {
      display: block;
      flex-direction: column;
    }

    .analyses-and-insights-header-row {
      display: flex;
      flex-direction: row;
    }

    .connected-content-heading {
      color: var(--project-color-secondary);
      width: 50%;

      &.orange {
        color: var(--color-orange-theme);
      }

      &.gray {
        color: var(--color-gray);
      }
    }

    .insights-header-cell {
      display: block;
      width: calc(50% - var(--content-spacing));
      padding: 0 var(--content-spacing) 0 0;
      margin-left: var(--content-spacing);
    }

    .analyses-header-cell {
      display: block;
      width: 50%;
      padding: 0 var(--content-spacing);
    }

    .insights-header {
      display: block;
      width: 100%;
    }

    .analyses-header {
      display: block;
      width: 100%;
    }

    .insights-table-row {
      display: flex;
      flex-direction: row;

      .kfa-connected-insight-title,
      .kfa-connected-insight-body {
        @extend %clamp-2;
        color: var(--project-color-secondary);
      }

      .kfa-connected-insight-title:last-child {
        margin-bottom: 0;
      }

      .connected-insight-cell {
        display: block;
        width: calc(50% - var(--content-spacing));
        padding: var(--content-spacing) var(--content-spacing)
          var(--content-spacing) 0;
        margin-left: var(--content-spacing);
        border-bottom: var(--kfa-border);
      }

      .analyses-connected-to-insight-cell {
        border-bottom: var(--kfa-border);
      }

      &:last-child {
        .connected-insight-cell {
          padding-bottom: 0;
          border-bottom: none;
        }

        .analyses-connected-to-insight-cell {
          padding-bottom: 0;
          border-bottom: none;
        }
      }

      th,
      td {
        vertical-align: top;
      }

      .kfa-connected-insight-title-link {
        display: block;
        font-size: var(--font-size-l);
        line-height: var(--lineheight-l);
        color: var(--project-color-secondary);
        font-weight: var(--font-weight-bold);

        &:hover {
          .theme-dark & {
            color: var(--color-white);
          }
        }
      }

      .kfa-connected-insight-body .rich-text-editor-intro {
        font-size: var(--font-size-regular);
        font-weight: var(--font-weight-regular);
        line-height: var(--lineheight-regular);
      }
    }

    .kfa-directly-connected-analyses-row {
      display: flex;

      &:last-child {
        .kfa-connected-analysis-title-cell {
          padding-bottom: 0;
        }
      }
    }

    .kfa-connected-analysis-empty-cell {
      display: block;
      width: 50%;
    }

    .kfa-connected-analysis-title-cell {
      display: block;
      width: calc(50% - var(--content-spacing));
      padding: var(--content-spacing) 0 var(--content-spacing)
        var(--content-spacing);
      margin-right: var(--content-spacing);

      .analysis-title-heading {
        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    .analysis-title-link {
      display: block;
      color: var(--project-color-secondary);
      font-size: var(--font-size-regular);
      font-weight: var(--font-weight-medium);
      line-height: var(--lineheight-regular);

      &:hover {
        .theme-dark & {
          color: var(--color-white);
        }
      }
    }
  }
}
