@import 'src/styles/includes';

.add-library-items-box-container {
  .card {
    display: flex;
    background-color: var(--input-background);
    margin-top: var(--s4);
    padding: var(--content-spacing);
    margin-bottom: var(--s12);

    & > div > a {
      color: var(--color-orange-theme);
      display: flex;
      align-items: center;
      float: right;

      .edit-data-icon {
        margin-right: 0.7em;
        color: var(--color-orange-light);
      }

      .as-button {
        background-color: var(--button-primary-color);
        color: var(--color-white);
        display: inline-block;
        padding: var(--s2) var(--s4);
        border-radius: var(--s2px);
      }
      .as-button:hover {
        background-color: var(--button-primary-color-hover);
        color: var(--color-white);
      }
    }

    & > div > a.button-disabled {
      pointer-events: none;
      text-decoration: none;
      .as-button {
        background: var(--button-primary-color-disabled);
        border-color: var(--button-primary-color-disabled);
        color: var(--color-grey-medium);
      }
    }

    .card-content {
      margin-bottom: var(--s4);
    }

    & .title {
      font-size: var(--font-size-l);
      font-weight: var(--font-weight-bold);
      margin-bottom: var(--s4);
      margin-top: var(--s2);
    }
  }
}
.layout-saved-icon {
  margin-right: 0.7em;
  vertical-align: text-top;
}
.library-item-card-container {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: baseline;
}
.editdatalink {
  a:hover {
    color: var(--color-orange);
  }
}
