.ck.ck-button.ck-resolve-button {
  border: var(--s1px) solid var(--color-orange-theme);
  color: var(--color-orange-theme);
}

.ck-thread {
  padding: var(--s3);
}

.ck-thread-top-bar {
  padding-bottom: var(--s1);
  display: flex;
  justify-content: space-between;

  .ck-share-toggle {
    .ck-button__toggle {
      margin-left: 10px;
    }
  }

  .ck.ck-button.ck-switchbutton.ck-on .ck-button__toggle__inner {
    margin-left: auto;
  }

  .ck.ck-button.ck-switchbutton.ck-off {
    .ck-button__toggle {
      display: inline-block;
      border-radius: var(--s5);
      width: var(--s12);
      padding: var(--s2px);
      background: var(--color-grey-dark);

      border: var(--s1px) solid var(--color-grey-alt);

      &:hover {
        background: var(--color-grey-dark);
      }
    }

    .ck-button__toggle__inner {
      border-radius: 50% !important;
      height: var(--s5);
      width: var(--s5);
    }
  }

  .ck.ck-button.ck-switchbutton.ck-on {
    .ck-button__toggle {
      display: inline-block;
      border-radius: var(--s5);
      width: var(--s12);
      padding: var(--s2px);
      background: var(--color-orange);

      &:hover {
        background: var(--color-orange);
      }
    }

    .ck-button__toggle__inner {
      border-radius: 50% !important;
      height: var(--s5);
      width: var(--s5);
    }
  }
}

.ck.ck-button.ck-comment--edit,
.ck.ck-button.ck-comment--remove {
  height: 25px;
  width: 25px;
  &.ck-disabled {
    color: var(--color-grey-alt);
  }
}

.ck-comment__input-actions--cancel {
  --ck-color-button-cancel: var(--color-grey-alt);
}

.ck-comment__input-actions--submit {
  --ck-color-button-save: var(--color-orange-theme);
}

.ck-comment {
  &.comment-client-user .ck.ck-user.ck-comment__user {
    background-color: var(--color-yellow);

    .ck-user__name {
      color: var(--color-grey-dark);
    }
  }

  &.comment-pwc-user .ck.ck-user.ck-comment__user {
    background-color: var(--color-pink);

    .ck-user__name {
      color: var(--color-white);
    }
  }

  &.comment-unknown-user .ck.ck-user.ck-comment__user {
    background-color: var(--color-grey);

    .ck-user__name {
      color: var(--color-grey-dark);
    }
  }
}

.current-user-is-client .ck-comment__input-container .ck.ck-user {
  background-color: var(--color-yellow);

  .ck-user__name {
    color: var(--color-grey-dark);
  }
}

.current-user-is-pwc .ck-comment__input-container .ck.ck-user {
  background-color: var(--color-pink);

  .ck-user__name {
    color: var(--color-white);
  }
}
