@import 'src/styles/includes';

div.multi-select-container {
  background: var(--tageditor-background-color);
  border: var(--s1px) solid var(--input-border);
  border-radius: var(--border-radius);
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-top: none;
  font-size: var(--font-size-regular);
  font-weight: var(--font-weight-regular);
  line-height: var(--input-lineheight);
  position: absolute;
  width: 100%;
  z-index: 100;

  &.loading {
    padding-top: var(--s6);
    padding-bottom: var(--s6);
  }

  & > .scrollable {
    overflow-y: scroll;
    max-height: rem(450);
    min-height: var(--s12);
    position: relative;
  }

  & > div > div {
    padding: var(--s3);
  }

  .action-button-container {
    border-top: var(--border-size-standard) solid var(--input-border);

    & > button {
      margin: var(--s1);
      padding: var(--s3);
    }
  }
}
