@import 'src/styles/includes';

.comment-panel-closed {
  width: var(--s8);
  @include query($sm, max) {
    width: var(--s3);
  }
}

.comment-panel-container {
  --ck-color-base-text: var(--text-primary);
  --ck-color-comment-background: var(--color-page-background);
  --ck-user-avatar-background: var(--input-alert-color);
  --ck-color-comments-info: var(--text-primary);
  --ck-color-annotation-info: var(--text-primary);
  --ck-color-comment-separator: var(--color-grey-medium);
  --ck-color-comment-count: var(--color-ck-actions);
  --ck-color-comment-input-background: var(--ck-color-comment-background);
  --ck-color-base-background: var(--ck-color-comment-background);
  --ck-color-button-default-hover-background: var(
    --ck-color-comment-background
  );
  --ck-color-button-on-background: var(--ck-color-comment-background);
  --ck-color-button-default-active-background: var(
    --ck-color-comment-background
  );
  --ck-color-button-on-hover-background: var(--ck-color-comment-background);
  --ck-color-button-on-active-background: var(--ck-color-comment-background);
  --ck-color-button-on-disabled-background: var(--ck-color-comment-background);
  --ck-color-comment-remove-background: var(--ck-color-comment-background);
  --ck-color-annotation-icon: var(--color-orange-lighter);
  --ck-user-avatar-color: var(--color-black-alt);
  --ck-color-mention-text: var(--text-primary);
  background: var(--color-page-background);
  position: relative;
  display: flex;
  flex-direction: column;
  transition: background var(--transition);

  @include query($md, max) {
    margin-top: calc(var(--sn17) + var(--sn2px));
    margin-left: calc(var(--sn8) + var(--sn2px));
  }

  &.comment-panel-open {
    width: 400px;

    @include query($md, max) {
      width: 300px;
      margin-top: 0;
      margin-left: 0;
    }

    @include query($sm, max) {
      position: absolute;
      width: 100%;
      right: 0;
    }
  }

  .top,
  .bottom {
    display: none;
  }

  .ck-comment__content {
    .mention {
      font-weight: bold;
    }
  }

  .comment-panel-actions {
    display: flex;
    height: var(--s12);

    button {
      --ck-action-color: var(--tab-text-color-active);

      border-radius: 0;
      flex: 1;
      height: var(--s12);

      &:not(.action-active) {
        background: var(--tab-background-color);
        border-color: var(--tab-background-color);
        color: var(--tab-text-color);
      }

      &:hover,
      &.action-active {
        background-color: var(--tab-background-color-active);
        border-color: var(--tab-background-color-active);
        color: var(--tab-text-color-active);
      }
    }
  }

  .ui-button.button-outline {
    &:disabled {
      opacity: var(--ck-disabled-opacity);
    }
  }

  .new-comment-button-container {
    padding: var(--s6);
    height: rem(106);

    .new-comment-button {
      border-color: var(--color-orange-theme);
      color: var(--color-orange-theme);
    }
  }

  .new-comment-button {
    background: var(--ck-color-comment-background);
    padding: var(--s4);
    width: 100%;
  }

  .ck-sidebar-item {
    margin-top: var(--s6);
    position: relative !important;
    top: 0 !important;
  }

  .comment-icon,
  .collapse-icon {
    --icon-secondary-color: var(--color-orange);
    background: var(--ck-toggle-background);
    border-radius: 50%;
    border: none;
    left: calc(var(--sn15) / 2);
    max-height: var(--s15);
    max-width: var(--s15);
    padding: var(--s4);
    position: absolute;
    top: var(--s20);
    z-index: 1;

    @include query($sm, max) {
      left: initial;
    }
  }

  .comment-icon {
    @include query($md, max) {
      padding: var(--s2);
    }
  }

  &.comment-panel-closed .comment-icon {
    @include query($sm, max) {
      left: initial;
      right: 0;
    }
  }

  &.comment-panel-open {
    @include query($sm, max) {
      height: calc(100vh - var(--s16));
      width: 100vw;
    }
  }

  .unresolved-comments-button,
  .resolved-comments-button {
    border-radius: 0;
    margin-right: var(--s1);
  }

  #comment-panel {
    min-height: inherit;
    overflow-y: auto;
    padding: var(--s3) 5% var(--s5) 10%;

    @include query($sm, max) {
      overflow-y: auto;
      margin-top: var(--s12);
    }
  }

  .ck.ck-sidebar {
    min-height: auto !important;
  }

  .ck {
    .ck-comment__input {
      margin-left: 5%;
      width: 95%;
    }

    .ck-annotation-wrapper {
      box-shadow: none;
    }

    .ck-comment__input {
      border: var(--s1px) solid var(--color-ck-actions);
      border-radius: var(--s1);
    }
  }
}

.comment-tabs {
  display: flex;
  flex-direction: column;
  flex-grow: 1;

  .nav-item {
    display: flex;
    flex-grow: 1;
    justify-content: center;
  }
}

[data-comments='open'] {
  .project-container {
    .user-details-container {
      @include query($xxl, max) {
        flex-direction: column;

        > div:first-of-type {
          margin-bottom: var(--s2);
        }
      }
      @include query($xl, max) {
        min-width: 70%;
      }
    }
    .deal-details-container {
      flex-wrap: wrap;
      @include query($xxl, max) {
        > div {
          min-width: 50%;
        }
      }
      @include query($xl, max) {
        min-width: 30%;
        > div {
          min-width: 80%;
        }
      }
    }
    .new-and-updated-container {
      flex-direction: row;
    }
  }
  .kpi-container {
    @include query($xxl, max) {
      .kpi-subpanel {
        flex-direction: column;

        .kpi-section:not(:last-child) {
          border-right: none;
          border-bottom: var(--s1px) solid var(--kpi-border-color);
        }
      }
    }
  }
  .project-overview-subpanel-container {
    @include query($xxl, max) {
      grid-template-columns: auto;
    }
  }

  .project-overview-module__inner {
    @include query($xl, max) {
      flex-flow: column;

      .content-item-container {
        width: auto !important;
      }

      [class*='topic-panel--'] {
        width: auto;
      }
      .topic-panel--big-left-column {
        margin-bottom: var(--s8);
        border-right: none;
      }
    }
  }
}
[data-appbanner='open'] .comment-panel {
  .comment-panel-container {
    height: calc(100vh - var(--s16) - var(--app-banner-height-offset));
    padding-bottom: var(--s20);

    @include query($sm, max) {
      top: var(--app-banner-height-offset);

      &.comment-panel-closed .comment-icon {
        top: var(--sn16);
      }
    }
  }
}
