@import 'src/styles/includes';

.a-radio {
  align-items: center;
  display: inline-flex;
  margin-bottom: unset;
  position: relative;
  vertical-align: middle;

  input[type='radio'] {
    height: 0;
    opacity: 0;
    width: 0;

    + .a-radio-mark {
      background: var(--color-grey-dark);
      border-radius: 50%;
      border: var(--input-radio-border-size) solid var(--input-radio-border);
      cursor: pointer;
      display: inline-block;
      height: var(--input-radio-size);
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      transition: all 0.2s ease-in-out;
      width: var(--input-radio-size);

      &:focus {
        outline: none;
      }

      .a-radio-inner {
        background-color: var(--color-white);
        border-radius: 50%;
        display: none;
        height: var(--font-size-xxs);
        left: 50%;
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
        width: var(--font-size-xxs);
      }
    }

    ~ .a-radio-text {
      color: var(--text-primary);
      cursor: pointer;
      font-size: var(--font-size-regular);
      line-height: var(--s4);
      margin-left: var(--s4);
      padding-left: var(--padding-standard);
    }

    &:checked {
      + .a-radio-mark {
        background: var(--background-secondary);
        border: none;

        .a-radio-inner {
          display: inline-block;
        }
      }

      &:disabled + .a-radio-mark {
        background: var(--color-red-lighter);
      }
    }

    &:focus + .a-radio-mark {
      outline: var(--s1px) dotted;
      outline: var(--s1) auto -webkit-focus-ring-color;
    }

    &:disabled {
      + .a-radio-mark {
        border-color: var(--color-grey-light);
        cursor: not-allowed;
      }

      ~ .a-radio-text {
        color: var(--color-grey);
        cursor: not-allowed;
      }
    }
  }
}
