@import 'src/styles/includes';

.file-list-container {
  --file-list-panel-text: #bbbbbb;

  .theme-light & {
    --color-grey-medium: var(--color-grey-darker);
    --color-grey-darker: var(--color-white);
    --file-list-panel-text: #464646;
  }

  @extend %max-content-width;
  padding: var(--container-spacing);
}
