@import 'src/styles/includes';

.insight-top-bar-container {
  border-bottom: var(--border-standard);
  padding: 0 var(--container-spacing);

  .insight-top-bar {
    align-items: center;
    display: flex;
    flex-direction: row;
    height: var(--s16);
    justify-content: space-between;

    @include query($xs, 'max') {
      align-items: flex-start;
      flex-direction: column;
      padding-left: 0;

      > div:first-of-type {
        margin-bottom: var(--s4);
      }
    }

    @include query($md) {
      height: var(--s16);
      padding-right: 0;
    }

    @include query($lg) {
      width: 100%;
      padding-right: var(--s5);
    }

    & > div {
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }

    & > div:not(:first-of-type) > button {
      margin: 0 var(--s2);
    }

    & > div:not(:first-of-type) > button:last-of-type {
      margin-right: 0;
    }

    .share-button {
      white-space: nowrap;
    }
  }
}
.insight-content {
  max-width: $content-width;

  .content-label.insight {
    color: var(--text-primary);
    font-size: var(--font-size-xl);
    margin-top: var(--s4);
    margin-bottom: var(--s1);
  }
  .page-last-updated {
    margin-bottom: var(--s3);
  }
  .section-name {
    padding-top: var(--s8);
    color: var(--label-color);
  }
}
