@import 'src/styles/includes';
.four-block-narrow-right {
  display: grid;
  grid-template-rows: fit-content(100%) fit-content(100%);
  grid-template-columns: 67% 33%;
  gap: var(--s3);
  margin: 0 0 var(--s12) 0;

  @media (max-width: #{$md + 'px'}) {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
  }

  .panel-area-1 {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    @media (max-width: #{$md + 'px'}) {
      grid-row: 1 / 2;
      grid-column: 1;
    }
  }

  .panel-area-2 {
    grid-row: 1 / 2;
    grid-column: 2 / 3;
    @media (max-width: #{$md + 'px'}) {
      grid-row: 2 / 3;
      grid-column: 1;
    }
  }

  .panel-area-3 {
    grid-row: 2 / 3;
    grid-column: 1 /2;
    @media (max-width: #{$md + 'px'}) {
      grid-row: 3 / 4;
      grid-column: 1;
    }
  }

  .panel-area-4 {
    grid-row: 2 / 3;
    grid-column: 2 /3;
    @media (max-width: #{$md + 'px'}) {
      grid-row: 4 / 5;
      grid-column: 1;
    }
  }
}
