@import 'src/styles/includes';

.two-column-layout {
  display: grid;
  grid-template-rows: fit-content(100%);
  grid-template-columns: 1fr 1fr;
  margin: 0 0 var(--s12) 0;

  &:last-child {
    margin-bottom: 0;
  }

  @include query($sm, max) {
    grid-template-columns: 1fr;
    grid-template-rows: [topRow] 1fr [bottomRow] 1fr;
  }

  .panel-area-1 {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    padding-right: var(--s3);
    border-right: 1px solid var(--color-grey-medium);
    overflow: auto;

    @include query($sm, max) {
      grid-row: 1 / 2;
      grid-column: 1;
      padding-right: 0;
      padding-bottom: var(--s3);
      border-bottom: 1px solid var(--color-grey-medium);
      border-right: none;
    }
  }

  .panel-area-2 {
    grid-row: 1 / 2;
    grid-column: 2 / 3;
    padding-left: var(--s3);
    overflow: auto;

    @include query($sm, max) {
      grid-row: 2 / 3;
      grid-column: 1;
      padding-left: 0;
      padding-top: var(--s3);
    }
  }
}
