@import 'src/styles/includes';

.file-upload-action-button-container {
  .button.button-link {
    padding: 0 var(--s3);
  }

  .action-button {
    background: var(--action-button-background);
    width: var(--s10);
    height: var(--s10);
    border-radius: 100px;
    padding: 0 !important;
    overflow: visible;
  }

  .action-button:hover {
    background: var(--action-button-background-hover);
  }

  .action-button:active {
    background: var(--action-button-background-hover);
  }
}
