@import 'src/styles/includes';

.step-indicator-container {
  margin-top: var(--s8);
  margin-bottom: var(--s12);
}

.sourcetray-container {
  @include scrollbars();
  box-sizing: content-box;
  background-color: var(--dragdrop-container-background) !important;
  border-bottom: var(--s1px) solid var(--dragdrop-container-border);
  margin-left: var(--sn12);
  margin-right: var(--sn12);
  overflow-x: auto;
  width: calc(100vw - var(--s21));

  @include query($sm, 'max') {
    margin-left: var(--sn4);
    margin-right: var(--sn5);
    width: 100vw;
  }

  @include query($xxl) {
    width: calc(100vw - var(--navbar-width-xxl));
  }
}

.drag-and-drop-layout-columns {
  @include query($md) {
    display: flex;
    flex-direction: row;
  }
}

.library-item-source-tray {
  // This important is kind of important to stop the ReactBDnD droppable props colouring this source tray
  background-color: var(--dragdrop-container-background);
  display: flex;
  flex-direction: row;
  padding: var(--s5) var(--s12);
  min-height: rem(104);
  width: 100%;

  &:after {
    content: ' ';
    display: inline-table;
    height: var(--s4);
    width: var(--s12);

    @include query($sm, 'max') {
      width: var(--s4);
    }
  }

  @include query($sm, 'max') {
    padding-left: var(--s4);
    padding-right: var(--s4);
  }

  .library-item-card {
    margin-bottom: 0;
  }
}

.library-item-card {
  background-color: var(--dragdrop-card-background);
  border: none;
  box-shadow: var(--card-shadow);
  color: var(--dragdrop-card-color);
  display: flex;
  padding: var(--s1) var(--s4);
  margin-bottom: var(--s3);
  width: 100%;
  max-width: 100%;

  &:last-child {
    margin-bottom: var(--s6);
  }
}

.library-item-card:active {
  border: 2px solid var(--color-orange);
}

.library-item-card-icon,
.library-item-card .library-item-card-icon {
  padding: var(--s2);
  border: none;
  margin-left: 0;
  margin-right: 0;
}

.library-item-card .library-item-card-title {
  color: var(--library-item-label-color);
  margin-left: 0;
  margin-right: 0;
}

.library-item-card-icon svg {
  width: 22px;
}

.library-item-icon,
.library-item-name {
  padding: var(--s2);
}

.layout-canvas {
  min-height: 400px;
  padding: var(--content-spacing);
  border: var(--dragdrop-container-dashed-border);
  border-radius: var(--s2px);
}

.layout-canvas {
  .library-item-card {
    margin-bottom: var(--s6);
    max-width: 25vw;
    width: 25vw;
    @include query($sm, 'max') {
      width: 86vw;
      max-width: 86vw;
    }
  }
}

.layout-half-canvas {
  min-height: 400px;
  padding: var(--content-spacing);
  border: var(--dragdrop-container-dashed-border);
  border-radius: var(--s2px);

  @include query($sm, 'max') {
    max-width: 100%;
  }

  .library-item-card {
    @include query($sm, max) {
      max-width: 80vw;
    }
  }
}

.canvas-left-side {
  margin-right: var(--s3);

  @include query($sm, 'max') {
    margin-right: 0;
    margin-bottom: var(--s3);
  }
}

.canvas-right-side {
  margin-left: var(--s3);

  @include query($sm, 'max') {
    margin-left: 0;
  }
}

.freeform-analysis-form-header {
  .active-content-header-container .active-content-header {
    width: 100%;
  }
}

%small-layout-container {
  border-radius: var(--s2px);
  display: inline-flex;
  height: var(--s5);
  margin-right: var(--s3);
  width: var(--s5);

  &:before,
  &:after {
    content: '';
    display: block;
  }
}

.layout-column {
  @include query($md) {
    flex-basis: 50%;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
}

.layout-column-header {
  align-items: center;
  display: flex;
  font-size: var(--font-size-regular);
  margin-bottom: var(--s3);
  line-height: var(--lineheight-regular);

  @include query($md) {
    display: none;
  }
}

.layout-left-column {
  @extend %small-layout-container;
  flex-direction: row;

  &:before {
    border: var(--s2px) solid var(--layout-border-on);
    border-radius: var(--s2px) 0 0 var(--s2px);
    width: calc(var(--s2) + var(--s2px));
  }

  &:after {
    border: var(--s2px) solid var(--layout-border-off);
    border-left: none;
    border-radius: 0 var(--s2px) var(--s2px) 0;
    width: calc(var(--s2) + var(--s2px));
  }
}

.layout-right-column {
  @extend %small-layout-container;
  flex-direction: row;

  &:before {
    border: var(--s2px) solid var(--layout-border-off);
    border-right: none;
    border-radius: var(--s2px) 0 0 var(--s2px);
    width: calc(var(--s2) + var(--s2px));
  }

  &:after {
    border: var(--s2px) solid var(--layout-border-on);
    border-radius: 0 var(--s2px) var(--s2px) 0;
    width: calc(var(--s2) + var(--s2px));
  }

  @include query($md) {
    margin-left: var(--s3);
  }
}

.freeform-analysis-layout {
  .form-content {
    max-width: unset;
  }
  .analysis-layout-contained {
    @extend %max-content-width;
  }
}
