.subcategory-wrapper {
  padding: var(--s6) var(--s12);
  padding-bottom: 0;

  .subcategory-title-wrapper {
    display: flex;
    align-items: flex-end;

    .subcategory-title {
      font-size: var(--s4);
      font-weight: 500;
      padding-right: var(--s3);
    }

    .text-input-fieldset {
      padding-bottom: 0;
      width: 112px;
    }
  }

  .complexity-scale {
    margin-top: var(--s6);
  }

  .description-wrapper {
    border-bottom: var(--s1px) solid var(--color-separator);
    padding-bottom: var(--s6);
  }
}
