@import 'src/styles/includes';

.analysis-report-preview {
  .analysis-report-preview-container {
    padding: 0 var(--s12);
  }

  &__back-link {
    display: flex;
    align-items: center;
    background: transparent;
    border-color: transparent;
    color: var(--color-orange-theme);

    &:hover,
    &:focus {
      color: var(--color-orange-theme);
    }
  }

  &__back-icon {
    margin-right: var(--s3);
  }

  & &__inner {
    display: flex;
    justify-content: space-between;
    width: 75%;
    max-width: 1056px;
    margin-top: var(--s7);
    margin-bottom: var(--s5);

    @include query($sm, max) {
      max-width: 100%;
    }

    &__status {
      p.page-last-updated {
        padding: 0;
      }
    }
  }

  p.page-last-updated {
    padding: var(--s2) var(--s4) 0 0;
    margin-bottom: var(--s0);
    @media screen and (min-width: 992px) {
    }
  }

  .analysis-pill-container {
    padding: var(--s2) var(--s4) 0 0;
    margin-bottom: var(--s4);
    @media screen and (min-width: 992px) {
    }
  }

  .preview-image {
    width: 75%;
    max-width: 1056px;
    margin-bottom: var(--s7);
    display: block;

    @include query($sm, max) {
      max-width: 100%;
    }
  }
}
