/* Popover Style Overrides */
html {
  // Popover:Left
  .bs-popover-left,
  .bs-popover-auto[x-placement^='left'] {
    padding-right: var(--s3);

    .arrow {
      height: var(--s6);
      margin: 0 calc(var(--s3) - var(--s2px)) 0 0;
      width: var(--s3);

      &::before {
        border-width: var(--s3);
        border-right-width: 0;
        border-left-color: var(--popup-border-color);
      }

      &::after {
        border-width: var(--s3);
        border-right-width: 0;
        border-left-color: var(--popup-background-color);
      }
    }
  }

  // Popover:Top
  .bs-popover-top,
  .bs-popover-auto[x-placement^='top'] {
    padding-bottom: var(--s2px);

    .arrow {
      height: var(--s3);
      margin: var(--s1) 0 0 var(--sn1);
      width: var(--s6);

      &::before {
        border-width: var(--s3);
        border-bottom-width: 0;
        border-top-color: var(--popup-border-color);
      }

      &::after {
        border-width: var(--s3);
        border-bottom-width: 0;
        border-top-color: var(--popup-background-color);
      }
    }
  }

  // Popover:Bottom
  .bs-popover-bottom,
  .bs-popover-auto[x-placement^='bottom'] {
    padding-top: var(--s2);

    .arrow {
      height: var(--s3);
      margin: var(--s1) 0 0 var(--sn1);
      width: var(--s6);

      &::before {
        border-width: var(--s3);
        border-top-width: 0;
        border-bottom-color: var(--popup-border-color);
        left: var(--s1);
      }

      &::after {
        border-width: var(--s3);
        border-top-width: 0;
        border-bottom-color: var(--popup-background-color);
        left: var(--s1);
      }
    }
  }

  .popover {
    background: transparent;
    border: none;

    .popover-body {
      border-radius: var(--s1);
      border: var(--popup-border);
      box-shadow: var(--popup-shadow);
      background-color: var(--popup-background-color);
      color: var(--project-color-secondary);
      margin-top: var(--sn1px);
      width: auto;
      min-height: rem(96);
      min-width: rem(135);

      display: flex;
      flex-direction: column;
      align-items: flex-start;
      padding: var(--popup-padding);

      .popover-header {
        background: transparent;
        border: none;
        color: var(--project-color-main);
        padding: 0 0 var(--s2);

        &::before {
          border: none;
        }
      }

      .ui-button.button-menu {
        display: flex;
        width: 100%;
        padding: 0;
        margin-bottom: var(--s5);
        text-align: left;

        svg path.fill {
          fill: transparent;
        }

        &:hover {
          path.outline {
            fill: transparent;
          }

          path.fill {
            fill: var(--button-primary-color);
          }
        }

        &:last-child {
          margin-bottom: 0;
        }
      }

      .button:disabled {
        svg path.fill {
          fill: transparent;
        }
        svg path.outline {
          fill: var(--color-grey);
        }
        color: var(--color-grey);
        opacity: 0.5;
      }
    }
  }
}

html #popover-moremenu {
  .arrow {
    z-index: 10;
  }
  .popup-menu.popover-body {
    position: relative;
    left: var(--s4);
  }
}
