.file-status-button {
  &:hover {
    path.fill {
      fill: var(--button-primary-color);
    }

    svg.icon.icon-orange.outline path.outline {
      fill: transparent;
    }
  }
}

.approve-decline {
  display: flex;

  button {
    margin-right: calc(var(--font-size-s) + var(--sn1px));
  }

  div.divider {
    border-left: 1px solid var(--color-grey-medium);
    margin-right: calc(var(--font-size-s) + var(--sn1px));
  }
}
