@import 'src/styles/includes';

.client-manipulation-bar {
  position: fixed;
  bottom: 1rem;
  z-index: 200;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--color-white);
  margin: 0;
  border-radius: 50px;
  background-color: var(--client-manipulation-tooltip-background);
  height: 52px;
  box-shadow: 20px 20px 20px rgba(0, 0, 0, 0.2);

  .ui-tooltip-tip {
    font-size: var(--font-size-s);
    color: var(--client-manipulation-tooltip-font-color);
    background-color: var(--client-manipulation-tooltip-background);
    box-shadow: 6px 6px 6px rgba(0, 0, 0, 0.2);
    margin-bottom: var(--s2px);
  }

  .ui-tooltip-top .ui-tooltip-tip {
    &::before,
    &::after {
      border-color: var(--client-manipulation-tooltip-background) transparent
        transparent transparent;
    }

    &::before {
      border-color: var(--client-manipulation-tooltip-background) transparent
        transparent transparent;
    }
  }

  @include query($sm, max) {
    margin-left: 50%;
    transform: translateX(-50%);
  }

  @include query($md) {
    right: 2rem;
  }

  button {
    border-radius: 50px;
    padding: var(--s2) var(--s4);
    white-space: nowrap;
    text-decoration: none;
    font-size: var(--font-size-s);
    color: var(--client-manipulation-button-font-color);
    background-color: var(--client-manipulation-button-background-color);

    @include query($sm, max) {
      font-size: var(--font-size-s);
    }
  }

  button.reset-all {
    color: var(--client-manipulation-bar-reset-font-color);
    background-color: var(--client-manipulation-bar-reset-background-color);
    text-decoration: none;
    font-size: var(--font-size-s);
    padding-left: var(--s3);
    padding-right: var(--s6);

    &:hover {
      text-decoration: underline;
    }
  }

  .toggles {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--client-manipulation-button-background-color);
    color: var(--color-grey-dark);
    border-radius: 50px;
    border: 1px solid var(--client-manipulation-button-border-color);
    height: var(--s12);
    margin-left: var(--s2px);

    button {
      height: var(--s12);
      width: 126px;

      &:not(.active) {
        background: none;
      }
      &.active.pwc-default {
        background-color: var(--color-pink);
        color: var(--color-white);
        font-size: var(--font-size-s);
        margin-right: -1px;
      }

      &.active.your-view {
        background-color: var(--color-yellow);
        color: var(--color-black-alt);
        font-size: var(--font-size-s);
        margin-left: -1px;
      }

      &:hover {
        text-decoration: underline;
      }
    }
  }
}
