@import 'src/styles/includes';

.rs-panel-layout-tab2 {
  display: flex;
  flex-direction: column;
  background-color: var(--background-default);
  height: 100%;
  padding: var(--s6);

  .rs-panel-available-layouts2 {
    background-color: var(--background-default);
    display: flex;
    flex-direction: column;

    &__list {
      margin: 0;
      padding: 0 calc(var(--s2) + var(--s2px)) 0 0;
      display: grid;
      grid-template-columns: repeat(5, 1fr);
      margin-bottom: var(--s6);
    }
    .small-container {
      position: relative;
      display: flex;
      flex-grow: 1;
      flex-shrink: 1;
      margin-right: calc(var(--s2) + var(--s1px));

      img {
        max-width: 100%;
        max-height: 100%;
      }
    }
  }

  .layout-image-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(208, 74, 2, 0.28);
    border: var(--s2px) solid var(--color-orange);
  }
}
