@import 'src/styles/includes';

.file-list-panel {
  padding: calc(var(--s8) - var(--s1px));
  border-radius: var(--s2px);
  box-shadow: 0 var(--sn1px) 0 0 var(--color-grey-medium);
  background-color: var(--color-grey-darker);
  color: var(--file-list-panel-text);

  @include query($sm, max) {
    padding: calc(var(--s5) - var(--s2px));
  }
}
