@import 'src/styles/includes';

.project-overview-module {
  border-radius: 2px;
  background-color: var(--background-primary);
  box-shadow: var(--table-shadow);
  margin-bottom: var(--s6);
  padding: var(--content-spacing);

  > * {
    word-break: break-word;
  }

  li {
    margin-top: 0;
  }

  li > ul:not(:empty) {
    border-top: none;
  }

  li > ul > li {
    border-bottom: none;
  }

  &__inner {
    display: flex;
    flex-flow: row wrap;
  }

  a,
  a:visited,
  a:active {
    color: var(--project-color-secondary);
    text-decoration: none;
  }

  a:hover,
  a:visited:hover,
  a:active:hover {
    color: var(--project-color-secondary);
    text-decoration: underline;
  }
}

.delete-topic-button {
  display: flex;
  align-items: center;
}

.delete-topic-icon {
  margin-right: 0.7em;
}
