@import 'src/styles/includes';

#popover-user {
  max-width: 285px;

  .popover-body {
    font-size: var(--font-size-regular);
    width: auto;

    .user-profile-dropdown-menu-section {
      width: 100%;
      padding: var(--s4) 0;
      border-top: var(--s1px) solid var(--popup-rule-color);

      .toggle-container,
      .toggle-checkbox {
        margin-bottom: 0;
        line-height: 1;
      }

      &:first-child {
        border: none;
        padding-top: 0;
      }

      &:last-child {
        padding-bottom: 0;
      }
    }

    p {
      &:first-child {
        color: var(--breadcrumb-text);
        font-weight: var(--font-weight-medium);
        margin: 0;
      }
    }

    .toggle-checkbox {
      padding: 0;

      &::before,
      &::after {
        content: none;
      }
    }
  }
}

.freeze .usersnippet-initials {
  @include query($sm, 'max') {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.3s, opacity 0.3s linear;
  }
}
