@import 'src/styles/includes';

.project-state-pill-container {
  display: flex;
  flex-wrap: wrap;

  .project-state-pill {
    display: flex;
    align-items: center;
    height: calc(var(--s7) + var(--s2px));
    grid-template-columns: calc(var(--s2p) + var(--s2px)) 1fr;
    grid-column-gap: calc(var(--s2px) + var(--s3px));
    font-size: var(--font-size-s);
    line-height: var(--font-size-s);
    font-weight: var(--font-weight-medium);
    padding: var(--s2px) var(--s3);
    color: var(--color-black-alt);
    background-color: var(--color-grey-light);
    border-radius: var(--s4);
    white-space: nowrap;

    svg {
      margin-bottom: var(--s1px);
    }

    &--icon {
      color: black;
    }

    &__no-icon {
      grid-template-columns: 1fr;
    }

    &__small {
      height: calc(var(--s4) + var(--s2px));
      font-weight: var(--font-weight-regular);
    }
  }
}
