@import './variables';

@function rem($size, $neg: false) {
  $remSize: $size / 16;

  @if $neg==true {
    @return -#{$remSize}rem;
  } @else {
    @return #{$remSize}rem;
  }
}

@mixin fontSize($size) {
  font-size: $size;
  font-size: rem($size);
}

@mixin query($size, $boundary: 'min') {
  $max: 0;

  @if ($size==$xs) {
    $max: $sm - 1;
  } @else if ($size==$sm) {
    $max: $md - 1;
  } @else if ($size==$md) {
    $max: $lg - 1;
  } @else if ($size==$lg) {
    $max: $xl - 1;
  } @else {
    $max: ($size - 1);
  }

  @if $boundary== 'max' {
    @media screen and (max-width: #{$max+'px'}) {
      @content;
    }
  } @else if $boundary== 'only' {
    @media screen and (min-width: #{$size+'px'}) and (max-width: #{$max+'px'}) {
      @content;
    }
  } @else {
    @media screen and (min-width: #{$size+'px'}) {
      @content;
    }
  }
}

@mixin scrollbars(
  $size: var(--s2),
  $foreground-color: var(--scrollbar-handle-color),
  $background-color: var(--scrollbar-track-color),
  $foreground-color-hover: var(--scrollbar-handle-color-hover),
  $firefox-background: var(--scrollbar-track-color)
) {
  scrollbar-color: var(--color-grey-variant) $firefox-background;
  scrollbar-width: thin; // Firefox does not "do" values for width

  // For Google Chrome
  &::-webkit-scrollbar {
    border-radius: $size;
    height: $size;
    width: $size;
  }

  &::-webkit-scrollbar-track {
    background: $background-color;
    border-radius: $size;
  }

  &::-webkit-scrollbar-thumb {
    background: $foreground-color;
    border-radius: $size;

    &:hover {
      background: $foreground-color-hover;
    }
  }

  // For Internet Explorer
  /* stylelint-disable-next-line scss/selector-no-redundant-nesting-selector */
  & {
    -ms-scrollbar-3dlight-color: $foreground-color;
    -ms-scrollbar-face-color: $foreground-color;
    scrollbar-face-color: $foreground-color;
    -ms-scrollbar-track-color: $background-color;
    scrollbar-track-color: $background-color;
  }
}
