@import 'src/styles/includes';
.stack-two-block-bottom {
  display: grid;
  grid-template-rows: fit-content(100%) fit-content(100%);
  grid-template-columns: 1fr 1fr;
  gap: var(--s3);
  margin: 0 0 var(--s12) 0;

  @include query($sm, max) {
    grid-template-columns: 1fr;
    grid-template-rows: [topRow] 1fr [middleRow] 1fr [bottomRow] 1fr;
  }

  @include query($sm, max) {
    display: block;
  }

  .panel-area {
    @include query($lg, max) {
      overflow: auto;
    }
  }

  .panel-area-1 {
    grid-row: 1 / 2;
    grid-column: 1 / span 2;
    margin-bottom: var(--s4);
    border-bottom: var(--s1px) solid var(--color-grey-medium);

    @include query($sm, max) {
      grid-row: 1 / 3;
      grid-column: 1;
    }
  }

  .panel-area-2 {
    position: relative;
    grid-row: 2 / 2;
    grid-column: 1 / 2;

    @include query($sm, max) {
      grid-row: 2 / 3;
      grid-column: 1;
    }

    @include query($md, min) {
      &::after {
        position: absolute;
        bottom: 0;
        top: 0;
        right: 0;
        content: '';
        width: var(--s1px);
        border-right: var(--s1px) solid var(--color-grey-medium);
      }
    }

    @include query($sm, max) {
      border-bottom: var(--s1px) solid var(--color-grey-medium);
    }
  }

  .panel-area-3 {
    grid-row: 2 / 2;
    grid-column: 2 /2;
    @include query($sm, max) {
      grid-row: 3 / 3;
      grid-column: 1;
    }
  }
}
