@import 'src/styles/includes';

.draggable-library-item-card2 {
  min-height: var(--s16);
  margin: 0 0 calc(2 * (var(--s4) + var(--s2px))) 0;

  .rich-text-library-item-edit {
    @include query($sm, 'max') {
      width: auto;
      max-width: unset;
    }
  }

  .library-item-card2 {
    max-width: unset;
    min-width: unset;
    margin: 0;
    box-shadow: 0 var(--sn1px) 0 0 var(--table-shadow-color);
    background-color: var(--dragdrop-card-background);
    border: var(--s1px) dashed var(--dragdrop-layout-icon-fill);
    border-radius: var(--s2px);

    &:active {
      border: var(--s2px) solid var(--color-orange-theme);
      box-shadow: none;
      height: 218px;
      width: 250px;

      .library-item-card-icon {
        display: none;
      }

      .draggedLibraryItemCard {
        box-sizing: border-box;
        margin-left: -80px;
        margin-top: 20px;
        max-width: 500px;
        max-height: 500px;
        transform: scale(0.5);
      }
    }
    &:hover:not(:active) {
      border: var(--s1px) dashed var(--hovered-section-border-color);
      box-shadow: none;
    }
    .library-item-card-icon {
      padding: 0;
      border: 0;
      margin: 0 var(--s4) 0 0;
      min-height: unset;
      display: flex;
      align-items: center;
      & svg {
        width: var(--s6);
        height: var(--s6);
      }
    }
  }
}
