@import '../../styles/includes';

.content-list-header {
  padding-top: var(--s4);
  padding-bottom: var(--s4);

  @include query($sm, 'max') {
    padding-top: var(--s1);
    padding-bottom: var(--s5);
  }

  .sort-order-checkbox-cell {
    justify-self: self-end;

    .ui-checkbox {
      padding: 0;
      display: inline-block;
      margin: 0;

      .checkbox:disabled::before {
        border-color: var(--input-background);
      }

      span {
        display: none;
      }
    }

    & + .sort-order-cell {
      margin-left: var(--s6);
    }
  }

  .sort-order-cell {
    .ui-tooltip {
      margin-left: var(--s2);

      .ui-tooltip-tip {
        max-width: 500px;
        text-align: left;
      }
    }

    &.tooltip-short {
      .ui-tooltip-tip {
        max-width: 400px;
      }
    }

    .sort-chevron-desc {
      margin-left: var(--s2);
    }
  }

  button.header-cell {
    align-items: center;
    color: var(--project-color-secondary);
    display: flex;
    width: fit-content;
    font-size: rem(16);
    font-weight: var(--font-weight-bold);

    &:hover {
      text-decoration: underline;
    }

    &:active {
      color: var(--color-grey-alt);
      text-decoration: underline;
    }

    &:focus {
      text-decoration: none;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}
