@import 'src/styles/includes';

.policy-container {
  color: var(--project-color-main);
  max-width: $content-width;

  a {
    text-decoration: underline;
  }

  hr {
    border-top: 1px solid var(--color-grey-dark);
    margin: var(--s16) 0 var(--s8) 0;
  }

  table {
    display: grid;
    border-collapse: collapse;
    grid-template-columns:
      minmax(30px, 2fr)
      minmax(30px, 2fr)
      minmax(30px, 2fr)
      minmax(30px, 2.5fr)
      minmax(30px, 2fr);
    font-size: var(--font-size-m);

    &.disabled {
      color: var(--color-grey-alt);
    }
  }

  thead,
  tbody,
  tr {
    display: contents;
  }

  th,
  td {
    padding: 15px;
    overflow: hidden;
  }

  th {
    top: 0;
    text-align: left;
    color: var(--color-grey-alt);
  }

  th:last-child {
    border: 0;
  }

  td {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  tr:nth-child(odd) td {
    background: var(--navbar-color);
    border-top: 1px solid var(--color-grey-dark);
  }

  #performance-cookies-section-header {
    display: grid;

    grid-template-columns: 1fr 1fr;
    grid-template-areas: 'title toggle' 'info info';
    margin-top: var(--s4);

    @include query($xs, 'max') {
      grid-template-columns: 1fr;
      grid-template-areas: 'title' 'info' 'toggle';
    }

    & > h6 {
      grid-area: title;
    }

    & > .toggle-container {
      grid-area: toggle;
      width: fit-content;
      margin-left: auto;

      @include query($xs, 'max') {
        margin-left: 0;
      }
    }

    & > .performance-cookies-toggle-info {
      grid-area: info;
      margin-top: var(--s4);
      padding-bottom: var(--s3);
      display: flex;
      align-items: flex-start;

      @include query($xs, 'max') {
        margin-top: 0;
        margin-bottom: var(--s5);
      }

      span:first-child {
        padding-right: var(--s3);
        margin-top: -2px;
      }
    }
  }
}
