@import 'src/styles/includes';

.header {
  background-color: var(--header-background);
  height: var(--s16);
  width: 100%;
  border-bottom: var(--s1px) solid var(--border-primary);

  .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;

    @include query($sm, 'max') {
      justify-content: flex-end;
    }

    .header-content-breadcrumbs {
      margin: 0 var(--container-spacing);

      div {
        color: var(--breadcrumb-text);
      }

      @include query($sm, 'max') {
        display: none;
      }
    }

    .header-content-usersnippet {
      border-left: none;
      color: var(--text-primary);
      display: flex;
      height: var(--s12);
      margin: var(--s2) var(--s12) var(--s2) var(--s12);
      padding: var(--s1px) 0 0 var(--s5);

      @include query($sm, 'max') {
        display: none;
      }
    }

    .usersnippet-photo {
      background-color: var(--text-alternate);
      border-radius: var(--s25);
      height: var(--s10);
      width: var(--s10);
    }

    .usersnippet-text {
      padding-left: var(--padding-half);
    }

    .usersnippet-name {
      font-weight: bold;
      color: var(--text-primary);
      font-size: var(--font-size-s);
    }

    .usersnippet-position {
      font-weight: normal;
      color: var(--color-grey);
      font-size: var(--font-size-s);
    }

    .snippet-pendo-container {
      align-items: center;
      display: flex;
      margin-right: var(--container-spacing);
    }

    .usersnippet-initials {
      button.button-no-appearance {
        align-items: center;
        border-radius: 50%;
        display: flex;
        font-size: var(--font-size-s);
        font-weight: var(--font-weight-medium);
        height: var(--s8);
        justify-content: center;
        line-height: 1;
        margin-left: var(--s6);
        text-transform: uppercase;
        width: var(--s8);
      }
    }
  }

  .pendo-help {
    justify-content: center;
    margin-left: var(--s6);
    display: flex;

    .icon-help .outline {
      fill: var(--icon-header-help);
    }

    svg:hover {
      cursor: pointer;
    }
  }
}

.full-height {
  height: calc(100vh - 128px);
}

.activecontent {
  background-color: var(--background-default);
  border-left: var(--s21) solid var(--navbar-color);

  @include query($md, 'max') {
    border-left: none;
  }
}
