@import 'src/styles/includes';

.upload-modal {
  .modal-dialog {
    max-width: 832px;
    .modal-content {
      padding: 0;

      .modal-header {
        padding: var(--content-spacing);
        border-color: var(--color-separator);

        h2 {
          font-weight: var(--font-weight-medium);
          margin-bottom: var(--s3);
        }

        .close {
          font-weight: var(--font-weight-regular);
        }

        p {
          font-size: var(--font-size-medium);
          margin-bottom: 0;
        }
      }

      .modal-body {
        background: var(--color-page-background);
        padding: var(--content-spacing);
        padding-top: var(--s7);
      }

      .modal-footer {
        padding: var(--content-spacing);
        border-color: var(--color-separator);
      }
    }
  }


    h3 {
      margin: var(--s6) 0 var(--s4) 0;
    }

    h5 {
      font-size: rem(18);
      font-weight: var(--font-weight-regular);
      line-height: var(--lineheight-medium);
      margin-bottom: var(--s6);
    }

    .modal-header {
      padding: var(--s12) var(--s12) var(--s2) var(--s12);

      .close {
        position: absolute;
        right: var(--s6);
        top: var(--s4);
      }

      .back-button {
        position: absolute;
        top: var(--s2);
        white-space: nowrap;
      }
    }
    .modal-body {
      min-height: 400px;
    }

    .modal-body,
    .modal-footer {
      background: var(--color-page-background);
      padding: 0 var(--s12) var(--s12) var(--s12);
    }

    .modal-actions {
      padding-top: var(--content-spacing);
    }

    .basic label:empty {
      margin: 0;
      padding: 0;
    }
}