@import 'src/styles/includes';

.report-panel-tabs {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  max-width: 100%;
  &__list {
    margin: 0;
    padding: 0 calc(var(--s4) + var(--s2px));
    list-style-type: none;
    display: flex;
    text-align: center;

    @include query($lg, max) {
      padding: 0 var(--s2);
    }
    @include query($md, max) {
      padding: 0;
    }
  }

  &__list-item {
    flex: 1;
    padding: 0 var(--s3);
    height: var(--s12);
    color: var(--color-orange-theme);
    border-bottom: var(--s3px) solid transparent;
    display: flex;
    justify-content: center;
    align-items: center;

    &--active {
      border-bottom: calc(var(--s2px) + var(--s2px)) solid var(--tab-text-color-active);
      color: var(--tab-text-color-active);
    }

    @include query($md, max) {
      padding: var(--s3) 0;
    }
  }

  &__list-toggle {
    display: flex;
    align-items: center;
    padding-left: var(--s4);
    border-bottom: var(--s3px) solid transparent;
    @include query($md, max) {
      padding: 0 var(--s3);
    }
  }
}

.report-tabs-content {
  border-bottom-color: var(--border-primary);
  border-bottom-width: var(--s1px);
  border-bottom-style: solid;
  border-top-color: var(--border-primary);
  border-top-width: var(--s1px);
  border-top-style: solid;
}
