@import 'src/styles/includes';

%colrow {
  display: flex;
  flex-direction: column;

  @include query($lg) {
    align-items: center;
    flex-direction: row;
  }
}

%select {
  select {
    max-width: 90%;
    width: 90%;

    @include query($xl) {
      max-width: 95%;
      width: 95%;
    }
  }
}

.linked-adjustment-container {
  @include query($lg) {
    min-width: rem(915);
  }
}

.linked-adjustments-title {
  margin-bottom: var(--s3);
}

.linked-adjustment-row {
  display: flex;
  margin: var(--s6) 0;

  @include query($md, max) {
    flex-direction: column;

    .pill-container {
      margin-bottom: 0;
    }

    .document-row {
      margin-top: 0;
      flex-direction: column;
    }
  }

  .clearable-display-pill {
    display: inline-flex;
    align-items: center;
    margin: 0px;
    height: 30px;

    @include query($lg) {
      max-width: 100%;
    }
  }

  .link-to {
    align-items: center;
    color: var(--project-color-secondary);
    display: inline-flex;
    white-space: nowrap;

    @include query($lg) {
      margin-right: rem(18);
    }
  }

  .select-component .select-container {
    margin-right: rem(10);
  }

  .document-row {
    display: flex;

    @include query($lg) {
      flex-direction: row;
    }

    > div {
      margin-top: var(--s2);

      @include query($lg) {
        margin-top: 0;
      }
    }
  }

  > div {
    &.pill-container {
      overflow: hidden;
      display: flex;
      margin-right: rem(18);

      .status-pill {
        display: block;
        flex-grow: 1;
        max-width: 90vw;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

        @include query($lg) {
          width: fit-content;
        }
      }

      .display-link-remove {
        background-color: var(--color-red);
        color: var(--color-white);

        .clearable-link-icon {
          margin-right: 0.5em;
        }
      }
    }

    &.linked-adjustment-row-actions {
      @include query($lg) {
        text-align: center;
      }

      .ui-button.true {
        border-color: var(--button-primary-color-disabled);
        color: var(--color-grey-medium);
        cursor: none;
        pointer-events: none;
        transition: all var(--default-transition);
      }
    }
  }

  &.viewing .document-row {
    .pill-container,
    .clearable-display-pill {
      .display-link-remove {
        background-color: var(--color-red);
        color: var(--color-white);

        .clearable-link-icon {
          margin-right: 0.5em;
        }
      }
    }
  }
}

.button-link.add-link {
  text-decoration: none;
  margin-bottom: var(--s1);
  margin-top: var(--s2);

  .helper-icon {
    margin-right: var(--s2);
  }
  span {
    color: var(--color-orange);
  }
}

.separator {
  margin: 0px;
}

.linked-adjustment-title {
  margin-bottom: var(--s6);
}

.linked-adjustment-description.helper-text {
  font-size: var(--font-size-s);
  font-weight: var(--font-weight-regular);
  color: var(--helper-text-color);
  margin-bottom: var(--s3);
}
