@import 'src/styles/includes';

.narrative-section {
  .narrative-card {
    border-radius: var(--border-radius);
    margin: var(--s4) 0 var(--s6) 0;
    overflow-wrap: break-word;
    overflow: hidden;
    padding: var(--content-spacing);
  }

  .narrative-card-title {
    color: var(--narrative-copy);
    margin-bottom: var(--margin-half);

    &:hover {
      color: var(--project-color-main);
    }
  }

  .narrative-section-title {
    color: var(--color-orange-theme);
    font-weight: var(--font-weight-bold);
  }

  .narrative-card-title {
    font-weight: var(--font-weight-bold);
  }

  &.narrative-section-insights {
    .narrative-card-title {
      font-size: var(--font-size-l);
      font-weight: var(--font-weight-bold);
    }
  }

  .narrative-card-body {
    &,
    p,
    li {
      color: var(--narrative-copy);
      font-size: var(--font-size-regular);
      line-height: var(--lineheight-regular);
      margin-top: var(--margin-half);
    }

    &.truncate {
      @extend %clamp-2;
    }
  }

  .narrative-card-empty {
    align-items: center;
    color: var(--label-color-disabled);
    display: flex;
    font-size: var(--font-size-medium);
    justify-content: center;
    min-height: rem(200);
    padding: var(--content-spacing);
    text-align: center;
  }
}

.list-grid {
  .narrative-section {
    .narrative-card {
      border-top: 0;
      padding-left: 0;
      padding-top: 0;
      margin-top: var(--s2);
      overflow: hidden;
      overflow-wrap: break-word;
    }

    .narrative-card-body {
      @extend %clamp-2;

      color: var(--narrative-copy);
      font-size: var(--font-size-regular);
    }

    .narrative-card-empty {
      display: none;
    }
  }
}
