.response-section-container {
  background: var(--background-primary);
  padding: var(--s4) 0;
  margin-bottom: var(--s5);
  border-top: var(--s1px) solid var(--border-primary);

  .response-section-button-header {
    display: flex;
    justify-content: flex-end;
  }

  table {
    width: 100%;
  }

  th {
    font-size: var(--s8);
    padding: var(--s4) var(--s12);
  }

  td {
    width: 50%;
    padding: var(--s3) var(--s12);

    &.question-cell {
      font-weight: 600;
    }

    &.answer-cell {
      color: var(--text-secondary);
    }
  }
}
