@import '../../styles/includes';

.content-list {
  @include query($md, 'min') {
    padding-top: var(--s6);
  }

  .content-list-card,
  .content-list-header {
    width: 100%;
    display: grid;
    grid-template-columns: 60px 4fr 2fr 2fr 60px;
  }

  .content-list-header {
    .sort-order-cell {
      grid-column: 1 / span 2;
    }

    @include query($sm, 'max') {
      & > *:not(.sort-order-checkbox-cell) {
        display: none;
      }
    }
  }

  .content-list-card {
    .sort-order-cell {
      grid-row: 1;
      grid-column: 1;
    }

    .content-link {
      grid-row: 1;
      grid-column: 1 / span 5;

      & > a {
        width: fit-content;
      }
    }

    .sort-order-cell + .content-link {
      grid-row: 1;
      grid-column: 2;
    }

    .display-date {
      grid-row: 1;
      grid-column: 3;
    }

    .display-content-status {
      grid-row: 1;
      grid-column: 4;
    }

    .content-actions-row-menu {
      grid-row: 1;
      grid-column: 5;
    }

    .tags-list-container {
      grid-row: 2;
      grid-column: 2 / span 3;
    }

    @include query($sm, 'max') {
      grid-template-columns: 5% 52% 28% 15%;
      row-gap: var(--s4);

      .sort-order-cell {
        grid-row: 1;
        grid-column: 1;
      }

      .sort-order-cell + .content-link {
        grid-row: 1;
        grid-column: 2 / span 3;
      }

      .display-date {
        grid-row: 2;
        grid-column: 1 / span 2;
      }

      .display-content-status {
        grid-row: 2;
        grid-column: 3;
      }

      .content-actions-row-menu {
        grid-row: 2;
        grid-column: 4;
      }

      .tags-list-container {
        grid-row: 3;
        grid-column: 1 / span 4;
      }
    }
  }

  &.drag-enabled {
    .content-list-card,
    .content-list-header {
      grid-template-columns: 84px 60px 4fr 2fr 2fr 60px;
    }

    .content-list-header {
      .sort-order-checkbox-cell {
        grid-column: 1;
      }

      .sort-order-cell {
        grid-column: 2 / span 2;
      }

      @include query($sm, 'max') {
        grid-template-columns: 40px 1fr;
      }
    }

    .content-list-card {
      .sort-order-cell-icon {
        grid-row: 1;
        grid-column: 1;
      }

      .sort-order-cell {
        grid-row: 1;
        grid-column: 2;
      }

      .sort-order-cell + .content-link {
        grid-row: 1;
        grid-column: 3;
      }

      .display-date {
        grid-row: 1;
        grid-column: 4;
      }

      .display-content-status {
        grid-row: 1;
        grid-column: 5;
      }

      .content-actions-row-menu {
        grid-row: 1;
        grid-column: 6;
      }

      .tags-list-container {
        grid-row: 2;
        grid-column: 3 / span 3;
      }

      @include query($sm, 'max') {
        grid-template-columns: 40px 1fr 10fr 6fr 3fr;
        row-gap: var(--s4);

        .sort-order-cell-icon {
          grid-row: 1 / span 2;
          grid-column: 1;
        }

        .sort-order-cell {
          grid-row: 1;
          grid-column: 2;
        }

        .sort-order-cell + .content-link {
          grid-row: 1;
          grid-column: 3 / span 3;
        }

        .display-date {
          grid-row: 2;
          grid-column: 2 / span 2;
        }

        .display-content-status {
          grid-row: 2;
          grid-column: 4;
        }

        .content-actions-row-menu {
          grid-row: 2;
          grid-column: 5;
        }

        .tags-list-container {
          grid-row: 3;
          grid-column: 2 / span 4;
        }
      }
    }
  }
}
