@import 'src/styles/includes';

.error-modal .modal-dialog {
  max-width: 520px;

  .modal-content {
    padding: var(--s12);
    box-shadow: var(--card-shadow);

    .modal-header {
      margin-bottom: var(--s4);

      .close-button {
        position: absolute;
        top: var(--s6);
        right: var(--s6);
        line-height: 18px;
      }

      .modal-title {
        font-size: var(--font-size-xl);
        line-height: var(--s12);

        .title-with-icon {
          display: flex;
          align-items: center;

          svg {
            margin-right: var(--s3);
          }
        }
      }
    }

    .modal-body {
      a {
        white-space: nowrap;
        color: var(--color-orange-theme);
        text-decoration: underline;
      }
    }
  }
}
