@import 'src/styles/includes';

.new-and-updated-container {
  color: var(--project-color-secondary);
  display: flex;
  flex-wrap: wrap;
  width: 100%;

  @include query($md, max) {
    margin-top: var(--s4);
  }

  .notification-label {
    font-size: var(--font-size-xs);
    line-height: var(--s4);
    color: var(--breadcrumb-text);
    text-decoration: underline;
  }
}
