@import 'src/styles/includes';

.hypothesis-list-container {
  @extend %max-content-width;

  padding: var(--content-spacing) var(--container-spacing) var(--s12);

  .toggle-container {
    button.button-toggle svg,
    .button.button-toggle svg {
      margin-left: var(--s2);
    }

    .button {
      padding: 0 var (--s2) 0;
    }

    .button-toggle {
      background: transparent;
      border-color: transparent;
      color: var(--button-primary-color);
    }
  }

  .tag-editor-container .tageditor {
    margin: 0;
  }

  ~ .tag-editor-container {
    padding: 0 var(--container-spacing);
  }

  .list-container-h2 {
    margin-bottom: var(--s6);
  }

  .input-field-container {
    margin-top: 0px;
  }

  .a-form-error {
    margin: 0px;
  }
}
