@import '../../styles/includes';

.content-list-card {
  align-items: center;
  align-content: center;
  background-color: var(--background-primary);
  border-radius: var(--table-border-radius);
  box-shadow: var(--card-shadow);
  display: flex;
  justify-content: space-between;
  margin-bottom: var(--margin-half);
  min-height: var(--s21);
  overflow: hidden;
  border: 1px solid transparent;
  padding-top: var(--s4);
  padding-bottom: var(--s4);

  @include query($sm, 'max') {
    padding: var(--s5);
  }

  &:hover {
    border-color: var(--table-shadow-color);
    border-style: solid;
    border-width: 1px;
  }

  .sort-order-cell-icon {
    text-align: right;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-left: var(--s6);

    svg.icon.fill {
      path.fill {
        fill: var(--color-grey-dark);
      }

      &.enabled path.fill {
        fill: var(--color-grey-medium);
      }
    }

    .ui-checkbox {
      padding: 0;
      display: inline-block;
      margin: 0;

      .checkbox:disabled::before {
        border-color: var(--input-background);
      }

      span {
        display: none;
      }
    }

    @include query($sm, 'max') {
      flex-direction: column-reverse;
      margin-left: 0;
      margin-right: var(--s5);
      margin-top: var(--s1);
      align-self: self-start;

      .ui-checkbox {
        margin-bottom: var(--s4);
      }
    }
  }

  .sort-order-cell {
    justify-self: center;

    .sort-order-cell-wrapper {
      height: var(--s6);
      width: var(--s6);
      line-height: var(--s6);
      text-align: center;
    }

    @include query($sm, 'max') {
      border-bottom: 1px solid var(--table-shadow-color);
      height: 100%;
      padding-top: var(--s2px);
    }
  }

  .content-link {
    padding: var(--s1) var(--s5) var(--s1) var(--s6);

    & > a {
      color: var(--link-color);
      font-weight: var(--font-weight-bold);
    }

    @include query($sm, 'max') {
      padding-top: 0;
      padding-right: 0;
      padding-left: 0;
    }
  }

  .sort-order-cell + .content-link {
    border-left: 1px solid var(--table-shadow-color);
    height: 100%;

    @include query($sm, 'max') {
      border: none;
      border-bottom: 1px solid var(--table-shadow-color);
      padding-left: var(--s2);
      padding-bottom: var(--s4);
    }
  }

  .display-date:before {
    @include query($sm, 'max') {
      content: 'Updated ';
    }
  }

  .sort-order-cell,
  .display-date,
  .display-content-status {
    color: var(--project-color-secondary);
  }

  .content-actions-row-menu {
    text-align: right;
    padding-right: var(--s5);

    @include query($sm, 'max') {
      padding-right: 0;
    }
  }

  .tags-list-container {
    padding-left: var(--s6);
    padding-top: var(--s3);
    border-left: 1px solid var(--table-shadow-color);
    margin-top: 0;

    @include query($sm, 'max') {
      padding-left: 0;
      padding-top: 0;
      border: none;
    }
  }

  &.is-selected,
  &.is-dragging {
    border: 1px solid var(--color-orange-light);

    svg.icon.fill.enabled {
      path.fill {
        fill: var(--color-orange);
      }
    }
  }
}

.theme-light {
  .content-list-card {
    .sort-order-cell-icon {
      svg.icon.fill {
        path.fill {
          fill: var(--color-grey-lighter);
        }

        &.enabled path.fill {
          fill: var(--color-grey-light);
        }
      }

      .ui-checkbox .checkbox:disabled::before {
        border-color: var(--color-grey-light);
      }
    }

    &.is-selected,
    &.is-dragging {
      border: 1px solid var(--color-orange-light);

      svg.icon.fill.enabled {
        path.fill {
          fill: var(--color-orange);
        }
      }
    }
  }
}

.content-list.in-drag-mode .content-list-card {
  &.is-selected:not(.is-dragging) {
    transform: none !important;
    transition: none !important;
    height: 1px !important;
    min-height: 0;
    padding: 0;
    margin: 0;
    border: none;
    visibility: hidden;
  }

  &.is-dragging-multiple {
    .sort-order-cell {
      .sort-order-cell-wrapper {
        border-radius: 50%;
        background-color: var(--color-orange);
        color: var(--color-white);
        height: var(--s6);
        width: var(--s6);
        line-height: var(--s6);
        justify-self: center;
      }

      @include query($sm, 'max') {
        padding-top: var(--s1);
      }
    }
  }
}
