@import 'src/styles/includes';

.add-analysis-type-container {
  @extend %max-content-width;

  padding: var(--content-spacing) var(--container-spacing) var(--s12);

  .card-container {
    display: flex;
    flex-direction: column;
    margin-bottom: var(--s12);

    @include query($md) {
      flex-direction: row;
      justify-content: space-between;
    }

    @include query($lg) {
      justify-content: flex-start;
    }

    .card {
      background-color: var(--background-primary);
      padding: var(--content-spacing);
      box-shadow: 0 -1px 0 0 var(--color-grey-medium);
      border-radius: 2px;
      border: none;

      .theme-light & {
        box-shadow: 0 1px 0 0 rgba(0,0,0,0.16);
      }
    }
  }

  .sharepointlink {
    float: right;
  }

  .card {
    display: inline-block;

    @include query($sm, max) {
      margin-bottom: var(--s4);

      &:last-of-type {
        margin-bottom: 0;
      }
    }

    @include query($md) {
      width: 48%;

      &:first-of-type {
        margin-right: var(--s4);
      }
    }

    @include query($lg) {
      width: rem(410);
    }

    & > a {
      --icon-primary-color: var(--color-white);
      align-items: center;
      background-color: var(--button-primary-color);
      border-radius: var(--s2px);
      color: var(--color-white);
      display: inline-flex;
      height: var(--s12);
      padding: 0 var(--s6);

      &:hover {
        color: var(--color-white);
        background-color: var(--color-orange-hover);
      }
    }

    .card-content {
      margin-bottom: var(--s6);
    }

    & > .title {
      font-size: var(--font-size-l);
      font-weight: var(--font-weight-bold);
      line-height: var(--lineheight-l);
      margin-bottom: var(--s6);
      margin-top: var(--s4);
    }
  }

  footer {
    text-align: right;
  }

  .highlight {
    color: var(--color-orange-theme);
    text-decoration: underline;
  }

  hr {
    background-color: white;
    margin-bottom: 2rem;
    margin-top: 3rem;
  }

  p {
    font-size: var(--font-size-l);
    margin-bottom: var(--s12);

    @include query($md, 'max') {
      font-size: var(--font-size-regular);
      margin-bottom: var(--s7);
    }
  }

  .icon {
    margin-right: var(--s2);
  }
}

.add-analysis-type-container .card-container {
  background: none;
  border: none;
  box-shadow: none;
  padding: 0;
}

.add-analysis-type-container .card-container:hover {
  border: none;
  box-shadow: none;
}
