.survey-sidebar-container {
  padding: var(--s4);

  .survey-sidebar-header {
    color: var(--text-primary);
    font-size: var(--s5);
    border-bottom: 1px solid var(--color-separator);
    padding-bottom: var(--s4);
    font-weight: 500;
  }

  .progress-ph {
    margin-right: var(--s3);
    --double-circle-width: 24px; // Jack said I could hardcode this

    .ui-progress-circle-label {
      color: var(--text-secondary);
    }
  }

  .response-results {
    a {
      color: var(--color-orange-theme);

      &:hover {
        color: var(--color-orange-theme);
      }
    }
  }
  .prefill-survey-container {
    padding-top: var(--s4);
    display: flex;
    justify-content: center;
    flex-direction: column;

    .prefill-survey-button-container {
      display: flex;
      justify-content: center;
    }

    .prefill-survey-error {
      color: var(--color-red);
      text-align: center;
    }
  }
}
