@import 'src/styles/includes';

.analysis-top-bar-container {
  border-bottom: var(--border-standard);
  padding: 0 var(--container-spacing);

  @include query($md, max) {
    padding-right: calc(var(--s19) + var(--s2px));
  }

  .analysis-top-bar {
    align-items: center;
    display: flex;
    flex-direction: row;
    height: var(--s16);
    justify-content: space-between;

    @include query($sm, 'max') {
      padding-left: 0;

      .ui-tooltip {
        .ui-tooltip-tip {
          display: none;
        }
      }
    }
    @include query($md, 'max') {
      padding-right: 0;
    }

    @include query($lg) {
      width: 100%;
    }

    .analysis-pill-container {
      flex: 1;
      margin: 0 0 var(--s1) var(--s1);
    }

    & > div {
      display: flex;
    }

    & > div:not(:first-of-type) > button {
      margin: 0 var(--s2);
    }

    & > div:not(:first-of-type) > button:last-of-type {
      margin-right: 0;
    }
  }
}
