@import 'src/styles/includes';

.project-disclaimer-container {
  color: var(--project-color-main);
  font-family: var(--font-family);
  max-width: 800px;
  padding: var(--s8) var(--s4);

  @include query($lg) {
    padding: var(--s12);
  }

  .project-disclaimer {
    @include scrollbars();
    padding-top: var(--s6);
    width: 100%;

    @media screen and (min-width: 767px) and (min-height: 750px) {
      height: calc(100vh - 410px);
      overflow: auto;
    }
  }

  .separator {
    margin: var(--s6) var(--s5) var(--s6) 0;
    width: auto;
  }

  .project-disclaimer-title {
    font-size: var(--font-size-xxl);
    font-weight: var(--font-weight-medium);
    line-height: var(--lineheight-xxl);
    margin-bottom: rem(30);
  }

  .project-disclaimer-prompt {
    font-size: var(--font-size-l);
    font-weight: var(--font-weight-regular);
    line-height: var(--lineheight-l);
  }

  .project-disclaimer-content {
    font-size: var(--font-size-sm);
    font-weight: var(--font-weight-regular);
    line-height: var(--lineheight-regular);
    margin-right: var(--s5);
  }

  .project-disclaimer-actions {
    align-items: center;
    display: flex;

    & > * {
      white-space: nowrap;
      width: 100%;
    }

    @media screen and (min-width: 767px) {
      justify-content: flex-end;
      padding-top: var(--s6);
      padding-right: var(--s10);
      & > * {
        margin: var(--s4);
        width: auto;
      }
      a {
        color: var(--color-orange-theme);
        border-bottom: 1px solid;

        &:hover {
          color: var(--color-orange-theme);
          text-decoration: none;
        }
      }
    }

    @media screen and (max-width: 767px) {
      flex-direction: column-reverse;
      padding: var(--s4) 0;
      border-top: var(--s2px) solid var(--border-primary);

      & > * {
        margin: var(--s2) 0;
        text-align: center;
      }

      & > a {
        border: var(--s2px) solid var(--color-orange);
        padding: var(--s2);
      }
    }
  }
}
