@import 'src/styles/includes';

.list-grid-analyses {
  padding: 0 0 var(--s12) 0;

  @media screen and (max-width: 767px) {
    padding: 0 0 var(--s4) 0;
  }

  .row {
    margin-left: 0;
    margin-right: 0;

    .pill-container {
      flex-wrap: wrap;
      flex-basis: 80%;
    }
  }

  .col {
    @media screen and (max-width: 767px) {
      display: flex;
    }

    &.align-center {
      align-items: center;
    }
  }
}
