@import 'src/styles/includes';

:root {
  --navbar-width: var(--s21);

  @include query($xxl) {
    --navbar-width: var(--navbar-width-xxl);
  }
}

.list-grid {
  padding-top: var(--content-spacing);

  @include query($md) {
    max-width: calc(
      100vw - var(--navbar-width) - var(--container-spacing) -
        var(--container-spacing)
    );
  }
}

.list-container-header {
  display: flex;
  justify-content: space-between;

  h2 {
    margin-bottom: var(--s2);

    @include query($sm, max) {
      margin-bottom: var(--s6);
    }
  }
}

.list-table {
  padding-top: var(--content-spacing);

  @include query($sm, 'max') {
    flex-direction: column;
  }

  .header-row {
    display: flex;
    justify-content: space-between;
    padding: var(--s4) var(--content-spacing);

    @include query($sm, 'max') {
      display: none;
    }

    & .header-cell {
      align-items: center;
      color: var(--project-color-secondary);
      display: flex;
      font-weight: var(--font-weight-bold);
      width: 18%;

      &:first-of-type {
        padding-left: 44px;
        width: calc(42% - var(--s7));
      }

      &:last-of-type {
        width: var(--s10);
      }

      &:hover {
        text-decoration: underline;
      }

      &:active {
        color: var(--color-grey-alt);
        text-decoration: underline;
      }

      span:not([class*='icon-table-sort-']) {
        display: none;
      }

      span.icon-table-sort-desc {
        margin-left: var(--s3);
        transition: transform 150ms ease;
        margin-top: calc(-1 * var(--s1));

        &.asc {
          transform: rotate(-180deg) translate(-25%, -25%);
          transform-origin: center;
          transition: transform 150ms ease;
        }

        svg > path {
          fill: var(--text-alternate);
          overflow: visible;
        }
      }
    }
  }

  .row-group {
    margin: 0;
  }

  .list-table-row {
    align-items: center;
    background-color: var(--background-primary);
    border-radius: var(--table-border-radius);
    box-shadow: var(--card-shadow);
    display: flex;
    justify-content: space-between;
    margin-bottom: var(--margin-half);
    min-height: var(--s21);
    overflow: hidden;
    border: 1px solid transparent;
    padding: 0 var(--content-spacing);

    &:last-child {
      margin-bottom: 0;
    }

    &.content-list-table-row {
      flex-direction: column;
      justify-content: center;
      padding: var(--s4) var(--content-spacing);

      &:hover {
        border-style: solid;
        border-width: 1px 1px 1px 1px;
      }
    }

    &:not(.content-list-table-row) {
      @include query($sm, 'max') {
        display: grid;
        grid-template-columns: 33% 33% 33%;
        grid-auto-flow: row;
        height: auto;
        padding: var(--s4);
      }
    }

    &:hover {
      border-color: var(--table-shadow-color);
      border-style: solid;
      border-width: 0 1px 1px 1px;
    }

    a,
    a:visited,
    a:active {
      color: var(--project-color-main);
      font-size: var(--font-size-regular);
      font-weight: var(--font-weight-bold);
      line-height: var(--lineheight-regular);

      &:hover {
        color: var(--project-color-main);
        text-decoration: underline;
      }
    }

    .list-table-subrow {
      align-items: center;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 100%;

      &:first-child {
        @include query($sm, 'max') {
          display: grid;
          grid-template-columns: repeat(3, 33%);
          height: auto;
          grid-gap: var(--s4) 0;
        }

        .title-cell {
          @include query($sm, max) {
            border-bottom: var(--s1px) solid var(--table-border-color);
            padding-bottom: var(--content-spacing);
          }
          margin: 0;
          padding: 0;
          overflow: visible;

          > a {
            margin: 0;
          }
        }
      }

      &.list-table-row-sub-component:not(:empty) {
        flex-direction: column;
        align-items: flex-start;
      }
    }

    .cell {
      @include query($sm, 'max') {
        text-align: center;

        &:nth-of-type(2) {
          grid-column: 1;
          text-align: left;
        }

        &:nth-of-type(3) {
          text-align: left;
        }

        &:nth-of-type(4) {
          grid-column: 1;
          text-align: left;
        }

        &:nth-of-type(5) {
          text-align: left;
        }

        &:last-of-type {
          grid-column: 3;
          text-align: right;
          width: auto;
          overflow: visible;
        }
      }

      @include query($xl) {
        overflow: hidden;
        width: 18%;

        .clamp-lines div {
          text-overflow: ellipsis;
          white-space: nowrap;
          width: 90%;
          overflow: hidden;
        }

        &:first-of-type {
          width: calc(42% - var(--s7));
        }

        &:last-of-type {
          overflow: visible;
          width: var(--s10);
        }
      }

      @include query($lg, 'max') {
        overflow: hidden;
        width: 18%;

        .clamp-lines div {
          text-overflow: ellipsis;
          white-space: nowrap;
          width: 90%;
          overflow: hidden;
        }

        &:first-of-type {
          width: calc(42% - var(--s7));
        }

        &:last-of-type {
          overflow: visible;
          width: var(--s10);
        }
      }

      @include query($md, 'max') {
        overflow: hidden;

        .clamp-lines div {
          text-overflow: ellipsis;
          white-space: nowrap;
          width: 90%;
          overflow: hidden;
        }

        &:first-of-type {
          width: calc(42% - var(--s7));
        }

        &:last-of-type {
          overflow: visible;
          width: var(--s10);
        }
      }

      @include query($sm, 'max') {
        overflow: hidden;
        width: auto;

        .clamp-lines div {
          text-overflow: ellipsis;
          white-space: nowrap;
          width: 90%;
          overflow: hidden;
        }

        &:first-of-type {
          width: auto;
        }

        &:last-of-type {
          width: auto;
          overflow: visible;
        }
      }
    }

    .bold-cell {
      color: var(--project-color-main);
      font-weight: var(--font-weight-bold);

      @include query($sm, 'max') {
        font-size: var(--font-size-xl);
        text-align: left;
        grid-column: 1 / 4;
      }
    }

    strong.clamp-lines {
      font-weight: var(--font-weight-bold);
    }

    .right-align-cell {
      justify-content: flex-end;
    }
  }
}
