@import 'src/styles/includes';

%multipleAreas {
  gap: var(--s3);
}

%sideBySide {
  .panel-area-1 {
    padding-right: var(--s3);
    border-right: var(--s1px) solid var(--color-grey-medium);

    @include query($sm, max) {
      padding-right: 0;
      padding-bottom: var(--s3);
      border-bottom: var(--s1px) solid var(--color-grey-medium);
      border-right: none;
    }
  }
  .panel-area-2 {
    padding-left: var(--s3);

    @include query($sm, max) {
      padding-top: var(--s3);
    }
  }
}

@mixin stackedBorders() {
  .panel-area-1 {
    padding-bottom: var(--s3);
    border-bottom: var(--s1px) solid var(--color-grey-medium);
  }
  .panel-area-2 {
    padding-top: var(--s3);
  }
}

%mobileStyles {
  @include query($sm, max) {
    display: block;
  }
}

.section-onecolumn {
  display: flex;
  flex-direction: column;
  align-content: center;
  border-radius: 2px;
  width: 100%;
  margin-bottom: var(--s12);

  li > ul:not(:empty) {
    border-top: none;
  }

  &--eighty-percent {
    width: 80%;
  }

  &--sixty-percent {
    width: 60%;
  }

  &--forty-percent {
    width: 40%;
  }

  &--eighty-percent,
  &--sixty-percent,
  &--forty-percent {
    @include query($sm, max) {
      width: 100%;
    }
  }

  &--one-column-overflow {
    .analysis-library-item-container {
      padding-left: 0;
      padding-right: 0;
    }
  }
}

.section-grid {
  margin: var(--s6) 0;
  display: grid;

  > .panel-area {
    overflow: hidden;
  }

  &.two-row-two-column-50-50 {
    @extend %multipleAreas;
    grid-template-columns: 1fr 1fr;

    .panel-area-1 {
      position: relative;
      overflow: visible;

      &::before {
        @include query($md, min) {
          position: absolute;
          top: 0;
          right: calc(var(--sn1) + var(--sn3px));
          bottom: 0;
          content: '';
          width: var(--s1px);
          margin-bottom: calc(var(--sn3));
          border-right: var(--s1px) solid var(--color-grey-medium);
          @include query($md, min) {
            margin-bottom: var(--s3);
          }
        }
      }
      &::after {
        @include query($md, min) {
          position: absolute;
          bottom: calc(var(--sn1) + var(--sn3px));
          left: 0;
          right: 0;
          content: '';
          height: var(--s1px);
          margin: 0 var(--s3);
          border-bottom: var(--s1px) solid var(--color-grey-medium);
          @include query($md, max) {
            margin-right: var(--sn1);
          }
        }
      }
    }

    .panel-area-4 {
      position: relative;
      overflow: visible;

      &::after {
        @include query($md, min) {
          position: absolute;
          top: calc(var(--sn1) + var(--sn2px));
          left: 0;
          right: 0;
          content: '';
          height: var(--s1px);
          margin: 0 var(--s3);
          border-top: var(--s1px) solid var(--color-grey-medium);
          @include query($md, max) {
            margin-left: var(--sn2);
          }
        }
      }

      &::before {
        @include query($md, min) {
          position: absolute;
          top: 0;
          left: calc(var(--sn1) + var(--sn2px));
          bottom: 0;
          content: '';
          width: var(--s1px);
          margin-top: var(--s3);
          border-left: var(--s1px) solid var(--color-grey-medium);
        }
      }
    }

    @include query($md, max) {
      .panel-area-1 {
        order: 1;
      }
      .panel-area-3 {
        order: 2;
        @include query($sm, max) {
          border-bottom: var(--s1px) solid var(--color-grey-medium);
        }
      }
      .panel-area-2 {
        order: 3;
      }
      .panel-area-4 {
        order: 4;
      }
    }

    @include query($sm, max) {
      display: flex;
      flex-direction: column;
    }
  }

  &.two-row-three-column {
    @extend %multipleAreas;
    grid-template-columns: 1fr 1fr 1fr;

    .panel-area {
      margin: calc(var(--sn1) + var(--sn2px));
    }

    @include query($sm, max) {
      .panel-area-4,
      .panel-area-5 {
        border-bottom: var(--s1px) solid var(--color-grey-medium);
      }
    }

    @include query($md, min) {
      .panel-area-1,
      .panel-area-2,
      .panel-area-4,
      .panel-area-5 {
        border-right: var(--s1px) solid var(--color-grey-medium);
        @include query($md, max) {
          border-right: none;
          border-bottom: var(--s1px) solid var(--color-grey-medium);
        }
      }
    }

    @include query($lg, min) {
      .panel-area-1,
      .panel-area-2,
      .panel-area-3 {
        position: relative;

        &::after {
          position: absolute;
          bottom: 0;
          left: 0;
          right: 0;
          content: '';
          height: var(--s1px);
          margin: 0 var(--s6);
          border-bottom: var(--s1px) solid var(--color-grey-medium);
        }
      }
    }
    @include query($md, min) {
      @include query($md, max) {
        .panel-area-1,
        .panel-area-2,
        .panel-area-3 {
          position: relative;

          &::after {
            position: absolute;
            bottom: 0;
            top: 0;
            right: 0;
            content: '';
            width: var(--s1px);
            margin: var(--s6) 0;
            border-right: var(--s1px) solid var(--color-grey-medium);
          }
        }
      }
    }

    @include query($md, max) {
      grid-template-columns: 1fr 1fr;

      // First Row
      .panel-area-1 {
        order: 1;
      }
      .panel-area-4 {
        order: 2;
      }

      // Second Row
      .panel-area-2 {
        order: 3;
      }
      .panel-area-5 {
        order: 4;
      }

      // Third Row
      .panel-area-3 {
        order: 5;
      }
      .panel-area-6 {
        order: 6;
      }
    }

    @include query($sm, max) {
      display: flex;
      flex-direction: column;
    }
  }

  &.one-row-three-column {
    grid-template-columns: 1fr 1fr 1fr;
    @include query($md, max) {
      grid-template-columns: 1fr 1fr;
    }
    @extend %mobileStyles;

    > .panel-area {
      padding-left: var(--s3);
      padding-right: var(--s3);
      border-right: var(--s1px) solid var(--color-grey-medium);

      @include query($md, max) {
        margin: 0;
        padding: var(--s3);
        border: none;
      }

      @include query($sm, max) {
        border-bottom: var(--s1px) solid var(--color-grey-medium);
        &:last-child {
          border-bottom: none;
        }
      }

      &:first-child {
        @include query($lg, min) {
          padding-left: 0;
        }

        @include query($md, max) {
          @include query($md, min) {
            position: relative;
            &::before {
              position: absolute;
              top: 0;
              right: 0;
              bottom: 0;
              content: '';
              width: var(--s1px);
              margin: var(--s3) 0;
              border-right: var(--s1px) solid var(--color-grey-medium);
            }
            &::after {
              position: absolute;
              bottom: 0;
              left: 0;
              right: 0;
              content: '';
              height: var(--s1px);
              margin: 0 var(--s3);
              border-bottom: var(--s1px) solid var(--color-grey-medium);
            }
          }
        }
      }

      &:last-child {
        @include query($lg, min) {
          padding-right: 0;
          border-right: none;
        }
      }
    }

    .panel-area-2 {
      @include query($md, max) {
        @include query($md, min) {
          position: relative;
          &::after {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            content: '';
            height: var(--s1px);
            margin: 0 var(--s3);
            border-bottom: var(--s1px) solid var(--color-grey-medium);
          }
        }
      }
    }

    .panel-area-3 {
      @include query($md, max) {
        @include query($md, min) {
          position: relative;
          &::before {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            content: '';
            width: var(--s1px);
            margin: var(--s3) 0;
            border-right: var(--s1px) solid var(--color-grey-medium);
          }
        }
      }
    }
  }

  &.two_column_left_50_2a_r_50_1a {
    @extend %multipleAreas;
    grid-template-columns: 1fr 1fr;
    @extend %mobileStyles;
  }

  &.two-column-l67-1a-r33-1a {
    grid-template-columns: 2fr 1fr;
    grid-template-rows: 1fr;
    @extend %mobileStyles;
    @extend %sideBySide;
  }

  &.two-column-l40-1a-r60-1a {
    grid-template-columns: 2fr 3fr;
    grid-template-rows: 1fr;
    @extend %mobileStyles;
    @extend %sideBySide;
  }

  &.two-column-l80-1a-r20-1a {
    grid-template-columns: 4fr 1fr;
    grid-template-rows: 1fr;
    @extend %mobileStyles;
    @extend %sideBySide;
  }

  &.two-row-t80-1a-b20-1a {
    grid-template-columns: 1fr;
    @extend %mobileStyles;
    @include stackedBorders;
  }

  &.two-row-t60-1a-b40-1a {
    grid-template-columns: 1fr;
    @extend %mobileStyles;
    @include stackedBorders;
  }

  &.two-row-t20-1a-b80-1a {
    grid-template-columns: 1fr;
    @extend %mobileStyles;
    @include stackedBorders;
  }

  &.two_column_t_50_2a_b_50_1a {
    @extend %multipleAreas;
    grid-template-columns: 1fr 1fr;
    @extend %mobileStyles;

    & > div:nth-child(3) {
      grid-column: span 2;
    }

    .panel-area-1 {
      position: relative;
      overflow: visible;

      @include query($md, min) {
        margin-bottom: var(--s4);

        &::after {
          position: absolute;
          bottom: 0;
          top: 0;
          right: calc(var(--sn2) + var(--s1px));
          content: '';
          width: var(--s1px);
          border-right: var(--s1px) solid var(--color-grey-medium);
        }
      }

      @include query($sm, max) {
        border-bottom: var(--s1px) solid var(--color-grey-medium);
      }
    }

    .panel-area-3 {
      border-top: var(--s1px) solid var(--color-grey-medium);
    }
  }

  &.two-column-left-67-2a-right-33-1a {
    @extend %multipleAreas;
    grid-template-columns: 67% 33%;
  }

  &.two_column_left_50_2a_r_50_1a,
  &.two-column-left-67-2a-right-33-1a {
    @include query($sm, max) {
      display: flex;
      flex-direction: column;

      .panel-area-2 {
        order: 1;
      }
    }

    & > div:nth-child(2) {
      grid-row: span 2;
    }

    @include query($sm, max) {
      .panel-area-2,
      .panel-area-3 {
        border-top: var(--s1px) solid var(--color-grey-medium);
      }
    }

    @include query($md, min) {
      .panel-area-1 {
        position: relative;
        overflow: visible;
        &::after {
          position: absolute;
          bottom: calc(var(--sn2) + var(--s1px));
          left: 0;
          right: 0;
          content: '';
          height: var(--s1px);
          border-bottom: var(--s1px) solid var(--color-grey-medium);
        }
      }
      .panel-area-2 {
        position: relative;
        &::before {
          position: absolute;
          top: 0;
          left: var(--sn2);
          bottom: 0;
          content: '';
          width: var(--s1px);
          margin: 0 var(--s3);
          border-left: var(--s1px) solid var(--color-grey-medium);
        }
      }
    }
  }

  &.two-column-four-area-3left50-right50 {
    @extend %multipleAreas;
    grid-template-columns: 1fr 1fr;

    .panel-area-1,
    .panel-area-3,
    .panel-area-4 {
      margin-right: var(--s3);
    }

    .panel-area-2 {
      @include query($md, min) {
        border-left: var(--s1px) solid var(--color-grey-medium);
      }
      @include query($sm, max) {
        border-top: var(--s1px) solid var(--color-grey-medium);
      }
    }

    .panel-area-1,
    .panel-area-3 {
      border-bottom: var(--s1px) solid var(--color-grey-medium);
    }

    @include query($sm, max) {
      display: flex;
      flex-direction: column;

      .panel-area-2 {
        order: 1;
      }
    }

    & > div:nth-child(2) {
      grid-row: span 3;
    }
  }

  &.four-block-narrow-right {
    @extend %multipleAreas;
    grid-template-columns: 67% 33%;
    @extend %mobileStyles;

    .panel-area-1 {
      position: relative;
      overflow: visible;

      &::before {
        @include query($md, min) {
          position: absolute;
          top: 0;
          right: calc(var(--sn1) + var(--sn3px));
          bottom: 0;
          content: '';
          width: var(--s1px);
          margin-bottom: calc(var(--sn3));
          border-right: var(--s1px) solid var(--color-grey-medium);
          @include query($md, min) {
            margin-bottom: var(--s3);
          }
        }
      }
      &::after {
        @include query($md, min) {
          position: absolute;
          bottom: calc(var(--sn1) + var(--sn3px));
          left: 0;
          right: 0;
          content: '';
          height: var(--s1px);
          margin-right: var(--sn1);
          border-bottom: var(--s1px) solid var(--color-grey-medium);
          @include query($md, max) {
            margin-right: var(--sn1);
          }
        }
      }
    }

    @include query($sm, max) {
      .panel-area-2 {
        border-bottom: var(--s1px) solid var(--color-grey-medium);
      }
    }

    .panel-area-4 {
      position: relative;
      overflow: visible;

      &::after {
        @include query($md, min) {
          position: absolute;
          top: calc(var(--sn1) + var(--sn2px));
          left: 0;
          right: 0;
          content: '';
          height: var(--s1px);
          margin-left: var(--sn2);
          border-top: var(--s1px) solid var(--color-grey-medium);
          @include query($md, max) {
            margin-left: var(--sn2);
          }
        }
      }

      &::before {
        @include query($md, min) {
          position: absolute;
          top: 0;
          left: calc(var(--sn1) + var(--sn2px));
          bottom: 0;
          content: '';
          width: var(--s1px);
          margin-top: var(--s3);
          border-left: var(--s1px) solid var(--color-grey-medium);
        }
      }
    }
  }
}
