@import 'src/styles/includes';

.edit-layout-tabs {
  @include scrollbars(0, transparent, transparent, transparent);
  overflow-y: auto;
  background-color: var(--background-secondary);
}

.edit-layout-tab-list {
  align-items: flex-end;
  background-color: var(--background-secondary);
  display: flex;
  flex-direction: row;
  height: var(--s14);
  list-style: none;
  margin: 0;
  padding: 0;
  width: -webkit-min-content;
  width: min-content;

  &:focus {
    outline: 1px auto var(--text-primary);
  }
}

.edit-layout-tab-list-item {
  background-color: var(--tab-background-color);
  border-width: 0;
  color: var(--tab-text-color);
  cursor: pointer;
  font-size: var(--font-size-regular);
  line-height: var(--s12);
  margin-left: rem(6);
  padding: 0 var(--s6);
  white-space: nowrap;

  @include query($sm, max) {
    padding: 0 var(--s5);
  }

  &:last-of-type {
    margin-right: rem(6);
  }
}

.edit-layout-tab-list-active {
  background-color: var(--tab-background-color-active);
  color: var(--tab-text-color-active);
}

.edit-layout-tab-list-inactive {
  color: var(--color-grey-medium);
  cursor: default;

  &:hover {
    text-decoration: none;
  }
}
