@import 'src/styles/includes';

div.drop-area__droppable-container .background-text {
  max-width: 20rem;
  color: var(--file-list-panel-text);
}

.droppable-area {
  border-radius: var(--s2px);
  min-height: 220px;
  border: 1px dashed var(--color-grey);
  background-color: var(--background-primary);
}

.drop-area__droppable-container {
  position: relative;

  @include query($sm, max) {
    min-height: calc(var(--s25) + var(--s13) - var(--s2px));
    margin: var(--s3) 0 calc(var(--s4) + var(--s2px)) 0;
  }

  .drop-area__background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: var(--s2);
    text-align: center;
    display: flex;
    flex: 1 1 auto;

    &__content {
      display: flex;
      flex-direction: column;
      flex: 1 1 auto;
      justify-content: center;
      align-items: center;
    }

    &__hint {
      margin-bottom: calc(var(--s3px) + var(--s12));
      max-width: calc(var(--s25) + var(--s15));
      color: var(--file-list-panel-text);
      font-size: var(--font-size-regular);
      font-weight: var(--font-weight-medium);
      letter-spacing: 0;
      line-height: var(--s6);
      text-align: center;

      @include query($sm, max) {
        margin: var(--s4) 0 0 0;
      }

      &.hint-error {
        color: var(--color-error);
      }
    }
  }
  
}
