@import 'src/styles/includes';

.list-grid-hypotheses {
  .card-container {
    padding: var(--content-spacing);
    margin-bottom: var(--s6);

    &:last-child {
      margin-bottom: 0;
    }

    @include query(960) {
      padding: var(--content-spacing) 0;
    }

    .card-details {
      @include query(960) {
        padding-left: var(--content-spacing);
        padding-right: var(--content-spacing);
      }
    }

    .card-title a h2 {
      margin-bottom: var(--s4);
      font-size: var(--font-size-xl);
      font-weight: var(--font-weight-bold);
      line-height: var(--lineheight-xl);
    }

    .card-description {
      @extend %clamp-4;
      color: var(--project-color-main);
      margin-bottom: var(--s6);
      padding-bottom: 0;
    }

    .tags-list-container {
      padding: 0;

      @include query(960) {
        padding-left: var(--content-spacing);
        padding-right: var(--content-spacing);
      }
    }
  }

  .narrative-section {
    .narrative-card {
      @include query($sm, max) {
        margin-bottom: var(--s6);
      }
    }
  }

  .connected-kfas-section {
    @include query($md, max) {
      display: none;
    }
  }

  .hypothesis-card-contents {
    @include query($lg) {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
    }

    .card-title {
      word-break: break-word;
    }
  }

  .card-narrative-mobile {
    padding-bottom: var(--s4);
    margin: var(--s4) 0 var(--s4) 0;
    border-bottom: var(--s1px) solid var(--color-separator);

    @include query($lg) {
      display: none;
    }
  }

  .hide-mobile .narrative-section-key-focus-areas {
    padding-top: var(--s6);
    margin-top: var(--s6);
    margin-right: var(--s12);
    border-top: var(--kfa-border);
  }
}

.card-insights,
.card-analyses {
  .narrative-section .narrative-card {
    height: calc(var(--s20) * 2);
    padding-top: var(--s2);
    padding-right: var(--s12);
    border-radius: 0;
    overflow: hidden;
  }

  $narratives: 10;
  @for $i from 0 through $narratives {
    &.total-#{$i} {
      @for $b from 1 through $i {
        .narrative-card:nth-of-type(#{$b}) {
          border-bottom: var(--kfa-border);
        }
      }
    }
  }
}

.hypothesis-grid-card-footer {
  display: flex;
  justify-content: space-between;

  @include query(960) {
    border-top: var(--kfa-border);
    margin-top: var(--s6);
    padding-top: var(--s6);
    margin-left: var(--content-spacing);
    margin-right: var(--content-spacing);
  }
}
