@import 'src/styles/includes';

.rte-formik-field {
  align-items: center;
  display: flex;
  margin-bottom: rem(30);
  position: relative;

  .text-input-fieldset {
    position: relative;
    padding: 0;
    width: 100%;

    &.d3-rich-text {
      max-width: none;
    }

    label.text-input-control-label {
      padding: var(--s5) 0 0 var(--s3);
      position: absolute;
      z-index: 1;
      color: var(--input-color-placeholder);
      font-size: var(--font-size-regular);
      font-weight: var(--font-weight-regular);

      @include query($md) {
        font-size: var(--font-size-l);
      }
    }

    .ck.ck-editor__main > .ck-editor__editable {
      min-height: var(--s17);
      transition-duration: 0s !important;

      &_inline {
        padding-top: var(--s2) !important;
        padding-left: var(--s1) !important;
      }

      &:not(.ck-editor__nested-editable).ck-focused {
        box-shadow: none;
        padding-left: var(--s2px) !important;
      }
    }
  }

  .icon {
    display: block;
    position: absolute;
    right: 0;
    z-index: 1;
    height: var(--s5);
    margin-right: var(--s3);
    pointer-events: none;
  }

  &.label-lifted {
    .text-input-fieldset {
      label.text-input-control-label {
        padding: var(--s1) 0 0 var(--s4);
        font-size: var(--font-size-xs);
      }
    }

    .icon {
      display: none;
    }
  }

  .ck.ck-toolbar {
    display: none;
  }

  .ck-editor__editable.ck-blurred {
    min-height: unset;
  }
}

.ck-rounded-corners .ck.ck-editor__main > .ck-editor__editable,
.ck.ck-editor__main > .ck-editor__editable.ck-rounded-corners {
  border-radius: var(--s1) !important;
}
