@import 'src/styles/includes';

.add-project-items-header-menu {
  text-align: right;
  width: 100%;

  @include query($md) {
    display: none;
  }
}

#popover-add {
  max-width: 285px;

  .popover-body {
    border-radius: var(--s1);
    border: var(--popup-border);
    box-shadow: var(--popup-shadow);
    font-size: var(--font-size-regular);
    background-color: var(--popup-background-color);
    width: auto;
    color: var(--popup-color);
  }
}

.header-add-popover {
  .dropdown-item {
    align-items: center;
    color: var(--color-orange-theme);
    display: flex;
    margin-bottom: var(--s5);
    padding: 0;

    &:last-child {
      margin-bottom: 0;
    }

    .icon {
      margin-right: var(--s2);
    }

    &:focus,
    &:hover {
      background-color: transparent;
      color: var(--color-orange-theme);
      text-decoration: underline;

      .icon {
        .fill {
          fill: var(--color-orange-theme);
        }

        .outline {
          visibility: hidden;
        }
      }
    }
  }
}

.add-header-popover-overlay {
  background: var(--color-black);
  bottom: 0;
  display: block;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 2;
  opacity: 0.35;
}

.freeze .add-popover-button {
  @include query($sm, 'max') {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.3s, opacity 0.3s linear;
  }
}
