@import 'src/styles/includes';

.diagnostic-content-container {
  width: 100%;

  .progress-bar {
    background-color: var(--color-page-background);
    flex-direction: row;
    max-width: none;
    padding: var(--s4) 0 var(--s4) var(--s10);
    margin: 0 var(--s8) 0 0;

    .progress-info {
      color: var(--project-color-secondary);
      font-size: rem(18);
      font-weight: 300;
    }
  }

  hr {
    border-bottom: 1px solid var(--border-primary);
    width: 100%;
  }

  .diagnostic-content {
    padding: 0 var(--s10);
    max-width: rem(1000);
    width: 100%;

    .dd-question-wrapper {
      margin-top: var(--s10);
    }
  }
}
