.accordion-connected-content {
  .accordion {
    padding-top: var(--s3);
    margin-top: var(--s4);
  }

  .narrative-section {
    margin-top: var(--s4);

    .narrative-card {
      margin-top: var(--s3);
      margin-bottom: 0;
      padding: 0;
      border-top: none;

      &-title {
        font-size: var(--font-size-normal);
        font-weight: var(--font-weight-medium);
        line-height: var(--lineheight-m);
      }
    }

    .narrative-section-title {
      font-weight: var(--font-weight-medium);
    }
  }

  &.loading {
    padding-top: var(--s6);
    padding-bottom: var(--s6);
  }
}
