@import '../../../../../../styles/includes';

.topic-text-input-width-limit {
  .text-input-fieldset {
    .text-input-container.datalist {
      .text-input {
        @include query($lg) {
          width: 40%;
        }

        @include query($sm, max) {
          width: 100%;
        }
      }
    }
  }
}
