.bridge-chart-container {
  .highcharts-root {
    padding: 0 var(--content-spacing) var(--content-spacing);
  }

  .highcharts-title {
    padding: var(--content-spacing) var(--content-spacing) 0;
  }

  .highcharts-background {
    fill: transparent;
  }

  .highcharts-graph {
    stroke-dasharray: 2, 4;
  }
}

.tri-pane-container > section:nth-of-type(1) .bridge-chart-container {
  padding: 0 0 var(--content-spacing);
}
