@import 'src/styles/includes';

.password-wrapper {
  margin-top: var(--s8);

  .content {
    display: flex;

    svg {
      margin-top: .4rem;
    }

    div {
      margin-left: var(--s3);
      font-size:  var(--font-size-s);
    }
  }

  input {
    margin-top: var(--s3);
    background: var(--tageditor-background-color);
    border: var(--s1px) solid var(--input-color-placeholder);
    border-radius: var(--s2px);
    color: var(--input-color);
    font-size: var(--font-size-regular);
    font-weight: var(--font-weight-regular);
    height: var(--s12);
    line-height: var(--input-lineheight);
    padding-left: var(--s3);
    width: rem(350);
  }
}