@import 'src/styles/includes';

html.mobile-sidenav-open main {
  opacity: 0;
}

.app-layout {
  &.freeze {
    @include query($sm, max) {
      background-color: var(--background-default);
      height: calc(100vh + 120px);

      .side-bar-logo {
        width: 100%;
      }

      main {
        .page-content,
        .project-list-layout {
          opacity: 0;
        }
      }
    }
  }

  .side-bar {
    background: var(--background-default);
    width: 0;

    .navigation .menu-item:hover {
      text-decoration: none;
    }

    @include query($sm, max) {
      &.true {
        // min-height: 100vh;
        padding-bottom: rem(150);
        overflow-y: auto;
        // -webkit-overflow-scrolling: touch;
      }
    }

    @include query($md) {
      position: fixed;
      width: var(--s21);
      z-index: 1;
      border-right: var(--s1px) solid var(--border-primary);
    }

    @include query($xxl) {
      width: var(--navbar-width-xxl);
    }

    .side-bar-logo {
      align-items: center;
      background: var(--background-default);
      border-bottom: var(--s1px) solid var(--border-primary);
      display: flex;
      height: var(--s16);
      justify-content: center;

      @include query($sm, 'max') {
        justify-content: flex-start;
        position: absolute;
        z-index: 1;
      }

      @include query($xxl) {
        justify-content: flex-start;
        padding-left: rem(19);
      }

      svg #Navigation {
        fill: var(--color-logo);
      }
    }
  }

  main {
    background: var(--color-page-background);
    width: 100%;
  }
}

.content-container {
  max-width: rem(1920);
}

.freeze {
  main {
    @include query($sm, max) {
      position: fixed;
    }
  }
}
