@import 'src/styles/includes';

$verticalSpacing: var(--s4);

.file-list-item {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: repeat(5, 1fr);
  align-items: center;

  background-color: var(--background-primary);
  box-shadow: var(--card-shadow);
  border-radius: var(--s2px);
  padding: var(--content-spacing);
  border: var(--s1px) solid transparent;
  margin-bottom: $verticalSpacing;
  color: var(--file-list-panel-text);

  .file-status-button {
    display: flex;
    align-items: center;
  }

  @media (max-width: #{$md + 'px'}) {
    display: block;
  }

  &--three-columns {
    grid-template-columns: repeat(3, 1fr);
  }

  > div {
    line-height: 2; // Multiplier not PX

    @media (max-width: #{$md + 'px'}) {
      margin: $verticalSpacing 0;
      line-height: 1; // Multiplier not PX

      &:first-child {
        margin-top: 0;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__date {
    @media (max-width: #{$md + 'px'}) {
      border-top: 1px solid var(--color-grey-medium);
      padding-top: $verticalSpacing;
    }
  }

  &__file-name {
    color: var(--project-color-main);
  }

  &__file-name,
  &__uploaded-by {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  & > &__uploaded-by {
    @media (max-width: #{$md + 'px'}) {
      line-height: 1.3;
    }
  }

  &__uploaded-by,
  &__date {
    @media (max-width: #{$md + 'px'}) {
      font-size: var(--font-size-s);
    }
  }

  &__file-name {
    @media (max-width: #{$md + 'px'}) {
      font-size: var(--font-size-regular);
      font-weight: bold;
    }
  }
}
