@import 'src/styles/includes';

.form-layout {
  display: flex;
  flex-direction: column;

  .form-content-container {
    background: var(--color-page-background);
    display: flex;
    flex: auto;
    max-width: calc(
      100vw - var(--container-spacing) - var(--container-spacing) +
        var(--content-spacing)
    );
    min-height: var(--content-min-height);
    position: relative;
    width: 100%;

    @include query($lg, max) {
      @include query($md, min) {
        max-width: calc(
          100vw - var(--container-spacing) - var(--container-spacing) +
            var(--content-spacing) - 4rem
        );
      }
    }

    @include query($sm, 'max') {
      max-width: unset;
    }
  }

  .form-content {
    @extend %max-content-width;

    padding: var(--content-spacing) var(--container-spacing) var(--s12);

    @include query($sm, 'max') {
      h1 {
        font-size: var(--font-size-xl);
      }
    }
  }

  .form-content-container-background {
    background: var(--color-page-background);
  }

  .form-fields {
    width: 100%;

    @include query($lg) {
      max-width: 80%;
    }
  }

  .back-button {
    color: var(--color-orange-theme);
    padding-bottom: var(--s3);
    margin-left: 0;
  }
}

.form-bottom-actions {
  display: flex;
  justify-content: center;
  flex-direction: column-reverse;
  padding-top: var(--s6);

  @include query($md) {
    align-items: center;
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
  }

  .form-cancel-button {
    margin-right: 0;
    margin-top: var(--s2);
    padding: var(--s4);

    @include query($md) {
      margin-top: 0;
      margin-right: var(--s6);
    }
  }
}

.radio-group .control-label {
  color: var(--text-primary);
}
