@import 'src/styles/includes';
%multipleAreas {
  gap: var(--s3);
  min-height: calc(var(--s25) * 4);

  @media (max-width: #{$md + 'px'}) {
    height: auto;
    min-height: unset;
  }
}

.section {
  display: flex;
  flex-direction: column;
  margin: 0 0 var(--s6) 0;
  flex: 1 1 auto;

  &:first-of-type {
    padding: var(--s6) 0 0 0;
  }

  &__top-row {
    display: flex;
    flex-direction: column;
    & > * {
      display: flex;
    }
    &__row-1 {
      justify-content: space-between;
      align-items: center;
    }
    &__row-2 {
      justify-content: space-between;
      margin: var(--s2) 0 0 0;
      color: var(--label-color);
      font-family: var(--font-family);
      font-size: var(--font-size-s);
      letter-spacing: 0;
      line-height: var(--s6);
    }
  }

  &__layout {
    display: block;
    padding: 0 var(--s6) 0 0;
    &__title {
      font-weight: var(--font-weight-bold);
    }

    &__hyphen {
      margin: 0 var(--s1);
    }
  }

  &__delete-button {
    display: flex;
    align-items: flex-end;
  }

  &__title {
    font-size: var(--font-size-l);
    font-weight: var(--font-weight-bold);
    font-family: var(--font-family);
  }

  &__middle-row {
    margin: var(--s6) 0;
    display: grid;

    @media (max-width: #{$md + 'px'}) {
      display: block;
    }

    &.one-column {
      @extend %multipleAreas;
      grid-template-rows: [singleRow] 1fr;
      grid-template-columns: [singleCol] 1fr;
    }

    &.one-column-80 {
      @extend %multipleAreas;
      grid-template-rows: [singleRow] 1fr;
      grid-template-columns: [singleCol] 80%;
    }

    &.one-column-60 {
      @extend %multipleAreas;
      grid-template-rows: [singleRow] 1fr;
      grid-template-columns: [singleCol] 60%;
    }

    &.one-column-40 {
      @extend %multipleAreas;
      grid-template-rows: [singleRow] 1fr;
      grid-template-columns: [singleCol] 40%;
    }

    &.two-column {
      @extend %multipleAreas;
      grid-template-rows: 1fr;
      grid-template-columns: 1fr 1fr;
    }

    &.two-column-left40-right60 {
      @extend %multipleAreas;
      grid-template-rows: 1fr;
      grid-template-columns: 4fr 6fr;
    }

    &.two-column-left67-right33 {
      @extend %multipleAreas;
      grid-template-rows: 1fr;
      grid-template-columns: 2fr 1fr;
    }

    &.two-column-left80-right20 {
      @extend %multipleAreas;
      grid-template-rows: 1fr;
      grid-template-columns: 4fr 1fr;
    }

    &.two-row-top60-bottom40 {
      @extend %multipleAreas;
      grid-template-rows: 6fr 4fr;
      grid-template-columns: 1fr;
    }

    &.two-row-top80-bottom20 {
      @extend %multipleAreas;
      grid-template-rows: 4fr 1fr;
      grid-template-columns: 1fr;
    }

    &.two-row-top20-bottom80 {
      @extend %multipleAreas;
      grid-template-rows: 1fr 4fr;
      grid-template-columns: 1fr;
    }

    &.three-column {
      @extend %multipleAreas;
      grid-template-rows: 1fr;
      grid-template-columns: 1fr 1fr 1fr;
    }

    &.two-column-three-area-2left50-right50 {
      @extend %multipleAreas;
      grid-template-rows: 1fr 1fr;
      grid-template-columns: 1fr 1fr;

      & > div:nth-child(2) {
        grid-row: span 2;
      }
    }

    &.two-column-three-area-2left67-right33 {
      @extend %multipleAreas;
      grid-template-rows: 1fr 1fr;
      grid-template-columns: 2fr 1fr;

      & > div:nth-child(2) {
        grid-row: span 2;
      }
    }

    &.two-row-three-area-top100-2bottom50 {
      @extend %multipleAreas;
      grid-template-rows: 1fr 1fr;
      grid-template-columns: 1fr 1fr;

      & > div:first-child {
        grid-column: span 2;
      }
    }

    &.two-row-three-area-2top50-bottom100 {
      @extend %multipleAreas;
      grid-template-rows: 1fr 1fr;
      grid-template-columns: 1fr 1fr;

      & > div:last-child {
        grid-column: span 2;
      }
    }

    &.two-column-four-area-3left50-right50 {
      @extend %multipleAreas;
      grid-template-rows: 1fr 1fr 1fr;
      grid-template-columns: 1fr 1fr;

      & > div:nth-child(2) {
        grid-row: span 3;
      }
    }

    &.two-row-two-column-50-50 {
      @extend %multipleAreas;
      grid-template-rows: 1fr 1fr;
      grid-template-columns: 1fr 1fr;
    }

    &.two-row-two-column-67-33 {
      @extend %multipleAreas;
      grid-template-rows: 1fr 1fr;
      grid-template-columns: 2fr 1fr;
    }

    &.two-row-three-column {
      @extend %multipleAreas;
      grid-template-rows: 1fr 1fr;
      grid-template-columns: 1fr 1fr 1fr;
    }

    &.one-column-text-overflow-area {
      @extend %multipleAreas;
      grid-template-rows: [singleRow] 1fr;
      grid-template-columns: [col1] 1fr;
    }
  }

  &__bottom-row {
    display: flex;
    justify-content: center;

    & > button {
      margin: 0 var(--s6);
    }

    &__button-text {
      margin: 0 0 0 var(--s3);
    }
  }

  .section-line {
    display: flex;
    flex: 1 1 auto;
    border-color: var(--color-orange-theme);
  }
}
