@import 'src/styles/includes';

.save-and-preview {
  padding-top: var(--s6);
  margin: 0 0 var(--s6) 0;
  min-height: calc(calc(var(--s25) * 6) + var(--s22));

  @include query($lg, max) {
    min-height: unset;
  }

  &__loading-container {
    justify-content: center;
  }

  .preview-image {
    display: block;
    margin-bottom: var(--content-spacing);
    @include query($lg, max) {
      max-width: 100%;
    }
  }
}
