@import 'src/styles/includes';

$contentWidths: $xs 100, $sm 160, $md 190, $lg 262;
.topic-panel:not(&.topic-panel--big-left-column, &.topic-panel--right-column)
  .content-item-container {
  @each $size, $calc in $contentWidths {
    @include query($size) {
      width: calc(100vw - #{$calc}px);
    }
  }
}

.rich-text-content-item-container {
  max-width: 700px;
  @include query($sm, max) {
    min-width: 70vw;
    max-width: 80vw;
  }
}

.subpanel .content-item-container {
  max-width: calc(100vw - 308px);
  @include query($xxl) {
    max-width: calc(100vw - 454px);
  }
  @include query($sm, max) {
    min-width: 80vw;
    max-width: 80vw;
  }
}
