@import 'src/styles/includes';

.draggable-library-item-card {
  min-height: var(--s16);
  max-width: calc(var(--s25) * 3);
  margin: 0 0 calc(var(--s4) + var(--s2px)) 0;

  .rich-text-library-item-edit {
    @include query($sm, 'max') {
      width: auto;
      max-width: unset;
    }
  }

  .library-item-card {
    max-width: unset;
    min-width: unset;
    margin: 0;
    box-shadow: 0 -1px 0 0 var(--table-shadow-color);
    background-color: var(--dragdrop-card-background);
    border: 2px solid transparent;
    border-radius: var(--s2px);

    &:active {
      border: 2px solid var(--color-orange-theme);
      box-shadow: none;
    }
    &:hover:not(:active) {
      border: 2px solid var(--dragdrop-layout-icon-fill);
      box-shadow: none;
    }
    .library-item-card-icon {
      padding: 0;
      border: 0;
      margin: 0 var(--s4) 0 0;
      min-height: unset;
      display: flex;
      align-items: center;
      & svg {
        width: var(--s6);
        height: var(--s6);
      }
    }
  }
}
