@import 'src/styles/includes';

.project-container {
  h2 {
    color: var(--project-color-main);
    font-size: var(--font-size-xl);
  }

  .project-panel {
    color: var(--project-color-main);
    background-color: var(--background-primary);
    border-radius: var(--border-size-standard);
    box-shadow: var(--table-shadow);
    margin-bottom: var(--s6);
    overflow: hidden;
    padding: var(--content-spacing);
    position: relative;

    .theme-light & {
      &__readonly {
        background-color: var(--lightmode-0);
      }
    }
    &__readonly {
      background-color: var(--color-grey-dark);
    }

    &:last-child {
      margin-bottom: 0;
    }

    .accordion {
      padding-top: var(--content-spacing);
    }
  }

  .project-title-description-container {
    margin-bottom: var(--s2);
    display: flex;
    align-content: center;
    justify-content: space-between;
    width: 100%;
  }

  .project-panel-link-arrow {
    height: var(--s8);
    right: var(--s4);
    top: var(--s2);

    @include query($lg) {
      top: var(--s4);
    }
  }

  .project-panel-row {
    @include query($sm, 'max') {
      display: block;
    }

    @include query($lg) {
      display: flex;
    }
  }

  .project-panel-row.center-row {
    align-items: center;
  }

  .project-title {
    align-items: center;
    display: flex;
    font-size: var(--font-size-xxl);
    font-weight: var(--font-weight-bold);
    justify-content: center;
    line-height: var(--lineheight-xxl);
    margin-right: var(--s5);

    &.failed,
    &.on-hold {
      align-items: flex-start;
      flex-direction: column;

      .project-status {
        display: flex;
        line-height: var(--lineheight-regular);

        .project-status__icon {
          margin-left: 0;
        }
      }
    }

    a {
      @extend %clamp-3;
      max-width: calc(70vw - var(--s21));

      @include query($xs, max) {
        max-width: 65vw;
      }

      @include query($xxl) {
        max-width: calc(80vw - var(--navbar-width-xxl));
      }
    }

    .title-overflow {
      @extend %clamp-3;

      @include query($xs, max) {
        max-width: 85vw;
      }

      @include query($sm, max) {
        max-width: 80vw;
      }

      @include query($md) {
        max-width: calc(73vw - var(--s21));
      }

      @include query($xxl) {
        max-width: calc(80vw - var(--navbar-width-xxl));
      }
    }

    .pills-container {
      margin-top: calc(var(--font-size-regular) - var(--s1));
      grid-column-gap: var(--s1);
      display: flex;
      align-items: center;
    }

    @include query($sm, max) {
      flex-direction: column;
      align-items: flex-start;
    }

    .status-pill {
      align-items: center;
      display: inline-flex;
      margin-bottom: var(--s3);
      margin-top: var(--s3);

      @include query($md) {
        margin-bottom: 0;
        margin-left: var(--s4);
        margin-top: 0;
      }

      &:first-of-type {
        margin-left: 0;
        @include query($md) {
          margin-left: var(--s4);
        }
      }
    }

    a {
      color: var(--project-color-main);
      font-weight: var(--font-weight-bold);

      &:hover {
        color: var(--project-color-main);
      }

      @include query($sm, 'max') {
        font-size: var(--font-size-xl);
        line-height: var(--lineheight-xl);
      }
    }
  }
}

.notification-icon {
  margin-right: var(--s3);
  margin-top: calc(-1 * var(--s2));

  @include query($md, 'max') {
    float: left;
    margin-top: var(--s1px);
  }
}

.activity-details {
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-left: var(--s8);

  .activity-details-title {
    display: inline-block;
    font-size: var(--font-size-xs);
    color: var(--project-color-secondary);
  }

  .activity-details-number {
    display: inline-block;
  }

  .notification-icon::before {
    content: '';
    display: inline-block;
    width: rem(6);
    height: rem(6);
    border-radius: rem(6);
    background-color: var(--color-green);
    margin-bottom: var(--s3px);
    margin-right: rem(6);
  }
}

.user-details-container {
  display: flex;

  @include query($xs, max) {
    flex-direction: column;
  }
}

.user-details-container:first-child {
  padding-right: var(--s8);
}

.user-details-container:last-child {
  padding-left: var(--s8);
}

.project-description-container {
  display: flex;
  padding-bottom: var(--content-spacing);

  @include query($md, max) {
    flex-direction: column;
  }

  > div {
    flex: 1 1 50%;
  }
}

.project-description {
  @extend %clamp-3;

  color: var(--project-color-secondary);
  white-space: break-spaces;
  padding-right: var(--s8);
}

.project-content {
  padding-left: 15px;
}

.deal-details-container {
  align-items: flex-start;
  display: flex;

  @include query($xs, max) {
    flex-direction: column;
  }

  @include query($lg) {
    flex-wrap: nowrap;
    justify-content: space-between;
    width: 100%;
  }

  .detail-section {
    width: 32%;

    @include query($xs, 'max') {
      width: 50%;
    }
    .deal-details {
      .deal-details-content {
        color: var(--project-color-main);
        line-height: var(--lineheight-regular);
        @include query($md, 'max') {
          font-size: var(--font-size-s);
        }
        @include query($sm, 'max') {
          font-size: var(--font-size-regular);
        }
      }
    }
  }
}

.project-pill {
  display: inline-block;
  background-color: var(--color-grey-light);
  color: var(--background-default);
  padding: var(--s1) var(--s3);
  border-radius: var(--s4);
  margin: var(--s1) var(--s3) 0 var(--s3);
  font-size: var(--font-size-s);
  font-weight: var(--font-weight-medium);
}

.deal-details {
  display: flex;
  justify-content: center;
  flex-direction: column;

  .deal-details-title {
    display: inline-block;
    font-size: var(--font-size-xs);
    color: var(--project-color-secondary);
  }

  .deal-details-number {
    display: inline-block;
  }
}

.project-panel {
  .accordion-toggle {
    color: var(--project-color-main);
    font-size: var(--font-size-l);
  }
}
