@import 'src/styles/includes';

.structured-analysis-add-from-template {
  background-color: var(--background-primary);
  box-shadow: var(--card-shadow);
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(rem(250), 1fr));
  margin: var(--s12) 0;
}

.pane .scrollable {
  @include scrollbars(
    var(--s1),
    var(--scrollbar-handle-color),
    var(--scrollbar-track-color),
    var(--scrollbar-handle-color-hover)
  );

  .analysis-table {
    margin-bottom: var(--s3);
  }
}
