@import 'src/styles/includes';

button.menu-item {
  border-radius: 0;
  width: 100%;

  &:hover {
    background: var(--menuitem-background-active);
  }
}

.add-icon {
  & > .menu-item-icon {
    pointer-events: none;

    .icon path.fill {
      fill: var(--icon-add-color);
    }
  }

  &:hover {
    & > .menu-item-icon {
      .icon path.fill {
        fill: var(--color-orange-light-alt);
      }
    }
  }
}

.add-link {
  background-color: transparent;
  border: none;
  padding: 0;
  position: relative;

  .label {
    color: var(--text-alternate);
  }
}

.menu-wrapper {
  position: relative;
}

.add-popover {
  display: none;
  position: absolute;
  border-radius: var(--s2px);
  background-color: var(--menuitem-background);
  background-clip: padding-box;
  border: var(--popup-border);
  box-shadow: var(--popup-shadow);
  left: var(--s21);
  top: 0;
  z-index: 5;
  padding: var(--popup-padding);

  &.show {
    display: block;
  }

  .dropdown-item {
    display: flex;
    align-items: center;
    color: var(--color-orange-theme);
    padding: 0;
    margin-bottom: var(--s5);

    .icon {
      margin-right: var(--s2);
    }

    &:last-child {
      margin-bottom: 0;
    }

    &:focus,
    &:hover {
      background-color: transparent;
      color: var(--color-orange-theme);
      text-decoration: underline;

      .icon {
        .fill {
          fill: var(--color-orange-theme);
        }

        .outline {
          visibility: hidden;
        }
      }
    }
  }

  // This is for the "tail" on the left side of the popover.
  &:before,
  &:after {
    border: 9px solid transparent;
    border-top-color: transparent;
    content: '';
    position: absolute;
    left: 0;
    top: 22px;
  }

  &:before {
    border-right-color: var(--menuitem-background);
    transform: translate(rem(17, true), 0);
    z-index: 1;
  }
  &:after {
    border-right-color: var(--popup-border-color);
    margin-left: rem(2, true);
    transform: translate(rem(16, true), 0);
  }
}

.add-popover-overlay {
  background: transparent;
  bottom: 0;
  display: block;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 2;
}

@include query($xxl) {
  .add-popover {
    left: rem(228);
  }
}
