@import 'src/styles/includes';

.key-focus-area-topic-panel {
  h3 {
    margin-bottom: var(--s3);
  }

  .section-header {
    color: var(--kpi-text-labels);
    font-size: var(--font-size-s);
    line-height: var(--lineheight-regular);
    margin-bottom: var(--s2);
  }

  .rich-text-editor-intro {
    font-size: var(--font-size-regular);
    line-height: var(--lineheight-regular);
    margin: 0;
  }

  &__title {
    &:hover {
      text-decoration: underline;
    }
  }

  &-body {
    @extend %clamp-10;
    margin-bottom: var(--s3);

    &:last-child {
      margin-bottom: 0;
    }
  }

  &-conclusion {
    @extend %clamp-5;
    margin-bottom: 0;
  }
}
