.review-responses-container {
  display: flex;
  flex-direction: column;
  padding: var(--s10);

  .additional-comments {
    padding: var(--s8) 0;

    p {
      color: var(--text-secondary);

      &.additional-comments-label {
        color: var(--text-primary);
        font-weight: 500;
      }
    }
  }
}

.responses-footer {
  display: flex;

  .reset-answers-button {
    margin-right: 0;
    margin-left: auto;
  }
}
