@import 'src/styles/includes';

.page-title-container {
  h2 {
    display: block;
    margin-bottom: var(--s4);
    font-weight: var(--font-weight-bold);
    font-size: var(--font-size-xl);
  }

  &.contain {
    padding: var(--s4) var(--s4) 0;

    @include query($lg) {
      padding: var(--s12) var(--s12) 0;
    }
  }
}
