@import 'src/styles/includes';

.report-select {
  margin-bottom: var(--s6);
}

.page-select {
  margin-bottom: var(--s6);
}

.hidden-power-bi-container,
.hidden-power-bi-hint {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

.visible-power-bi-container {
  margin-top: var(--s8);
  padding: var(--s2);
  height: rem(280);
}

.visible-power-bi-hint {
  margin-top: var(--s8);
}

.visible-power-bi-hint + .visible-power-bi-container {
  margin-top: 0;
}
