@import 'src/styles/includes';

.narrative-card {
  .link-content-buttons {
    border-top: var(--s1px) solid var(--color-grey-alt);
    padding-top: var(--s4);
    margin-top: var(--s4);
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

    .button-block {
      display: inline-block;
    }

    .shared-pill {
      padding: 5px 10px;
      float: right;
      height: auto;
      margin-left: auto;

      svg {
        padding: 0;
      }
    }
  }
}

.narrative-section .narrative-card {
  &.narrative-card-connected {
    border: var(--s1px) solid var(--color-orange);
    overflow: hidden;
    overflow-wrap: break-word;
  }
}
