.upload-success-banner {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  height: var(--s16);
  background-color: var(--color-green);
  margin: var(--s7) 0;

  --upload-success-banner-text: #ffffff;

  .icon-checkmark {
    padding-right: var(--s3);
  }

  p {
    margin: 0;
    color: var(--upload-success-banner-text);
    font-weight: var(--font-weight-medium);
  }

  button {
    position: absolute;
    right: var(--s12);
    background-color: transparent;
  }
}
