@import 'src/styles/includes';

.deal-details {
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-right: var(--s8);

  @include query($md, 'max') {
    margin: var(--s2) 0;
  }

  .deal-details-title {
    color: var(--color-grey);
    display: inline-block;
    font-size: var(--font-size-xs);
    line-height: 1;
  }

  .deal-details-number {
    display: inline-block;
  }

  .SharePoint {
    max-width: 150px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
