@import 'src/styles/includes';

.analysis-library-item-container {
  border-radius: 2px;
  padding: var(--s7) var(--s6) 0 var(--s6);
  max-width: 100%;

  &:last-child {
    padding-bottom: var(--s6);
    height: 100%;
  }
}

.analysis-library-item-content {
  @include scrollbars();
  cursor: default;
  max-width: 100%;
  overflow: auto;
}

.analysis-main-content {
  max-width: 1920px;
  margin: auto;

  @include query($md, max) {
    max-width: 80vw;
  }

  @include query($sm, max) {
    max-width: 90vw;
  }

  h2 {
    line-height: var(--line-height-regular);
    margin-bottom: 0px;
  }

  .page-title-container {
    margin-top: var(--s4);
    margin-bottom: var(--s1);
  }

  p.page-last-updated {
    padding: 0 var(--s4) 0 0;
    margin-bottom: var(--s3);
    @media screen and (min-width: 992px) {
    }
  }
  .analysis-pill-container {
    margin-bottom: var(--s8);
    @media screen and (min-width: 992px) {
    }
  }

  // Add border between sections if more than 1 section provided
  > .section-grid ~ .section-grid {
    border-top: var(--s1px) solid var(--color-grey-medium);
    padding-top: var(--s6);
  }

  .content-label,
  .section-name {
    display: none;
  }
  .section-text {
    margin-top: var(--s4);
    margin-bottom: var(--s8);
  }

  @include query($lg) {
    .section-text {
      max-width: $content-width;
    }
  }
}

.tri-pane-header {
  h2 {
    margin-bottom: 0px;
  }

  .page-title-container {
    padding-bottom: var(--s1);
  }

  .page-last-updated {
    margin-bottom: var(--s3);
  }
}
