@import 'src/styles/includes';

footer {
  @extend %max-content-width;
  padding: 0 var(--container-spacing);

  .footer-link {
    color: var(--project-color-main);
    display: inline-block;
    margin: var(--content-spacing) var(--s6) var(--s2) 0;
    white-space: nowrap;

    &:hover {
      color: var(--color-orange);
      text-decoration: underline;
    }
  }

  .copyright {
    color: var(--breadcrumb-text);
    font-size: 14px;

    a {
      color: var(--project-color-main);
      padding-right: 0;
      text-decoration: underline;

      &:hover,
      &:focus {
        color: var(--project-color-main);
      }
    }
  }
}
