@import 'src/styles/includes';

.topic-top-bar-container {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr auto auto auto;
  grid-template-rows: auto auto auto;
  grid-template-areas:
    'title move edit more'
    'last  last last last'
    'pill  pill pill pill';
  grid-gap: var(--s2);

  margin-bottom: var(--s4);

  @include query($sm, 'max') {
    grid-template-areas:
      '.     move  edit  more'
      'title title title title'
      'last  last  last  last'
      'pill  pill  pill  pill';
  }

  .topic-title {
    grid-area: title;
    font-size: var(--font-size-xl);
  }

  .more-menu {
    grid-area: more;
  }

  .topic-edit-button {
    grid-area: edit;
    padding-left: var(--s1);
  }

  .page-last-updated {
    @media screen and (max-width: 767px) {
      display: grid;
    }
    grid-area: last;
    line-height: 1.5;
    margin: 0;
  }

  .pills-section {
    grid-area: pill;
  }

  .move-buttons-section {
    grid-area: move;
  }

  .topic-move-button {
    background-color: transparent;
  }
}
