.popup-menu.popup-confirmation.share-file-popover {
  border: 1px solid var(--color-orange-light);
}
.fade.share-file-popover.show.popover.bs-popover-left {
  .arrow {
    height: var(--s6);
    margin: 0 calc(var(--s3) - var(--s2px)) 0 0;
    width: var(--s3);

    &::before {
      border-width: var(--s3);
      border-right-width: 0;
      border-left-color: var(--color-orange-light);
    }

    &::after {
      border-width: var(--s3);
      border-right-width: 0;
      border-left-color: var(--popup-background-color);
    }
  }
}

.share-file-popover.fade.popover.bs-popover-bottom > .arrow::before {
  border-bottom-color: var(--color-orange-light);
}