@import 'src/styles/includes';

.survey-welcome-modal.modal-dialog {
  margin: auto;
  max-width: 80%;

  @include query($md) {
    max-width: 60%;
  }
  @include query($xl) {
    max-width: 40%;
  }

  .modal-content {
    padding: var(--content-spacing);

    .modal-title.h4 {
      align-self: flex-start;
    }

    .modal-body {
      color: var(--project-color-secondary);
    }

    .modal-footer {
      justify-content: flex-start;
      padding-top: var(--content-spacing);

      button {
        margin-left: 0;
      }
    }
  }
}
