@import 'src/styles/includes';

.tags-list-container {
  line-height: var(--lineheight-s);

  display: flex;
  flex-direction: row;
  align-items: flex-start;

  margin-top: var(--s3);

  .tag-icon {
    margin: 0 var(--s2) 0 0;
    color: var(--color-tags);
  }

  .tags {
    flex: 1;
    .punctuation {
      color: var(--color-tags);
    }
    .tag-text {
      color: var(--color-tags);
      font-size: var(--font-size-s);
    }
  }
}
