@import 'src/styles/includes';

.library-item-select {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;

  select,
  option {
    max-width: rem(450);
  }

  @include query(850, 'max') {
    align-items: flex-start;
    flex-direction: column;

    select,
    option {
      max-width: 80vw;
    }
  }

  label {
    margin-bottom: 0;
    margin-right: 0.5rem;
  }
}
