.d3-rich-text {
  &.text-input-fieldset {
    z-index: 0;
  }
  .ck-editor__editable {
    min-height: 8.75em;
  }
}

.rich-text-library-item-edit {
  /*
   * See discussion:
   * https://github.com/ckeditor/ckeditor5/issues/636
   *
   * For now CKEditor authors are not going to implement any dedicated props/plugins
   * to set height or min-height in editor
   *
   * As a workaround overriding ck-editor__main class.
   */
  .ck-editor__main {
    overflow-y: scroll;
    max-height: 260px;
  }
}

.rich-text-editor-paragraph,
.modal .rich-text-editor-paragraph,
.ck {
  a,
  a:link,
  a:visited,
  a:local-link,
  a:not([class]),
  a:not([class]):visited,
  a:not([class]):local-link {
    background: transparent;
    color: var(--project-color-main);
    text-decoration: underline;
  }
  a:hover,
  a:not([class]):hover {
    color: var(--project-color-main);
    text-decoration: underline;
  }
}

div.ck.ck-toolbar {
  display: none;
}

.d3-rich-text.text-input-fieldset
  .ck-rounded-corners
  .ck.ck-editor__editable_inline {
  .rich-text-editor-intro {
    &:first-child {
      margin-top: var(--s6);
    }
  }
}
