@import 'src/styles/includes';

.settings {
  color: var(--project-color-main);

  .page-title-container {
    h2 {
      margin-bottom: 0px;
    }
  }

  .form-layout .form-content {
    padding-bottom: 0;
  }

  .select-multiple .pills {
    margin-top: -24px;
  }

  .settings-container {
    margin: auto;

    h1 {
      font-size: var(--font-size-xxl);
      font-weight: var(--font-weight-bold);
      line-height: var(--lineheight-xxl);
      margin: var(--s12) 0 var(--s12);
    }

    .settings-description {
      margin-bottom: var(--s6);
      max-width: $content-width;
    }

    .form-bottom-actions {
      padding-top: 0;
      margin: var(--s8) 0 var(--s12);
    }

    .ui-tabs {
      margin-top: var(--s6);
    }

    .ui-tab-content {
      max-width: 914px;
      margin-top: var(--s8);
    }
    .tab-content {
      @include query($sm, max) {
        padding: var(--s4);
      }
    }

    .notification-settings-form-container {
      .form-fields {
        .fieldset label,
        .radio-group-label {
          padding-top: 0;
        }

        .radio-group {
          margin-bottom: var(--s8);
        }
      }
    }

    .project-settings-form-container {
      .form-fields {
        max-width: rem(350);
        width: calc(100% - var(--s4));
        padding-top: var(--s8);

        .text-input-control-label {
          margin-bottom: 0;
        }

        .input-field-container {
          margin-top: -32px;
        }

        .fieldset label,
        .radio-group-label {
          padding-top: 0;
        }

        .textarea-fieldset {
          padding-bottom: 0;

          .text-area-control-label {
            font-weight: var(--font-weight-medium);
            margin-bottom: 0;
          }
        }

        .select-container {
          position: relative;
          margin-bottom: var(--s8);

          select {
            max-width: 100%;
          }

          &::after {
            position: absolute;
            top: var(--s2);
          }
        }

        select {
          border: var(--border-size-standard) solid
            var(--color-grey-light-alt-2);
          padding: 0;
          padding-left: var(--s2);
          top: 0;
          font-size: var(--font-size-regular);
          font-weight: var(--font-weight-regular);
        }

        .radio-label {
          padding: 0;
        }

        .radio-group {
          margin-bottom: var(--s8);
        }

        label {
          &.ui-checkbox {
            margin-bottom: var(--s8);
          }
        }
      }
    }

    .project-current-status-container {
      padding: var(--s8) 0;

      .status-header {
        font-weight: var(--font-weight-regular);
        font-size: var(--font-size-m);
      }

      .status-container, .reset-link-container {
        display: flex;
        grid-template-columns: 1fr 1fr;
      }

      .status-container {
        flex-wrap: wrap;
        grid-column-gap: var(--s4);
        margin: var(--s4) 0;
      }

      .description {
        font-weight: var(--font-weight-regular);
        color: var(--project-color-secondary);
      }

      .reset-link-container {
        align-items: center;
        height: 30px;

        .reset-link {
          color: var(--tab-text-color);
          font-size: var(--font-size-m);
          background-color: transparent;
        }

        .project-loader {
          margin-top: 0;
        }
      }
    }

    .list-table {
      padding: var(--s4) 0;

      .list-table-row {
        background-color: transparent;
        height: auto;
        padding: var(--s4) 0;
        border-color: var(--background-primary);
        border-style: solid;
        border-width: 0;
        align-items: flex-start;

        @media screen and (max-width: 767px) {
          grid-template-columns: 2;
        }

        .list-table-row:hover {
          border-color: var(--background-primary);
          border-style: solid;
          border-width: 0;
        }

        .cell {
          @media screen and (max-width: 767px) {
            text-align: left;
          }

          .cell-row {
            display: flex;
            margin: var(--s1) var(--s3) 0 0;
            flex-direction: column;
            margin-top: var(--s3);

            .user-pill {
              margin-bottom: var(--s1);
              align-self: flex-start;
            }

            .user-pill-form {
              display: flex;

              .fieldset {
                padding-bottom: 0;
                max-width: 300px;
                margin-right: var(--s5);
                flex-grow: 1;

                input {
                  min-height: inherit;
                }

                & + button {
                  flex-grow: 0;
                  padding-right: var(--s5);
                  padding-left: var(--s5);
                  width: auto;
                }
              }
            }
          }

          .text-input {
            width: 100%;
          }

          .role-subtext {
            font-size: 14px;
            color: var(--color-grey);
          }
        }

        .cell:first-of-type {
          width: 40%;
          padding-top: var(--s1);
          @media screen and (max-width: 767px) {
            width: 100%;
          }
        }
        .cell:last-of-type {
          width: 60%;
          display: flex;
          flex-direction: column;

          @media screen and (max-width: 767px) {
            margin-left: 0;
            width: 200%;
            padding-top: var(--s1);
            padding-left: var(--s4);
            grid-column: 2;
          }
        }
      }

      .header-row {
        padding: var(--s4) 0;

        .header-cell:hover {
          text-decoration: none;
        }
      }
      .header-row .header-cell:first-of-type {
        width: 40%;
      }
      .header-row .header-cell:last-of-type {
        width: 60%;
      }

      .user-settings-controls {
        display: flex;
        justify-content: flex-end;

        .button {
          width: auto;
        }

        @include query($sm, 'max') {
          justify-content: flex-start;
          flex-direction: column-reverse;

          .button {
            margin-top: var(--s5);
          }
        }
      }

      .alert {
        float: right;
        margin-top: var(--s3);
      }
    }
  }

  .input-alert {
    margin-bottom: var(--s2);

    .alert-text {
      font-size: var(--font-size-s);
      color: var(--color-orange);
      margin-left: var(--s2);
    }
  }

  .character-count-container {
    padding-top: 0px;
  }
}

footer {
  padding-top: 0px;
}
