@import 'src/styles/includes';

.welcomebox-container {
  --text-primary: var(--color-white);
  --icon-primary-color: var(--color-white);

  border-radius: 2px;
  background-color: var(--color-green);
  color: var(--text-primary);
  padding: var(--content-spacing);
  margin: var(--s6) 0;

  &__disabled {
    background-color: var(--color-grey-dark);

    .grey-button {
      background-color: var(--button-primary-color-disabled) !important;
      color: var(--button-primary-color-disabled-text) !important;
    }

    .theme-light & {
      background-color: var(--lightmode-0);
      color: var(--project-color-main);

      h2 span {
        color: var(--project-color-main);
      }
    }
  }

  h2 {
    margin-bottom: var(--s4);

    @include query($md, 'max') {
      margin-bottom: var(--s2);
    }
  }

  h2 span {
    color: var(--text-primary);
    font-size: var(--font-size-xl);
    font-weight: var(--font-weight-bold);
    line-height: 42px;

    @include query($md, 'max') {
      font-size: var(--font-size-xl);
      font-weight: var(--font-weight-bold);
      line-height: var(--lineheight-regular);
    }
  }

  p {
    font-size: var(--font-size-l);
    line-height: 32px;
    padding-bottom: 0px;
    margin-bottom: 0px;

    @include query($md, 'max') {
      line-height: var(--lineheight-l);
    }
  }

  a {
    color: var(--color-white);
    text-decoration: underline;

    &:hover {
      color: var(--color-white);
    }

    &.link-button {
      display: inline-block;
      margin-top: var(--content-spacing);
    }
  }

  .welcome-paragraph-icon {
    margin: 0 var(--s1) var(--s1) var(--s1);
  }

  .nowrap {
    white-space: nowrap;
  }

  .close-icon {
    float: right;
  }
}
