.initial-description-wrapper {
  li:first-child {
    margin-top: 0;
  }

  .button-no-underline {
    color: var(--text-secondary);
    display: flex;
    align-items: center;

    .icon {
      width: var(--s3);
    }

    .initial-description-button-text {
      padding-left: var(--s2);
      font-weight: 500;
    }
  }

  .category-description {
    color: var(--text-secondary);
  }
}
