@import 'src/styles/includes';

.section-area__droppable-container {

  border: var(--s1px) dashed var(--color-grey);
  border-radius: var(--s2px);
  background-color: var(--dragdrop-container-background-hover);

  --hovered-section-border-color: var(--color-white);

  .theme-light & {
    --hovered-section-border-color: var(--color-grey-darker-alt);
  }

  &:hover {
    border: var(--s1px) dashed var(--hovered-section-border-color);
    box-shadow: none;
  }

  position: relative;
  // we need to be able to drop the thumbnail so make it at least that big (218px)
  min-height: 218px;

  @media (max-width: #{$md + 'px'}) {
    min-height: calc(var(--s25) + var(--s13) - var(--s2px));
    margin: var(--s3) 0 calc(var(--s4) + var(--s2px)) 0;
  }

  &[class*='single-column-layout-text-overflow-area'] {
    grid-column: 1 / 2;
    display: grid;
    grid-template-columns: [leftCol] 1fr [rightCol] 1fr;
    grid-template-rows: 1fr;
    column-gap: var(--s6);
    & > .background-left {
      grid-column: 1 / 2;
      border-right: var(--s1px) dashed var(--color-grey-medium);
    }
    & > .background-right {
      grid-column: 2 / 3;
      border-left: var(--s1px) dashed var(--color-grey-medium);
    }
  }

  &[class*='two-column-2a-6733-layout-left-area-1'] {
    grid-column: 1 / 2;
  }

  &[class*='two-column-2a-6733-layout-right-area-1'] {
    grid-column: 2 / 3;
  }

  &[class*='two-row-three-area-layout-top-area-1'] {
    grid-row: 1 / 2;
    grid-column: 1 / 3;
  }

  &[class*='two-row-three-area-layout-bottom-area-1'] {
    grid-row: 2 / 3;
    grid-column: 1 / 2;
  }

  &[class*='two-row-three-area-layout-bottom-area-2'] {
    grid-row: 2 / 3;
    grid-column: 2 / 3;
  }

  &[class*='two-row-two-column-layout-area-67-1'] {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
  }

  &[class*='two-row-two-column-layout-area-67-2'] {
    grid-row: 2 / 3;
    grid-column: 1 / 2;
  }

  &[class*='two-row-two-column-layout-area-33-1'] {
    grid-row: 1 / 2;
    grid-column: 2 / 3;
  }

  &[class*='two-row-two-column-layout-area-33-2'] {
    grid-row: 2 / 3;
    grid-column: 2 / 3;
  }

  &[class*='two_row_t_80_1a_b_20_1a-layout-area-2'],
  &[class*='two_row_t_20_1a_b_80_1a-layout-area-1'] {
    & .section-area__background__hint {
      display: none;
    }
    @include query($sm, max) {
      & .section-area__background__hint {
        display: block;
      }
    }
  }

  &[class*='two_column_l_80_1a_r_20_1a-layout-area-2'] {
    @include query($lg, max) {
      & .section-area__background__hint {
        display: none;
      }
    }

    @include query($sm, max) {
      & .section-area__background__hint {
        display: block;
      }
    }
  }
  &[class*='three_column_two_row-layout-area'] {
    @include query($lg, max) {
      & .section-area__background__hint {
        display: none;
      }
    }

    @include query($sm, max) {
      & .section-area__background__hint {
        display: block;
      }
    }
  }

  &[class*='two_column_l_50_3a_r_50_1a-layout-area-1'],
  &[class*='two_column_l_50_3a_r_50_1a-layout-area-3'],
  &[class*='two_column_l_50_3a_r_50_1a-layout-area-4'] {
    @include query($lg, max) {
      & .section-area__background__hint {
        display: none;
      }
    }

    @include query($sm, max) {
      & .section-area__background__hint {
        display: block;
      }
    }
  }
  .section-area__background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: var(--s2);
    text-align: center;
    display: flex;
    flex: 1 1 auto;

    &__content {
      display: flex;
      flex-direction: column;
      flex: 1 1 auto;
      justify-content: center;
      align-items: center;
    }

    &__hint {
      margin: var(--s6) 0 0 0;
      max-width: calc(var(--s25) + var(--s15));
      color: var(--color-grey);
      font-size: var(--font-size-regular);
      font-weight: var(--font-weight-medium);
      letter-spacing: 0;
      line-height: var(--s6);
      text-align: center;

      @media (max-width: #{$md + 'px'}) {
        margin: var(--s4) 0 0 0;
      }

      &.hint-error {
        color: var(--color-error);
      }
    }
  }

  .section-area__item-container {
    position: relative;
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    background: transparent;
    padding: 0;
    &.background-left {
      border-right: var(--s1px) dashed var(--color-grey-medium);
    }
    & > .draggable-library-item-card {
      &:not(:last-of-type) {
        margin: 0 0 calc(var(--s4) + var(--s2px)) 0;
      }
      &:last-of-type {
        margin: 0;
      }
    }
  }
}

.clearSectionWrapper {
  position: absolute;
  bottom: calc(-1 * var(--font-size-xxs));
  right:0;
  z-index: 100;

  box-sizing: border-box;
  height: calc(var(--s10) + var(--s1px));
  width: calc(var(--s10) + var(--s1px));
  border: var(--s1px) solid var(--color-grey-medium);
  border-radius: var(--s2px);
  background-color: var(--color-grey-dark);

  .theme-light & {
    background-color: var(--color-white);
  }

  box-shadow: 0 var(--s2px) var(--s3) 0 rgba(0,0,0,0.2);
  padding-left: var(--s2);
}