@import 'src/styles/includes';

.accordion-toggle {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: transparent;
  color: var(--color-orange-theme);
  font-weight: var(--font-weight-medium);
  padding-left: 0;

  &:hover {
    background-color: transparent;
  }
}

.accordion-only-mobile {
  .accordion-content {
    display: none;
  }

  &.accordion-open {
    .accordion-content {
      display: block;
    }
  }
  @include query($md) {
    .accordion-toggle {
      display: none;
    }

    .accordion-content {
      display: block;
    }
  }
}
