@import 'src/styles/includes';

.project-list {
  h2 {
    color: var(--project-color-main);
  }
}

.kpi-container {
  border-radius: 2px;
  background-color: var(--background-primary);
  box-shadow: var(--table-shadow);
  color: var(--text-primary);
  margin-bottom: var(--s6);
  padding: var(--content-spacing);

  @include query($md, 'max') {
    width: 100%;
  }

  h2 {
    color: var(--text-primary-alt);
    margin-bottom: var(--s4);
    font-size: var(--font-size-xl);
    font-weight: var(--font-weight-bold);
    line-height: var(--lineheight-xl);
    display: inline-block;

    @include query($md, 'max') {
      font-size: var(--font-size-l);
      font-weight: var(--font-weight-bold);
      line-height: var(--lineheight-regular);
    }
  }

  .kpi-subpanel {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-left: calc(-1 * var(--s5));
    margin-right: calc(-1 * var(--s5));

    .kpi-section {
      border-right: var(--s1px) solid var(--kpi-border-color);
      flex: 1 1 33%;
      margin-bottom: var(--s4);
      padding-right: var(--s5);
      padding-left: var(--s5);

      &:last-of-type {
        border-bottom: none;
        border-right: none;
      }

      @include query($md, 'max') {
        flex: 1 1 33%;
      }

      @include query($sm, 'max') {
        flex: 1 1 100%;
        border-right: none;

        &:not(:last-of-type)::after {
          background: var(--kpi-border-color);
          content: '';
          display: block;
          height: var(--s1px);
        }
      }

      h5 {
        color: var(--text-primary-alt);
        margin-bottom: var(--s6);

        @include query($md, max) {
          font-size: var(--font-size-m);
          font-weight: var(--font-weight-bold);
          line-height: var(--lineheight-regular);
        }
      }

      &-details {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
      }

      .kpi-detail {
        margin-bottom: var(--s6);
        min-width: 50%;

        .kpi-name {
          color: var(--kpi-text-labels);
        }

        .kpi-title {
          white-space: nowrap;
        }

        p {
          margin-bottom: 0;
        }

        .kpi-value {
          color: var(--text-primary-alt);
          font-size: var(--font-size-xl);
          line-height: var(--lineheight-xl);
          vertical-align: baseline;
          display: flex;

          @include query($md, 'max') {
            padding-bottom: var(--s4);
            font-size: var(--font-size-l);
            line-height: var(--lineheight-regular);
          }

          .kpi-value-icon {
            vertical-align: text-bottom;
            padding-left: var(--s4);
            margin-top: calc(-1 * var(--s1));

            .fill {
              fill: var(--text-primary-alt);
            }

            @include query($md, 'max') {
              padding-left: var(--s1);
            }
          }
        }
      }
    }
  }
}
