@import 'src/styles/includes';
.webkit-styles {
  &::-webkit-scrollbar {
    width: calc(var(--s1) + var(--s2px));
    height: calc(var(--s1) + var(--s2px));
  }

  &::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 calc(var(--s1) + var(--s2px))
      rgba(0, 0, 0, 0.3);
    box-shadow: inset 0 0 calc(var(--s1) + var(--s2px)) rgba(0, 0, 0, 0.3);
  }

  &::-webkit-scrollbar-thumb {
    background-color: var(--color-grey-medium);
    border-radius: var(--s3px);
  }
}

.bottom-panel {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;

  .report-panel-tabs {
    max-height: calc((var(--s25) * 2) + var(--s12) + var(--s2px));
  }

  .report-panel-tabs__list {
    background-color: var(--color-page-background);
    padding: 0 var(--s4);
    border-top-width: var(--s1px);
    border-top-style: solid;
    border-top-color: var(--border-primary);
    border-bottom-width: var(--s1px);
    border-bottom-style: solid;
    border-bottom-color: var(--border-primary);
  }

  .report-tabs-content {
    .library-items-list-data {
      padding: var(--s4) calc(var(--s4) + var(--s2px)) var(--s13)
        calc(var(--s4) + var(--s2px));

      .library-items-list-data__droppable-container {
        display: flex;
        min-height: var(--s16);
        overflow: hidden;
        margin: 0 var(--sn6) 0 0;

        &__list {
          flex-direction: row;
          overflow-x: scroll;
          overflow-y: hidden;
          padding: var(--s2px) 0 0 0;
          margin: 0 0 calc(var(--sn1) + var(--sn2px)) 0;

          @extend .webkit-styles;
        }

        .draggable-library-item-card {
          margin: 0 var(--s3) 0 0;
          min-width: calc((var(--s25) * 2) + var(--s15));
          &:last-of-type {
            margin: 0;
          }
        }
      }
    }

    .rs-panel-layout-tab {
      padding: var(--s4) calc(var(--s4) + var(--s2px));

      .filter-container_div {
        height: var(--s9);
        justify-content: unset;
      }
      .rs-panel-layout-buttons {
        margin-bottom: calc(var(--s4) + var(--s1px));
      }
      .rs-panel-available-layouts {
        display: flex;
        flex-direction: row;
        overflow: hidden;
        margin: 0 var(--sn6) 0 0;

        &__list {
          display: flex;
          grid-template-columns: unset;
          overflow-x: scroll;
          overflow-y: hidden;
          padding: 0;
          margin: 0 0 calc(var(--sn1) + var(--sn2px)) 0;

          @extend .webkit-styles;
        }

        .small-container {
          min-width: calc(var(--s23) - var(--s2px));
          height: calc(var(--s18) - var(--s2px));
          margin: 0 var(--s3) 0 0;
          &:last-of-type {
            margin: 0;
          }

          img {
            max-width: 100%;
            max-height: 100%;
          }
        }
      }
    }
  }
}
