@import 'src/styles/includes';

.password-dialog {
  font-size: var(--font-size-regular);

  & > div:nth-child(1)  {
    font-weight: var(--font-weight-bold);
    margin-bottom: var(--font-size-regular);
    margin-top: var(--sn2px);
    margin-left: var(--sn2px);
    height: calc(var(--s3px) + var(--s6));
    color: var(--color-white)
  }

  & > div:nth-child(2)  {
    margin-left: var(--sn2px);
    height: calc(var(--s3px) + var(--s6));
  }

  & > div:nth-child(3)  {
    margin-top: var(--font-size-s);
    display: flex;
    align-items: center;

    & > div:nth-child(2) {
      padding-left: var(--lineheight-s);
    }
  }
}
