@import 'src/styles/includes';

.admin-project-list {
  .project-container {
    h2 {
      color: var(--project-color-main);
      font-size: var(--font-size-xl);
    }

    .project-panel {
      color: var(--project-color-main);
      background-color: var(--background-primary);
      border-radius: var(--border-size-standard);
      box-shadow: var(--table-shadow);
      margin-bottom: var(--s6);
      overflow: hidden;
      padding: var(--s6);
      position: relative;

      @include query($lg, 'max') {
        display: flex;
        flex-direction: column;
        padding: var(--s4);
      }
    }

    .project-title-description-container {
      margin-bottom: var(--s4);
      display: flex;
      align-content: center;
      justify-content: space-between;
      width: 100%;
    }

    .project-panel-link-arrow {
      height: var(--s8);
      right: var(--s4);
      top: var(--s2);

      @include query($lg) {
        top: var(--s4);
      }
    }

    .project-panel-row {
      @include query($sm, 'max') {
        display: block;
      }

      @include query($lg) {
        display: flex;
      }
    }

    .project-panel-row.center-row {
      align-items: center;
    }

    .project-title {
      align-items: center;
      display: flex;
      font-size: var(--font-size-xxl);
      line-height: var(--lineheight-xxl);
      font-weight: var(--font-weight-bold);
      margin-right: var(--s5);

      .status-pill {
        align-items: center;
        display: inline-flex;
        margin-left: var(--s4);
        margin-top: var(--s1);
      }

      a {
        color: var(--project-color-main);
        font-weight: var(--font-weight-bold);

        &:hover {
          color: var(--project-color-main);
        }

        @include query($sm, 'max') {
          font-size: var(--font-size-xl);
          line-height: var(--lineheight-xl);
        }
      }
    }
  }

  .notification-icon {
    margin-right: var(--s3);
    margin-top: calc(-1 * var(--s2));

    @include query($md, 'max') {
      float: left;
      margin-top: var(--s1px);
    }
  }

  .activity-details {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-left: var(--s8);

    .activity-details-title {
      display: inline-block;
      font-size: var(--font-size-xs);
      color: var(--project-color-secondary);
    }

    .activity-details-number {
      display: inline-block;
    }

    .notification-icon::before {
      content: '';
      display: inline-block;
      width: rem(6);
      height: rem(6);
      border-radius: rem(6);
      background-color: var(--color-green);
      margin-bottom: var(--s3px);
      margin-right: rem(6);
    }
  }

  .user-details-container {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    width: 75%;

    @include query($md, 'max') {
      width: 100%;
    }

    > div {
      @include query($lg, 'max') {
        width: 50%;
      }
      @include query($md, 'max') {
        width: 50%;
      }
      @include query($sm, 'max') {
        width: 50%;
      }
      @include query($xs, 'max') {
        width: 100%;
      }
    }
  }

  .user-details-container:first-child {
    padding-right: var(--s8);
  }

  .user-details-container:last-child {
    padding-left: var(--s8);
  }

  .project-description.col-md-6 {
    @extend %clamp-3;

    color: var(--project-color-secondary);
    white-space: break-spaces;
    padding-right: var(--s8);
  }

  .project-content.col-md-6 {
    padding-left: var(--s8);
  }

  .deal-details-container {
    align-items: flex-start;
    display: flex;
    flex-wrap: wrap;
    &.col-md-6 {
      padding-left: var(--s8);
    }

    @include query($lg) {
      flex-wrap: nowrap;
      justify-content: space-between;
      width: 100%;
    }

    .detail-section {
      width: 32%;

      @include query($xs, 'max') {
        width: 50%;
      }
      .deal-details {
        .deal-details-content {
          color: var(--project-color-main);
          line-height: var(--lineheight-regular);
          @include query($md, 'max') {
            font-size: var(--font-size-s);
          }
          @include query($sm, 'max') {
            font-size: var(--font-size-regular);
          }
        }
      }
    }
  }

  .project-pill {
    display: inline-block;
    background-color: var(--color-grey-light);
    color: var(--background-default);
    padding: var(--s1) var(--s3);
    border-radius: var(--s4);
    margin: var(--s1) var(--s3) 0 var(--s3);
    font-size: var(--font-size-s);
    font-weight: var(--font-weight-medium);
  }

  .deal-details {
    display: flex;
    justify-content: center;
    flex-direction: column;

    .deal-details-title {
      display: inline-block;
      font-size: var(--font-size-xs);
      color: var(--project-color-secondary);
    }

    .deal-details-number {
      display: inline-block;
    }
  }
}
