@import 'src/styles/includes';

.user-search-select {
  .text-input-fieldset {
    .text-input-container.basic {
      .text-input-control-label {
        display: none;
      }

      .has-icon-left {
        bottom: 0;
        top: var(--s1);
      }
    }

    .text-input-container.basic.has-value .text-input {
      border: var(--border-size-standard) solid var(--input-border-active);
    }
  }

  .user-search-select-list,
  .user-search-select-no-results,
  .user-search-select-loading,
  .user-search-select-error {
    background: var(--input-background);
    position: absolute;
    z-index: 8;
    border-radius: var(--border-radius);
    margin-top: 1px;
    padding-top: var(--s2);
    border: solid 2px var(--color-page-background);

    span {
      display: flex;
      height: 1.5em;
      margin: var(--s2) var(--s4) var(--s4);
    }

    .loader {
      margin: var(--s4);
    }

    ul {
      padding-left: var(--s1);
      list-style-type: none;
    }

    li {
      margin: 0;

      button {
        background: none;
        width: 100%;
        text-align: left;
        color: var(--input-color);
      }
    }
  }
}
