@import 'src/styles/includes';

:root {
  --layout-border-on: var(--color-white);
  --layout-border-off: var(--color-grey-medium);
}

.theme-light {
  --layout-border-on: var(--project-color-main);
  --layout-border-off: var(--color-grey);
}
.page-title-container h2 {
  margin-bottom: var(--s3);
}
.project-overview-description-text {
  font-weight: var(--font-weight-regular);
  margin-bottom: var(--s2);
}
.modal-dialog .modal-content.topic-modal {
  padding: 0;
  background-color: var(--color-page-background);
}

.topic-modal-header {
  border-bottom: var(--s1px) solid var(--modal-border-color);
  background-color: var(--modal-header-background-color);
  padding: var(--content-spacing);

  .topic-close-button {
    position: absolute;
    top: var(--content-spacing);
    right: var(--content-spacing);
  }
}
.topic-modal-body {
  padding: var(--content-spacing);
  padding-bottom: 0;
  background: var(--color-page-background);
}
.select-component .select-container .select-element select {
  border-color: var(--color-grey-light-alt-2);
  &:focus {
    border-width: 2px;
  }
}
.topic-modal-footer {
  padding: var(--content-spacing);
}
.select-container {
  padding-bottom: var(--s8);
}
.modal-body-section {
  color: var(--project-color-main);

  .textarea-fieldset .textarea-container.basic .text-area {
    color: var(--project-color-main);

    &:not(:focus) {
      border: 1px solid var(--color-grey-light-alt-2);
    }
  }
}

.character-count-container {
  display: block;
}

svg.icon.icon-black.fill path.fill.variant {
  fill: var(--color-yellow);
}

.modal-footer {
  padding: var(--content-spacing);
  .hidden-content-warning {
    font-size: var(--font-size-s);
    padding-left: var(--s2);
    font-weight: var(--font-weight-bold);
    color: var(--color-black);
  }

  .hidden-content-container {
    width: 100%;
    text-align: center;
    background-color: var(--color-yellow);
    svg.icon.icon-black.fill path.fill.always-dark {
      fill: var(--color-yellow);
    }
    padding: var(--s3);
    margin: 0 0 var(--s5) 0;
  }
}

.ck.ck-balloon-panel {
  --ck-z-default: 1051; /* Bootstrap modal + 1 */
  --ck-z-modal: calc(var(--ck-z-default) + 999);
}

%large-layout-container {
  border-radius: var(--s2px);
  height: var(--s10);
  width: var(--s25);
}

%small-layout-container {
  border-radius: var(--s2px);
  display: inline-flex;
  height: var(--s5);
  margin-right: var(--s3);
  width: var(--s11);

  &:before,
  &:after {
    content: '';
    display: block;
  }
}

.single-column-icon-unchecked {
  @extend %large-layout-container;
  border: var(--s2px) solid var(--layout-border-off);
}

.single-column-icon-checked {
  @extend %large-layout-container;
  border: var(--s2px) solid var(--layout-border-on);
}

.double-column-icon-unchecked {
  @extend %large-layout-container;
  border: var(--s2px) solid var(--layout-border-off);
  display: flex;
  flex-direction: row;

  &:before {
    border-right: var(--s2px) solid var(--layout-border-off);
    content: '';
    display: inline-block;
    width: 49px;
  }
}

.double-column-icon-checked {
  @extend %large-layout-container;
  border: var(--s2px) solid var(--layout-border-on);
