.bottom-area-checked {
  border-top: var(--s2px) solid var(--layout-border-on);
  display: flex;
  flex-direction: row;
  position: relative;
  top: 52%;
  height: 48%;
  width: 100%;
  margin-right: 0px;

  & > {
    div {
      position: relative;
      width: 50%;
  
      &:first-child {
        border-left: none;
      }
  
      &:last-child {
        border-left: var(--s2px) solid var(--layout-border-on);
      }
    }
  }
}

.bottom-area-unchecked {
  border-top: var(--s2px) solid var(--layout-border-off);
  display: flex;
  flex-direction: row;
  position: relative;
  top: 52%;
  height: 48%;
  width: 100%;
  margin-right: 0px;

  & > {
    div {
      position: relative;
      width: 50%;
  
      &:last-child {
        border-left: var(--s2px) solid var(--layout-border-off);
      }
    }
  }
}

.left-area-checked {
  display: flex;
  flex-direction: column;
  position: relative;
  top: 0;
  margin: 0px;
  width: 49px;

  &:first-child {
    border-right: var(--s2px) solid var(--layout-border-on);
  }
  & > {
    div {
      position: relative;
      height: 52%;
      border-bottom: var(--s2px) solid var(--layout-border-on);
    }
  }
}

.left-area-unchecked {
  display: flex;
  flex-direction: column;
  position: relative;
