  }
  & > {
    div {
      position: relative;
      height: 52%;
      border-bottom: var(--s2px) solid var(--layout-border-off);
    }
  }
}

.inner-div-right {
  border-left: var(--s2px) solid var(--layout-border-off);
}

.icon-margin {
  margin-right: calc(var(--s3));

  & > {
    img {
      width: calc(var(--s12) + var(--s2px));
      height: calc(var(--s12) + var(--s2px));
    }
  }
}

