  }
}

.text-input-container.title-text-input {
  @include query($lg, min) {
    width: 40%;
  }
}

.textarea-fieldset .textarea-container.basic .text-area-control-label {
  color: var(--headings-primary);
  font-weight: var(--font-weight-medium);
}

.content-type-radio-group {
