@import 'src/styles/includes';

.connected-content {
  border-top: var(--border-standard);
  border-top-color: var(--color-separator);
  margin-top: var(--margin-double);
  padding-top: var(--padding-standard);

  &-title {
    font-weight: var(--font-weight-medium);
    margin-bottom: var(--margin-standard);
  }

  .linked-narratives {
    > div {
      cursor: auto;
    }

    overflow-y: auto;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding-right: 5px;

    @include query($lg) {
      flex-direction: row;
    }

    @include query($md, 'max') {
      max-height: none;
    }

    .narrative-section {
      @include query($lg) {
        width: calc(33% - 12px);
      }
    }
  }
}
