@import 'src/styles/includes';

.terms-and-conditions {
  hr {
    border-top-color: var(--color-grey-alt);
  }

  h2 {
    margin-bottom: 0;
  }

  &-text,
  &-legal-statement,
  &-actions {
    width: 65%;

    @include query($xxl, 'max') {
      width: 80%;
    }

    @include query($xl, 'max') {
      width: 100%;
    }
  }

  &-instructions {
    font-size: var(--font-size-l);
    line-height: var(--lineheight-l);
    font-weight: var(--font-weight-regular);
  }

  &-text {
    height: calc(100vh - 415px);
    padding-right: var(--s8);
    padding-bottom: 0;
    overflow-y: scroll;
    position: relative;
  }

  &-legal-statement {
    padding: 0 var(--s8) 0 0;
    font-family: var(--font-family);
    font-size: var(--font-size-regular);
    letter-spacing: 0;
    line-height: var(--lineheight-regular);
    margin: var(--s7) 0 var(--s14) 0;
  }

  &-actions {
    display: flex;
    justify-content: flex-end;
    margin-top: var(--s5);
    margin-bottom: var(--s12);

    @include query($md, 'max') {
      flex-direction: column-reverse;

      .button {
        width: 100%;
        &:first-of-type {
          margin-top: var(--s4);
        }
      }
    }
  }

  &-footer-fade {
    position: sticky;
    bottom: 0;
    left: 0;
    right: 0;
    height: 50px;
    width: 100%;
    display: block;
    background: rgb(20, 20, 20);
    background: rgb(20, 20, 20);
    background: linear-gradient(
      180deg,
      rgba(20, 20, 20, 0) 0%,
      var(--color-page-background)
    );
  }

  @include query($xl, 'max') {
    table tr td:nth-child(2) {
      word-break: break-all;
    }
  }
}
