@import 'src/styles/includes';

.project-state-change-cards {
  display: flex;
  flex-wrap: wrap;

  .project-state-change-card-container {
    width: calc((4 * var(--s25)) + (2 * var(--s2px)));
    min-width: calc((4 * var(--s25)) + (2 * var(--s2px)));
    max-width: calc((4 * var(--s25)) + (2 * var(--s2px)));

    margin: var(--s10) var(--s10) 0 0;
    display: flex;
    flex-direction: column;
    flex: 1;

    @include query($sm, max) {
      width: 100%;
      margin: var(--s10) 0 0 0;
      max-width: 100%;
    }

    background-color: var(--card-background-color);
    border-radius: var(--s2px);
    box-shadow: 0 var(--sn1px) 0 0 var(--color-grey-medium);
    font-weight: var(--font-weight-regular);
    font-size: var(--font-size-m);
    padding: var(--s6) var(--s10);

    .state-change-card {
      &--header {
        background-color: transparent;
        font-size: var(--font-size-l);
        font-weight: bold;
        margin-bottom: var(--s5);

        &__disabled {
          color: var(--color-grey);
        }
      }

      &--text {
        &-container {
          margin-bottom: var(--s5);
        }

        &-area {
          margin-bottom: var(--s3);

          &__disabled {
            color: var(--color-grey);
          }
        }
      }

      &--button-container {
        display: flex;
        justify-content: flex-end;
        margin-top: auto;

        button {
          margin-left: var(--s8);
        }
      }
    }
  }
}

.project-state-change {
  &-warning {
    display: flex;
    flex-wrap: wrap;

    background-color: var(--card-background-color);
    border-radius: var(--s2px);
    box-shadow: 0 var(--sn1px) 0 0 var(--color-grey-medium);
    font-weight: var(--font-weight-light);
    font-size: var(--font-size-m);
    padding: var(--s6) var(--s10) var(--s6) var(--s10);

    min-width: calc((4 * var(--s25)) + (2 * var(--s2px)));
    flex-direction: column;
    flex: 1;

    &--body {
      display: flex;
    }

    &--text-area {
      padding-left: var(--s5);
      margin-bottom: var(--s3);
    }

    margin: var(--s5) 0;
  }

  &--buttons {
    button {
      border: solid var(--s1px) var(--button-primary-color);
      margin-right: var(--s5);

      &.transparent-button {
        background-color: transparent;
        color: var(--button-primary-color);
      }
    }
  }
}