@import 'src/styles/includes';

.rte-formik-field-2 {
  align-items: center;
  display: flex;
  position: relative;

  .text-input-fieldset {
    position: relative;
    padding: 0;
    width: 100%;

    &.d3-rich-text {
      max-width: none;

      .ck.ck-reset.ck-editor.ck-rounded-corners {
        border: none;
      }
    }

    label.text-input-control-label {
      padding: var(--s9) 0 0 var(--s3);
      position: absolute;
      z-index: 1;
      color: var(--input-color-placeholder);
      font-size: var(--font-size-l);
      font-weight: var(--font-weight-regular);

      @include query($md) {
        font-size: var(--font-size-l);
      }
    }

    .ck.ck-editor__main > .ck-editor__editable {
      background: none;
      min-height: var(--s17);
      transition-duration: 0s !important;

      &_inline {
        padding-top: var(--s2) !important;
        padding-left: var(--s1) !important;
      }

      &:not(.ck-editor__nested-editable).ck-focused {
        background: none;
        box-shadow: none;
        padding-left: var(--s2px) !important;
      }
    }

    .ck.ck-editor__main > .ck-editor__editable_inline {
      .rich-text-editor-intro {
        padding: 0 var(--s2);
        margin-left: 0;
        padding-left: var(--s2);
        border-radius: var(--border-radius);
        &:hover {
          background: var(--input-background);
        }
      }
    }
  }

  .description-icon {
    display: block;
    color: var(--input-color-placeholder);
    position: absolute;
    z-index: 1;
    height: var(--s5);
    margin-right: var(--s3);
    pointer-events: none;
  }

  &.label-lifted {
    .text-input-fieldset {
      label.text-input-control-label {
        margin: 0;
      }
    }

    .icon {
      display: none;
    }
  }

  .ck.ck-toolbar {
    display: none;
  }

  .ck-editor__editable.ck-blurred {
    min-height: unset;
  }
}

.ck-rounded-corners .ck.ck-editor__main > .ck-editor__editable,
.ck.ck-editor__main > .ck-editor__editable.ck-rounded-corners {
  border-radius: var(--s1) !important;
}
