#analysisDescription {
  font-family: var(--font-default);
  height: 100%;
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;

  .description-header {
    color: var(--text-primary-alt);
    font-size: var(--font-size-l);
    font-weight: var(--font-weight-bold);
    margin-bottom: var(--s3);
  }

  .description-subheader {
    display: flex;
    justify-content: space-between;

    border-bottom: 1px solid var(--text-primary);
    padding-bottom: 0.5em;
    margin-bottom: 1em;

    span {
      margin: 0 var(--s3) 0 0;
    }
  }

  div.description-body {
    padding-bottom: rem(16);
  }

  .library-item-error {
    color: var(--color-orange-theme);
    display: flex;
    flex-direction: row;

    &:before {
      content: '';
      background: url('data:image/svg+xml;utf8,<svg aria-label="alert" height="100%" viewBox="0 0 32 32" width="18" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"><g><path fill="%23d04a02" d="M31.742 27.158C32.476 28.42 31.558 30 30.088 30H1.912C.442 30-.476 28.42.258 27.158l14.088-24.21c.736-1.264 2.572-1.264 3.308 0l14.088 24.21zM16 26a2 2 0 100-4 2 2 0 000 4zm-2-8h4v-8h-4v8z"></path></g></svg>')
        no-repeat 0px var(--s2);
      height: var(--s10);
      width: var(--s10);
    }

    span,
    strong {
      color: inherit;
    }
  }
}

.edit-button-container {
  display: flex;
  justify-content: flex-end;

  border-top: 1px solid var(--text-primary);
  padding-top: 0.5em;
  margin-top: 1em;
}
