@import 'src/styles/includes';

.library-item-card2 {
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: auto;
  align-items: center;
  overflow: hidden;
  margin-bottom: var(--s6);
  border-radius: var(--table-border-radius);
  background-color: var(--background-primary);
  box-shadow: var(--card-shadow);
  border-color: var(--background-primary);
  border-style: solid;
  border-width: 0 1px 1px 1px;
  min-width: 25vw;
  max-width: 25vw;

  @include query($sm, 'max') {
    flex-basis: auto;
    min-width: unset;
    max-width: 60vw;
  }

  display: flex;
  padding: var(--s1) var(--s4);
  margin-bottom: var(--s3);

  .library-item-card-icon {
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    min-height: 120px;
    padding: var(--s8) var(--s4) var(--s6) var(--s6);
    border-color: var(--table-shadow-color);
    border-style: solid;
    border-width: 0 0 1px 0;

    .icon-grey-theme {
      &.fill .fill {
        fill: var(--icon-grey-color);
      }
      &.outline .outline {
        fill: var(--icon-grey-color);
      }
    }
  }

  .library-item-card-title {
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    font-size: var(--font-size-s);
    font-weight: var(--font-weight-bold);
    padding: var(--s3) var(--s1) var(--s3) var(--s1);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 90% !important;
  }

  .library-item-card-placeholder {
    @extend %clamp-1;

    color: var(--library-item-label-color);
    padding: var(--s1) var(--s1) 0 var(--s1);
    font-size: var(--font-size-s);
    font-weight: var(--font-weight-bold);
    margin-left: 0;
    margin-right: 0;
    max-width: 90%;
    max-height: var(--s10);
  }

  .library-item-card-placeholder h2,
  h3,
  h4,
  p {
    line-height: var(--lineheight-xl);
    white-space: nowrap;
  }

  .library-item-card-placeholder ul {
    overflow: hidden;
    list-style-position: inside;
    margin-left: var(--sn4);
  }
  .library-item-card-placeholder li {
    margin-top: 0;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-height: var(--lineheight-xl);
  }

  .rich-text-card-inner {
    flex-grow: 1;
  }
}

.library-item-card-icon,
.library-item-card .library-item-card-icon {
  padding: var(--s2);
  min-height: var(--s8);
  border: none;
  margin-left: 0;
  margin-right: 0;
}

.library-item-card .library-item-card-title {
  padding: 0 var(--s1) 0 var(--s1);
  margin-left: 0;
  margin-right: 0;
  overflow: hidden;
}

.library-item-card-icon svg {
  width: 22px;
}

.library-item-icon,
.library-item-name {
  padding: var(--s2);
}
