@import 'src/styles/includes';

.usersnippet-container {
  align-items: center;
  color: var(--project-color-main);
  display: flex;
  height: var(--s12);
  margin-right: var(--s10);
  width: 100%;

  @include query($md, 'max') {
    margin: var(--s2) 0;
    margin-right: var(--s2);
  }
}

.usersnippet-photo {
  align-items: center;
  background-color: var(--color-yellow);
  border-radius: var(--s25);
  color: var(--color-grey-dark);
  font-weight: var(--font-weight-medium);
  display: flex;
  height: calc(var(--s10) + var(--s2px));
  justify-content: center;
  min-width: calc(var(--s10) + var(--s2px));
  width: calc(var(--s10) + var(--s2px));
}

.usersnippet-text {
  padding-left: var(--s3);
}

.usersnippet-name {
  font-weight: normal;
  color: var(--project-color-main);
  font-size: var(--font-size-regular);
  line-height: var(--lineheight-xs);
}

.usersnippet-position {
  font-weight: normal;
  color: var(--project-color-secondary);
  font-size: var(--font-size-s);
  line-height: var(--s6);
}
