@import 'src/styles/includes';

#popover-linked-adjustments {
  border: none;
  max-width: rem(285);

  section {
    display: block;
    width: 100%;
  }

  .popover-body {
    color: var(--project-color-secondary);
    font-size: var(--font-size-s);
    max-height: rem(225);
    overflow-y: auto;
    width: auto;

    .linked-adjustments-title {
      width: 100%;
      padding: 0 0 var(--s3);
      margin-bottom: var(--s3);
      border: var(--modal-border);
      border-bottom: var(--popup-border);
    }

    .link-target-document {
      .link-target-document-name {
        padding-bottom: var(--s1);
        color: var(--project-color-main);
        font-weight: bold;
      }

      a.link-target-adjustment-name {
        display: block;
        &:link,
        &:active,
        &:hover,
        &:visited,
        &:focus {
          color: var(--color-orange-theme);
        }
        padding-bottom: 0.25rem;
      }

      .strikethrough {
        text-decoration: line-through;
      }

      span {
        display: block;
        padding-bottom: 0.25rem;
      }
    }
  }
}
