@import 'src/styles/includes';

.rs-panel-layout-tab {
  display: flex;
  flex-direction: column;
  background-color: var(--background-default);
  height: 100%;
  padding: var(--s6);

  .rs-panel-available-layouts {
    background-color: var(--background-default);
    overflow: hidden;
    display: flex;
    &__list {
      margin: 0;
      padding: 0 calc(var(--s2) + var(--s2px)) 0 0;
      display: grid;
      grid-template-columns: 50% 50%;
      overflow-y: scroll;
      & > div:nth-child(odd) {
        margin-right: calc(var(--s2) + var(--s1px));
      }
      & > div:nth-child(even) {
        margin-left: calc(var(--s2) + var(--s1px));
      }
      & > div {
        margin-bottom: calc(var(--s4) + var(--s2px));
      }

      &::-webkit-scrollbar {
        width: calc(var(--s1) + var(--s2px));
      }

      &::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 calc(var(--s1) + var(--s2px))
          rgba(0, 0, 0, 0.3);
        box-shadow: inset 0 0 calc(var(--s1) + var(--s2px)) rgba(0, 0, 0, 0.3);
      }

      &::-webkit-scrollbar-thumb {
        background-color: var(--color-grey-medium);
        border-radius: var(--s3px);
      }
    }
    .small-container {
      position: relative;
      display: flex;
      flex-grow: 1;
      flex-shrink: 1;

      img {
        max-width: 100%;
        max-height: 100%;
      }
    }
  }

  .layout-image-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(208, 74, 2, 0.28);
    border: var(--s2px) solid var(--color-orange);
  }
}
