.project-status {
  display: flex;
  align-items: flex-start;
  font-size: var(--font-size-regular);
  line-height: var(--lineheight-regular);
  font-weight: var(--font-weight-regular);

  &__icon {
    margin: 0 var(--s4) 0 var(--s4);
    @media screen and (max-width: 767px) {
      margin-left: 0;
    }
  }

  .project-loader {
    margin-top: 0;
  }
}
