.connected-kfas-section {
  padding-top: var(--s6);
  border-top: var(--s1px) solid var(--color-grey-medium);

  .theme-light & {
    border-top: var(--s1px) solid var(--color-grey);
  }

  .connected-kfas-section-heading {
    color: var(--color-orange-theme);
    font-size: var(--font-size-regular);
    font-weight: var(--font-weight-bold);
    line-height: var(--lineheight-regular);
  }

  .kfa-connected-kfa-title {

    &:last-child {
      margin-bottom: 0;
    }
  }

  .kfa-connected-kfa-link {
    display: block;
    font-size: var(--font-size-regular);
    font-weight: var(--font-weight-medium);
    color: var(--project-color-secondary);
    line-height: var(--lineheight-regular);

    &:hover {
      .theme-dark & {
        color: var(--color-white);
      }
    }
  }
}

