@import 'src/styles/includes';

.file-upload-actions {
  display: flex;
  justify-content: flex-end;

  @media (max-width: #{$md + 'px'}) {
    float: right;
  }

  .ui-button.icon-button {
    &.file-download {
      display: none;
      @media (min-width: #{($md + 1) + 'px'}) {
        display: flex;
        align-items: center;
      }
    }

    display: inline-flex;
    background: var(--action-button-background);
    width: var(--s10);
    height: var(--s10);
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border: var(--s1px) solid transparent;

    &:not(:last-child) {
      margin-right: var(--s3);
    }

    svg {
      height: var(--s6);
      width: var(--s6);
      pointer-events: none;
    }

    &:hover {
      background: var(--action-button-background-hover);
    }
  }

  .popover {
    .ui-button.button-menu.file-download {
      display: block;
      @media (min-width: #{($md + 1) + 'px'}) {
        display: none;
      }
    }
  }
}
