@import 'src/styles/includes';

.terms-and-conditions-view {
  .tab-list {
    width: 100%;
    max-width: 1024px;
    height: rem(54);
    background-color: var(--background-secondary);
    margin-bottom: 0;
    display: flex;

    a {
      align-items: center;
      background-color: var(--tab-background-color);
      cursor: pointer;
      color: var(--color-orange-theme);
      display: flex;
      height: var(--s12);
      justify-content: center;
      margin-left: 6px;
      margin-top: 6px;
      padding: 0 var(--s6);
    }

    a.active {
      font: 80px;
      background-color: var(--color-page-background);
      border-width: 1px 1px 0 1px;
      color: var(--project-color-main);
    }
  }

  .tab-content {
    @include scrollbars();

    height: calc(100vh - 410px);
    overflow-y: scroll;
    padding: var(--s8) var(--s8) 0 0;
    width: 100%;
    max-width: 1024px;

    .terms-and-conditions-footer-fade {
      position: sticky;
      bottom: 0;
      left: 0;
      right: 0;
      height: 50px;
      width: 100%;
      display: block;
      background: rgb(20, 20, 20);
      background: linear-gradient(
        180deg,
        rgba(20, 20, 20, 0) 0%,
        var(--color-page-background)
      );
    }
  }

  @include query($xl, 'max') {
    table tr td:nth-child(2) {
      word-break: break-all;
    }
  }
}
