.sidebar-link {
  font-size: var(--s4);
  color: var(--menu-link-color);
  border-bottom: 1px solid var(--color-separator);
  padding: var(--s4) var(--s2);
  display: flex;

  &:hover,
  &:visited:hover,
  &:active:hover {
    text-decoration: none;
    color: var(--menu-link-color);
  }
}

.active-sidebar-link {
  background-color: var(--color-page-background);
  color: var(--menuitem-color-label);
}
