@import '../../../styles/includes';

.shared-pill {
  background-color: var(--input-alert-color);
  color: var(--color-black);
  padding: var(--s1px) var(--s1) var(--s1px);
  border-radius: var(--s4);
  margin-right: var(--margin-half);
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-medium);
  white-space: nowrap;
  display: inline-block;
  height: 18px;
  line-height: 17px;
  span {
    color: var(--color-black);
    .hide-for-mobile {
      @include query($sm, max) {
        display: none;
      }
    }
  }
  svg {
    padding-bottom: var(--s0);
  }
  svg.fill path.fill {
    fill: var(--background-default);
  }
}

.text-shared-with-client {
  margin-left: var(--s0);
  margin-right: var(--s1);
}
