@import 'src/styles/includes';

.action-button-container {
  .button.button-link {
    padding: 0 var(--s3);
  }

  .action-button {
    background: var(--action-button-background);
    width: 38px;
    height: 38px;
    border-radius: 100px;
    padding: 0 !important;
    overflow: visible;
    color: var(--color-page-background);
  }

  .action-button:hover {
    background: var(--action-button-background);
    border: var(--card-border-hover);
  }

  .action-button:active {
    background: var(--action-button-background);
  }
}

.button-menu > svg {
  margin-right: var(--s1);
}
