@import 'src/styles/includes';

.textarea-container.title-fieldset {
  height: max-content;
  min-height: var(--s16);

  @include query($md) {
    min-height: var(--s20);
  }

  &:not(.label-lifted) .text-area-control-label {
    font-size: var(--font-size-l);
    font-weight: var(--font-weight-bold);
    padding: var(--s3) 0 0 var(--s3);

    @include query($md) {
      font-size: var(--font-size-xl);
      padding: var(--s5) 0 0 var(--s3);
    }
  }

  textarea {
    transition: padding-bottom var(--transition);
  }

  &.label-lifted {
    padding-bottom: 0;

    .text-area-control-label {
      padding: var(--s3) 0 0 var(--s3);
    }

    textarea {
      &:empty {
        min-height: var(--s20);
      }

      &:not(:empty) {
        line-height: var(--lineheight-xl);
        padding-bottom: var(--s8);
      }
    }
  }

  .text-area {
    font-size: var(--font-size-xl);
    font-weight: var(--font-weight-medium);
    padding-right: var(--s3);
  }

  .edit-icon {
    margin-top: var(--s5);

    @include query($md) {
      margin-top: var(--s7);
    }
  }
}
