@import 'src/styles/includes';

.separator + .external-source-links {
  padding-top: var(--content-spacing);
}

.external-source-links {
  text-align: end;
}
.external-source-link {
  color: var(--color-orange-theme);
  text-decoration: underline;

  &:hover {
    color: var(--text-primary);
  }
}
.source-file-tooltip-icon {
  vertical-align: text-bottom;
  display: inline-flex;
}
