@import 'src/styles/includes';

.active-content-header-container {
  height: calc(var(--s16) + var(--s2px));
  background-color: var(--topbar-color);
  border-bottom: var(--border-standard);
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
}

.active-content-header {
  padding: 0 var(--container-spacing);
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include query($md, 'max') {
    width: 100%;
    padding: 0 var(--s5);
  }

  @include query($md) {
    @include query($lg, 'max') {
      width: 100%;
    }
  }

  @include query($lg) {
    width: 80%;
  }

  .icon-label {
    color: var(--text-alternate-lighter);
  }

  .active-content-header-title {
    display: flex;
  }

  .active-content-header-actions {
    align-items: center;
    display: flex;

    .ui-button:not(.button-link) {
      white-space: nowrap;
    }
  }

  svg > path {
    fill: var(--background-secondary);
  }

  svg > path {
    fill: var(--background-secondary);
  }
}
