@import 'src/styles/includes';

.power-bi-report-select .select-container {
  display: flex;
  select {
    flex: auto;
    max-width: 100%;
  }
  &::after {
    right: var(--s6);
  }
}

.hidden-power-bi-container {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

.visible-power-bi-container {
  padding: var(--s2);
  height: rem(280);
}
