@import '../../../../styles/includes';

%progress-step-width {
  width: #{rem(348)};
}

.freeform-analysis-form-header + .form-content-container-background,
.library-item-list-container {
  .step-indicator-container {
    margin-bottom: var(--s6);
    margin-top: var(--s4);
  }

  .stepindicator-container {
    @extend %progress-step-width;
  }

  .form-content h1 {
    margin-bottom: var(--s7);
  }
}

.empty-library-items-list-card {
  margin-bottom: var(--s12);
}

.library-item-list-container {
  .stepindicator-container {
    @extend %progress-step-width;
  }
  .form-layout .active-content-header-container .active-content-header {
    width: 100%;
  }

  @include query($sm, 'max') {
    h1 {
      font-size: var(--font-size-xl);
    }
  }

  .form-layout .form-content-container {
    background-color: var(--color-page-background);
  }

  .list-table {
    padding: 0 0 var(--s3) 0;
    @include query($sm, 'max') {
      flex-direction: column;
    }

    .row-selected {
      border: 1px solid var(--color-orange-light);
    }

    .list-table-row {
      @include query($sm, 'max') {
        height: auto;
        padding: var(--s4);

        &:first-of-type {
          width: auto;
        }
      }
    }

    .cell {
      &:first-of-type {
        @include query($md, 'min') {
          margin: var(--s4);
          margin-right: 0;
        }
      }

      &:nth-of-type(2) {
        margin-left: 0;
      }

      &:last-of-type {
        @include query($sm, 'max') {
          margin-right: 0;
        }
      }
    }

    .bold-cell {
      @include query($sm, 'max') {
        font-size: var(--font-size-regular);
        grid-column: 1 / 4;
        border-bottom: 1px solid var(--color-grey-medium);
        margin-bottom: var(--s3);
        padding-bottom: var(--s4);
      }
    }
  }

  .active-content-header-actions {
    @include query($xs, 'max') {
      button {
        font-size: var(--font-size-s);
      }
    }
  }

  .success-message {
    font-weight: var(--font-weight-bold);
    margin-left: var(--s5);
    color: var(--color-green);
  }

  .library-item-description {
    color: var(--project-color-main);
  }

  p.twothirds {
    max-width: 66%;
    @include query($lg, 'max') {
      max-width: 66%;
    }
    @include query($sm, 'max') {
      max-width: 100%;
    }
  }
}

.library-item-checkbox[type='checkbox']:not(:checked),
.library-item-checkbox[type='checkbox']:checked {
  position: absolute;
  left: -9999px;
}
.library-item-checkbox[type='checkbox']:not(:checked) + label,
.library-item-checkbox[type='checkbox']:checked + label {
  position: relative;
  padding-left: 3em;
  font-weight: var(--font-weight-bold);
  cursor: pointer;
}
.library-item-checkbox[type='checkbox']:not(:checked) + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: var(--s6);
  height: var(--s6);
  border: 1px solid var(--input-border);
  background: var(--input-background);
  border-radius: var(--s2px);
}
.library-item-checkbox[type='checkbox']:checked + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: var(--s6);
  height: var(--s6);
  border: 1px solid var(--button-primary-color);
  background: var(--button-primary-color);
  border-radius: var(--s2px);
}
.library-item-checkbox[type='checkbox']:not(:checked) + label:after {
  opacity: 0;
  transform: rotate(45deg) scale(0);
}
.library-item-checkbox[type='checkbox']:checked + label:after {
  content: '';
  position: absolute;
  display: block;
  left: 9px;
  top: 3px;
  width: 6px;
  height: 14px;
  border: 2px solid white;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg) scale(1);
}
/* disabled checkbox */
.library-item-checkbox[type='checkbox']:disabled:not(:checked) + label:before,
.library-item-checkbox[type='checkbox']:disabled:checked + label:before {
  border-color: var(--icon-primary-color-disabled);
  background-color: var(--button-primary-color-disabled);
}
.library-item-checkbox[type='checkbox']:disabled:checked + label:after {
  color: var(--button-primary-color-disabled);
}
.library-item-checkbox[type='checkbox']:disabled + label {
  color: var(--button-primary-color-disabled);
}
/* accessibility */
.library-item-checkbox[type='checkbox']:checked:focus + label:before,
.library-item-checkbox[type='checkbox']:not(:checked):focus + label:before {
  border: 2px solid var(--button-primary-color);
}

.library-item-checkbox[type='checkbox']:checked ~ .list-table .list-table-row {
  border-color: red !important;
  border-style: solid;
  border-width: 2px;
}
