.right-panel2 {
  display: flex;
  position: fixed;

  flex: 1 1 auto;
  flex-direction: column;

  top: 0;
  right: 0;
  height: 100%;
  width: 30vw;
  z-index: 10;
  
  background-color: var(--background-default);
  border-left: var(--border-standard);

  overflow: hidden;

  &__header {
    display: flex;
    justify-content: flex-end;
    padding: var(--content-spacing);
  }
}
