@import 'src/styles/includes';

.section-name {
  font-size: var(--font-size-xs);
  line-height: var(--lineheight-xs);
  text-align: left;
  color: var(--color-disabled);
}

.analysis-main-content {
  .analysis {
    color: var(--color-blue);
  }

  .section-name {
    padding-top: var(--s3);
  }
}

.analysis-details-screen.tags-list-container {
  margin-bottom: var(--s12);
  margin-top: var(--s3);
}

.analysis-text {
  font-size: var(--font-size-l);
  line-height: var(--lineheight-l);
  text-align: left;
  color: var(--color-grey-light);
}

.analysis-content {
  .section-name {
    padding-top: var(--s3);
    color: var(--label-color-focused);
  }
}
