@import 'src/styles/includes';

.page-content {
  .overview-title-link {
    color: var(--card-link-color);
    transition: color var(--transition);

    svg.icon path.outline {
      fill: var(--card-link-color);
    }

    &:hover {
      color: var(--card-link-color-hover);

      svg.icon path.outline {
        fill: var(--card-link-color-hover);
      }
    }
  }

  .project-overview-subpanel .addbutton {
    svg.icon {
      path.outline {
        fill: var(--button-primary-color);
      }
      &:hover {
        path.outline {
          display: none;
        }
        path.fill {
          display: inline;
          fill: var(--button-primary-color);
        }
      }
    }
  }
}
