@import 'src/styles/includes';

.connected-content-controls {
  max-width: 80%;
  padding-bottom: var(--content-spacing);

  .text-input-fieldset .text-input-container .text-input {
    border-width: var(--s1px);
  }
}

.connect-content-icon {
  margin: var(--sn1) var(--s2) 0 0;
}

.connected-content {
  &-title {
    font-weight: var(--font-weight-medium);
    margin-bottom: var(--margin-standard);
  }
}

.connected-content-controls + .edit-connected-content {
  border-top: none;
  padding-bottom: var(--content-spacing);
}
