@import 'src/styles/includes';

.add-project-header-menu {
  text-align: right;
  width: 100%;

  @include query($md) {
    display: none;
  }
}

#popover-add-project {
  max-width: 285px;

  .popover-header {
    padding: 0.5rem 0.75rem;
    background-color: var(--popup-background-color);
    border: var(--popup-border);
    border-bottom: none;
    border-radius: 4px 4px 0px 0px;

    &:before {
      border-bottom-color: var(--popup-color);
    }
  }

  .popover-body {
    min-height: var(--s10);
    border-radius: var(--s1);
    border: var(--popup-border);
    box-shadow: var(--popup-shadow);
    font-size: var(--font-size-regular);
    background-color: var(--popup-background-color);
    width: auto;
    color: var(--popup-color);
  }

  .arrow {
    &::after {
      border-bottom-color: var(--popup-background-color);
      top: 3px;
    }
    &::before {
      border: inherit solid transparent;
      border-bottom-color: var(--popup-border-color);
      top: 1px;
      border-width: 0 0.5rem 0.5rem;
    }
  }
}

.add-header-popover-overlay {
  background: var(--color-black);
  bottom: 0;
  display: block;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 2;
  opacity: 0.35;
}
