@import 'src/styles/includes';

.delete-library-item-button {
  background: none;

  &:hover,
  &:active,
  &:focus,
  &:focus:active {
    background: none;
  }

  &:disabled {
    background: none;
  }
}

.popover-delete-library-item.bs-popover-left {
  background-color: transparent;

  &,
  * {
    transition: none;
  }
  .arrow {
    &::before {
      border-left-color: var(--button-primary-color);
      opacity: 1;
    }
  }

  .popover-header,
  .popover-body {
    background-color: var(--popup-background-color);
    border: none;
  }

  .popover-header {
    color: var(--project-color-main);
    font-weight: var(--font-weight-bold);
    padding: 0 0 var(--content-spacing);
  }

  .popover-body {
    border: var(--s1px) solid var(--button-primary-color);
    color: var(--project-color-secondary);

    .loader {
      margin: 0;
    }

    .actions-container {
      border-top: var(--s1px) solid var(--color-separator);
      display: flex;
      font-weight: var(--font-weight-medium);
      margin-top: var(--s4);
      padding-top: var(--s4);
      width: 100%;

      & > *:first-child {
        margin-right: var(--s4);
      }

      .close {
        font-weight: var(--font-weight-medium);

        &:hover {
          background: var(--hover-orange);
          text-decoration: underline;
        }

        &:active {
          background: var(--color-orange-active-alt);
          text-decoration: underline;
        }
      }

      .confirm {
        font-weight: var(--font-weight-medium);
      }
    }
  }
}
