@import '../mixins';
// Line Clamping
// usage: @extend %clamp-2 (for two lines)
$clamp-lines: 10;

@for $l from 1 through $clamp-lines {
  %clamp-#{$l} {
    display: -webkit-box;
    -webkit-line-clamp: #{$l};
    line-clamp: #{$l};
    overflow: hidden;
    text-overflow: ellipsis;
    overflow-wrap: break-word;
    -webkit-box-orient: vertical;
  }
}

@for $l from 1 through $clamp-lines {
  %clamp-sm-max-#{$l} {
    @include query($sm, max) {
      display: -webkit-box;
      -webkit-line-clamp: #{$l};
      line-clamp: #{$l};
      overflow: hidden;
      text-overflow: ellipsis;
      overflow-wrap: break-word;
      -webkit-box-orient: vertical;
    }
  }
}

%max-content-width {
  margin: 0 auto;
  max-width: rem($max-content-width);
  width: 100%;
}
