.question-footer {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin-top: var(--s4);
  justify-content: space-between;

  .icon.fill.back {
    margin-right: var(--s1);
  }

  .question-back {
    align-items: center;
    display: flex;

    &.inactive {
      color: var(--color-grey-medium);
      pointer-events: none;
    }
  }

  .answer-later {
    color: var(--color-orange-theme);
    margin-right: var(--s6);
    text-decoration: underline;

    &:hover,
    &:visited,
    &:active {
      color: var(--color-orange-theme);
      text-decoration: none;
    }
  }
}
