@import 'src/styles/includes';

.project-overview-placeholder-text {
  font-size: var(--font-size-l);
  margin-bottom: var(--s10);
}

.add-topic-button,
.project-overview-label {
  display: flex;
  align-items: center;
}

.project-overview-label {
  margin-bottom: 0.7em;
}

.add-topic-icon,
.project-overview-icon {
  margin-right: 0.7em;
}

.project-overview-header-container {
  padding: 0 var(--s6) 0 0;
  display: flex;
  justify-content: space-between;
  align-content: center;

  .add-topic-container {
    display: flex;
    align-items: center;
    color: var(--button-primary-color);
  }
}
