@import 'src/styles/includes';

.analysis-list-container {
  @extend %max-content-width;

  padding: var(--content-spacing) var(--container-spacing) var(--s12);

  & > h2 {
    margin-bottom: var(--s14);
  }

  .list-container-header {
    @include query($sm, max) {
      margin-bottom: var(--s6);
    }
  }

  .list-table {
    .list-table-row {
      .cell:not(:first-of-type) > span {
        color: var(--project-color-secondary);
      }
    }
  }

  table {
    tr > td:not(:first-of-type) {
      color: var(--project-color-secondary);
    }
  }

  .toggle-container {
    float: right;

    button.button-toggle svg,
    .button.button-toggle svg {
      margin-left: var(--s2);
    }

    .button {
      padding: var(--s2) 0;
    }

    .button-toggle {
      background: transparent;
      border-color: transparent;
      color: var(--button-primary-color);
    }
  }

  .title-cell {
    &.cell {
      padding-bottom: 0;
    }

    a {
      font-size: var(--font-size-regular);
      font-weight: var(--font-weight-bold);
      line-height: var(--s6);
    }
  }

  .analyses-view-h2 {
    margin-bottom: 0px;
  }

  .tageditor {
    margin-top: 0px;

    .a-form-error {
      margin-bottom: 0px;
    }

    .input-field-container {
      margin-top: 0px;
    }
  }
}
