@import 'src/styles/includes';

:root {
  --navbar-width: var(--s21);

  @include query($xxl) {
    --navbar-width: var(--navbar-width-xxl);
  }
}

.grid-cards {
  display: grid;
  grid-gap: var(--s6);
  grid-template-columns: 1fr;
  width: 100%;

  @include query($lg) {
    align-items: flex-start;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    max-width: calc(
      100vw - var(--navbar-width) - var(--container-spacing) -
        var(--container-spacing)
    );
  }

  .card-container {
    display: flex;
    flex-direction: column;
    flex: 0 1 auto;
    width: 100%;
  }
}

.card-container {
  background-color: var(--card-background-color);
  border-radius: var(--table-border-radius);
  border: var(--s1px) solid var(--card-background-color);
  box-shadow: var(--card-shadow);
  padding: var(--content-spacing);

  &:hover {
    border-color: var(--table-shadow-color);
  }

  .row {
    margin: 0;
  }

  .card-title {
    margin-bottom: 0;
    overflow-wrap: break-word;

    a {
      color: var(--text-primary);
      display: block;
      margin-bottom: var(--s3);

      h2 {
        font-size: var(--font-size-l);
        line-height: var(--lineheight-l);
        margin-bottom: 0;
        word-break: break-word;
      }
    }
  }

  .card-description {
    @extend %clamp-2;

    color: var(--project-color-secondary);
    line-height: var(--line-height-regular);
    padding-bottom: var(--s1);
    white-space: break-spaces;

    p,
    li {
      font-size: var(--font-size-regular);
    }

    @include query($sm, max) {
      max-width: 90vw !important;
    }
  }

  .down {
    flex-direction: column;
    overflow: hidden;
  }

  .card-action-footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: var(--content-spacing);

    .pill-container {
      align-items: center;
      flex-wrap: wrap;
      max-width: 80%;

      [class*='pill'] {
        margin-bottom: var(--s1);
        margin-top: var(--s1);
      }
    }
  }
}
