@import 'variables';
@import 'mixins';

// Override default variables before the import
$theme-colors: (
  'primary': #e45c2b,
  'danger': #c52a1a
);

.btn-outline-dark:hover svg {
  fill: var(--color-white);
}

.regular,
.regular > div:nth-child(1) {
  font-weight: var(--font-weight-regular);
}

.medium,
.medium > div:nth-child(1) {
  font-weight: var(--font-weight-medium);
}

.bold,
.bold > div:nth-child(1) {
  font-weight: var(--font-weight-bold);
}

.col {
  display: flex;
  padding: 0;

  @include query($sm, 'max') {
    display: block;
    padding: 0;
  }
}

// Gaps to use as layout spacing between components when you don't want to stick additional margin or padding on the component itself

.gap-half {
  height: var(--s2); // 8px
}

.gap-normal {
  height: var(--s4); // 16px
}

.gap-double {
  height: var(--s8); // 32px
}

.gap-yah {
  height: var(--s12); // 48px yah
}

.hide-mobile {
  @include query($sm, 'max') {
    display: none !important;
  }
}

.hide-desktop {
  @include query($md) {
    display: none !important;
  }
}

.ghost {
  opacity: 0.25;
  filter: grayscale(100%);
}

.visually-hidden {
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  white-space: nowrap; /* added line */
}

.visibility-hidden {
  visibility: hidden;
}

svg {
  overflow: visible;
}

@import 'global/modal';
@import 'global/popover';
@import 'elements/anchor';
@import 'elements/card_container';
@import 'elements/button';
@import 'elements/radio';
@import 'elements/fieldset';

body {
  color: var(--text-primary);
  &.no-outline {
    background-color: var(--color-black);
    background-color: var(--color-page-background);
    cursor: inherit;
  }
}

a {
  color: #e45c2b;
}

.header-logo {
  @include query($sm, max) {
    width: 53px;
  }
}

html {
  overflow-x: hidden;
}

body {
  max-width: 100vw;
  position: relative;
}

button.button-no-appearance.pwc-user,
.pwc-user {
  background-color: var(--color-pink);
  color: var(--color-white);

  &:hover {
    background-color: var(--color-pink);
  }
}
button.button-no-appearance.client-user,
.client-user {
  background-color: var(--color-yellow);
  color: var(--color-grey-dark);

  &:hover {
    background-color: var(--color-yellow);
  }
}

.skip-navigation {
  align-items: center;
  color: var(--color-white);
  display: flex;

  &:not(.focus-visible) {
    clip-path: rect(0 0 0 0) !important;
    position: absolute;
    top: var(--sn15);
  }

  &.focus-visible {
    background-color: var(--button-primary-color);
    height: var(--s15);
    padding-left: var(--content-spacing);
    position: relative;
    top: 0;
    text-decoration: underline;

    &:hover {
      color: var(--color-white);
    }

    .js-focus-visible & {
      border: var(--s2px) solid var(--color-white);
      border-radius: var(--s2px);
      box-shadow: none;
    }
  }
}


