@import 'src/styles/includes';

@mixin base-mobile-styles() {
  @include query($sm, 'max') {
    width: 100%;
    padding-left: 0;
  }
}

@mixin mobile-styles() {
  @include base-mobile-styles;
  @include query($sm, 'max') {
    border-right: none;
    border-bottom: var(--s1px) solid var(--kpi-border-color);
    margin-bottom: var(--s5);
  }
}

@mixin content-formatting() {
  .content-item-container {
    $contentWidths: $xs 100, $sm 160, $md 590, $lg 662;
    .content-item-container {
      @each $size, $calc in $contentWidths {
        @include query($size) {
          width: calc(100vw - #{$calc}px);
        }
      }
    }
  }
}

.topic-panel {
  width: 100%;
  padding-bottom: var(--s2);
  
  &.topic-panel--equal-left-column {
    width: 50%;
    padding-right: var(--s5);
    border-right: var(--s1px) solid var(--kpi-border-color);

    @include mobile-styles;
    @include content-formatting;
  }
  
  &.topic-panel--equal-right-column {
    width: 50%;
    padding-left: var(--s5);

    @include base-mobile-styles;
  }

  &.topic-panel--big-left-column {
    @media screen and (max-width: calc(#{$lg}px - 1px)) {
      width: 50%;
    }
    width: 67%;
    padding-right: var(--s5);
    border-right: var(--s1px) solid var(--kpi-border-color);

    @include mobile-styles;
    @include content-formatting;
  }

  &.topic-panel--small-left-column {
    @media screen and (max-width: calc(#{$lg}px - 1px)) {
      width: 50%;
      height: 50%;
    }
    width: 33%;
    padding-right: var(--s5);
    border-right: var(--s1px) solid var(--kpi-border-color);

    @include mobile-styles;
    @include content-formatting;
  }

  &.topic-panel--middle-column {
    @media screen and (max-width: calc(#{$lg}px - 1px)) {
      width: 50%;
      height: 50%;
    }
    width: 33%;
    padding-left: var(--s5);
    padding-right: var(--s5);
    border-right: var(--s1px) solid var(--kpi-border-color);

    @include mobile-styles;
    @include content-formatting;
  }

  &.topic-panel--right-column {
    @media screen and (max-width: calc(#{$lg}px - 1px)) {
      width: 50%;
    }
    width: 33%;
    padding-left: var(--s5);

    @include base-mobile-styles;
  }

  &.topic-panel--full-column {
    width: 100%;

    @include mobile-styles;
    @include content-formatting;
  }
  
  &.topic-panel--stacked-medium-top-row {
    height: 60%;

    @include mobile-styles;
    @include content-formatting;
  }
  &.topic-panel--stacked-medium-bottom-row {
    height: 40%;
    padding-top: var(--s2);
    @include mobile-styles;
    @include content-formatting;
  }

  &__rich-text, &__kfa {
    max-width: $content-width;
  }
}

.topic-panel-stacked-large-top-small-bottom {
  display: flex;
  flex-direction: column;

  @include mobile-styles;
  @include content-formatting;
}

.big-top-panel { 
  @media screen and (max-width: calc(#{$lg}px - 1px)) {
    height: 50%;
  }   
  height: 80%;

  @include base-mobile-styles;
}

.small-bottom-panel {
  @media screen and (max-width: calc(#{$lg}px - 1px)) {
    height: 50%;
  } 
  height: 20%;

  @include base-mobile-styles;
}

.topic-panel-stacked-top501A-bottom502A {
  display: flex;
  flex-direction: column;
  & > div:first-child {
    & > div {
      width: 100%;
    }    
  }
  &> {
    div {
      display: flex;
      flex-direction: row;
    }
  }

  @include base-mobile-styles;
}

.topic-panel-2col-l50-2a-r50-1a {
  display: flex;
  flex-direction: row;
  padding: var(--s2);

  &:first-child {
    display: flex;
    flex-direction: column;
  }
  
  @include mobile-styles;
  @include content-formatting;
}

.top-left {
  width: 50%;
  height: 50%;
}

.bottom-left {
  width: 50%;
  height: 50%;
}

.right-column {
  width: 50%;
}