@import 'src/styles/includes';

.unexpected-error-content {
  text-align: center;

  h1.unexpected-error-content-header {
    padding-top: var(--s20);
    font-size: 16em;
    line-height: 16rem;
    font-weight: 700;

    @include query($md, 'max') {
      padding-top: var(--s10);
      font-size: 10em;
      line-height: 10rem;
    }
  }

  p {
    padding: var(--s8);
  }

  .btn-primary {
    padding: var(--s4);
  }
}
