.category-overview-caption {
  padding: var(--s6) var(--s12);
  border-bottom: 1px solid var(--color-separator);

  .dd-category-title {
    font-size: var(--s7);
    font-weight: 500;
    padding: var(--s3) 0;
  }
}

.category-overview-score-wrapper {
  padding: var(--s6) var(--s12);

  .dd-category-score {
    font-size: var(--s5);
    font-weight: 500;
    padding: var(--s3) 0;
    display: flex;
    align-items: flex-end;

    .dd-category-score-text {
      padding-right: var(--s3);
    }

    .text-input-fieldset {
      padding-bottom: 0;
      width: 112px;
    }
  }

  .category-description {
    border-bottom: 1px solid var(--color-separator);
  }
}

.category-overview-content {
  padding: var(--s6) var(--s12);
}
