@import 'src/styles/includes';

.project-role-user-field {
  display: grid;
  grid-template-columns: 296px;
  grid-template-areas:
    'descr descr'
    'ussel ussel'
    'pillC pillC'
    'pillL pillL'
    'pillP pillP';
  gap: var(--s2) var(--s6);
  grid-auto-flow: row;
  align-items: start;
  padding: 0;
  margin-bottom: var(--s7);
  margin-top: var(--s3);

  @include query($sm) {
    grid-template-columns: 296px;
    row-gap: 0;
    grid-template-areas: 'descr ussel ussel';
  }

  @include query($lg) {
    grid-template-areas: 'descr ussel .    ';
  }

  &.has-users {
    @include query($sm) {
      row-gap: 0;
      grid-template-areas: 'descr pillC pillC';
    }
  }

  &.has-users.allows-many {
    @include query($sm) {
      grid-template-columns: 296px;
      row-gap: 0;
      grid-template-areas:
        'descr ussel ussel'
        '.     pillC pillC'
        'pillL pillP pillP';
    }

    @include query($lg) {
      grid-template-areas:
        'descr ussel .    '
        '.     pillC pillC'
        'pillL pillP pillP';
    }
  }

  h4 {
    margin-bottom: 0;
    font-weight: 400;
    font-size: var(--font-size-m);
  }

  &.allows-many.has-users .text-input {
    margin-bottom: var(--s4);
  }

  .project-user-description-cell {
    grid-area: descr;
    width: 296px;
    line-height: normal;

    span {
      font-size: var(--font-size-s);
      color: var(--project-color-secondary);
      vertical-align: top;
      line-height: var(--lineheight-s);
      display: inline-block;
    }
  }

  .user-search-select {
    grid-area: ussel;
    .text-input-fieldset {
      padding: 0;

      &.text-input-control-label {
        font-size: var(--font-size-l);
      }
      &.label-lifted .text-input-control-label {
        font-size: var(--font-size-s);
      }
      .text-input {
        font-size: var(--font-size-m);
      }
      .edit-icon {
        margin-top: var(--s3);
      }
    }
  }

  .project-user-pill-label {
    grid-area: pillL;
  }

  .project-user-pill-container {
    grid-area: pillC;
    &.pending {
      grid-area: pillP;
    }

    display: flex;
    flex-wrap: wrap;
    .clearable-display-pill {
      width: fit-content;
      overflow: hidden;
      max-width: 100%;
      margin-left: 0;
    }
  }
}
