@import 'src/styles/includes';

.analysis-form {

  .input-container {
    max-width: rem(725);
  }

  .textarea-fieldset
  .textarea-container.analysis-title:not(.label-lifted)
  .text-area-control-label {
    font-size: var(--font-size-xl);
    font-weight: var(--font-weight-bold);

    @media screen and (max-width: 767px) {
      font-size: var(--font-size-l);
    }
  }

  .tageditor {
    @include query($sm, 'max') {
      max-width: 100%;
    }

    .input-field-container {
      @include query($sm, 'max') {
        width: 100%;
      }
    }
  }
}
