.narrative-card {
  .shared-pill {
    padding: 5px 10px;
    float: right;
    margin-right: 0;
    height: auto;

    svg {
      padding: 0;
    }
  }

  .narrative-card-bottom {
    min-height: var(--s8);
    margin: var(--s5) 0 0 0;
    padding: 0;
  }

  .narrative-card-title {
    font-size: var(--font-size-l);
    line-height: var(--lineheight-l);
  }
}

.linked-narratives .narrative-card {
  background: var(--card-background-color);
  box-shadow: var(--card-shadow);
}
