.injest-select-popover {
  button, a {
    margin-bottom: var(--s3);

    svg {
      margin-right: var(--s3);
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.object-injest-toggle {
  &.ui-button.button-link {
    background: var(--action-button-background);
    width: var(--s10);
    height: var(--s10);
    border-radius: 50%;
  }
}

.object-injest-label {
  color: var(--label-color);
  margin-top: var(--s3);
}

.object-injest-empty-modal {
  .modal-dialog {
    max-width: 620px;

    .modal-header .close {
      padding: initial;
    }

    .modal-content {
      padding: var(--container-spacing);
    }

    p, ol li {
      color: var(--label-color);
    }
  }
}
