@import 'src/styles/includes';

.filter-container_div {
  height: var(--s9);
  margin-bottom: var(--s6);
  display: flex;
  flex-direction: row;
  justify-content: center;
  overflow: hidden;

  .filter-type {
    &:hover {
      background-color: var(--color-orange-theme);
      color: var(--color-black-alt);
    }
    &--selected-div {
      border: var(--s2px) solid var(--link-color);
      color: var(--link-color);
    }
  }

  & > div {
    display: flex;
    flex: 1 1 0;
    justify-content: center;
    align-items: center;
    font-size: var(--font-size-s);
    font-weight: var(--font-weight-medium);
    letter-spacing: 0;
    line-height: var(--s6);
    color: var(--color-orange-theme);
    border: var(--s1px) solid var(--color-orange-theme);
  }

  & > div:last-of-type {
    border-top-right-radius: var(--s2px);
    border-bottom-right-radius: var(--s2px);
  }

  & > div:first-of-type {
    border-top-left-radius: var(--s2px);
    border-bottom-left-radius: var(--s2px);
  }
}

.filter-header_div {
  display: flex;
  flex-direction: row;
  font-size: var(--font-size-regular);
  font-weight: var(--font-weight-medium);
  letter-spacing: 0;
  line-height: calc(var(--s5) - var(--s2px));
  margin: 0 0 var(--s3) 0;

  .view-all_button {
    color: var(--tab-text-color-active);
    line-height: var(--s6);
    height: var(--s6);
    margin-left: auto;
    margin-top: var(--sn2px);
  }
}
