@import 'src/styles/includes';

button.close {
  color: var(--color-orange-theme);
  text-shadow: none;
  font-size: 2rem;
  opacity: 1;
}

button.close:hover {
  color: var(--color-orange-theme);
}

.modal-title {
  align-self: flex-end;
}

.back-button {
  padding-left: 0;
  margin-right: calc(var(--s9) * -1);
  padding-bottom: var(--s4);

  & + .modal-title {
    margin-top: var(--s10);
  }
}

.import-hint {
  align-items: flex-start;
  color: var(--breadcrumb-text);
  display: flex;
  font-size: var(--font-size-s);
  line-height: var(--lineheight-s);
  margin-bottom: var(--s2);

  svg {
    margin: var(--s2px) var(--s2) 0 0;
  }

  p {
    margin-bottom: 0;
  }
}

.import-callout {
  border: var(--s1px) solid var(--input-radio-border);
  padding: var(--s4);
  margin-top: var(--s6);
}

.import-modal {
  h3 {
    margin: var(--s6) 0 var(--s4) 0;
  }

  h5 {
    font-size: rem(18);
    font-weight: var(--font-weight-regular);
    line-height: var(--lineheight-medium);
    margin-bottom: var(--s6);
  }

  .modal-header {
    padding: var(--s12) var(--s12) var(--s2) var(--s12);

    .close {
      position: absolute;
      right: var(--s6);
      top: var(--s4);
    }

    .back-button {
      position: absolute;
      top: var(--s2);
      white-space: nowrap;
    }
  }
  .modal-body,
  .modal-footer {
    padding: 0 var(--s12) var(--s12) var(--s12);
  }

  .modal-actions {
    padding-top: var(--content-spacing);
  }

  .basic label:empty {
    margin: 0;
    padding: 0;
  }
}
