@import 'src/styles/includes';

.select-multiple {
  .input-field-container {
    width: 100%;
    min-width: 276px;
    border-width: var(--s1px);
    color: var(--input-color);
    display: flex;
    flex-direction: column;
    overflow: hidden;
    margin-top: 0;
    margin-bottom: 0;
    position: relative;
    overflow: visible;
  }

  .text-input-fieldset .text-input-container.basic .text-input {
    border: var(--border-standard);
    border-color: var(--input-border);
  }

  .multi-select-container {
    width: 100%;
    background: var(--input-background);
    border: var(--border-size-standard) solid var(--input-border);
    border-top: none;
    border-radius: var(--border-radius);
    border-top-right-radius: 0;
    border-top-left-radius: 0;
    font-size: var(--font-size-regular);
    font-weight: var(--font-weight-regular);
    line-height: var(--input-lineheight);
    top: 0;
    position: absolute;
    width: 100%;
    z-index: 100;

    & > .scrollable {
      overflow-y: scroll;
      max-height: 450px;
      min-height: var(--s12);
      position: relative;
    }

    & > div > div {
      padding: var(--s3);
    }

    .display-option:hover {
      background-color: var(--background-secondary-hover);
    }

    .display-option:focus {
      background-color: var(--background-secondary-hover);
      outline: none;
    }
  }
  .pills {
    margin-top: var(--s8);
  }

  .multi-select-action-button-container {
    border-top-width: 0;

    & > button {
      margin: var(--s3);
      padding: var(--s3);
    }
  }

  .item-checkbox[type='checkbox']:not(:checked),
  .item-checkbox[type='checkbox']:checked {
    position: absolute;
    left: -9999px;
  }
  .item-checkbox[type='checkbox']:not(:checked) + label,
  .item-checkbox[type='checkbox']:checked + label {
    position: relative;
    padding-left: var(--s9);
    font-weight: var(--font-weight-normal);
    cursor: pointer;
  }
  .item-checkbox[type='checkbox']:not(:checked) + label:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: var(--s6);
    height: var(--s6);
    border: 1px solid var(--input-border);
    background: var(--input-background);
    border-radius: var(--s2px);
  }
  .item-checkbox[type='checkbox']:checked + label:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: var(--s6);
    height: var(--s6);
    border: 1px solid var(--button-primary-color);
    background: var(--button-primary-color);
    border-radius: var(--s2px);
  }
  .item-checkbox[type='checkbox']:not(:checked) + label:after {
    opacity: 0;
    transform: rotate(45deg) scale(0);
  }
  .item-checkbox[type='checkbox']:checked + label:after {
    content: '';
    position: absolute;
    display: block;
    left: 9px;
    top: 3px;
    width: 6px;
    height: 14px;
    border: 2px solid white;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg) scale(1);
  }
  /* disabled checkbox */
  .item-checkbox[type='checkbox']:disabled:not(:checked) + label:before,
  .item-checkbox[type='checkbox']:disabled:checked + label:before {
    border-color: var(--icon-primary-color-disabled);
    background-color: var(--button-primary-color-disabled);
  }
  .item-checkbox[type='checkbox']:disabled:checked + label:after {
    color: var(--button-primary-color-disabled);
  }
  .item-checkbox[type='checkbox']:disabled + label {
    color: var(--button-primary-color-disabled);
  }
  /* accessibility */
  .item-checkbox[type='checkbox']:checked:focus + label:before,
  .item-checkbox[type='checkbox']:not(:checked):focus + label:before {
    border: 2px solid var(--button-primary-color);
  }

  .item-checkbox[type='checkbox']:checked ~ .list-table .list-table-row {
    border-color: red !important;
    border-style: solid;
    border-width: 2px;
  }
}
