.rich-text-card-inner {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
  max-width: 85%;

  &__delete-button {
    padding: 0;
    background-color: transparent;

    &:hover,
    &:focus {
      background-color: transparent;

      .rich-text-card-inner__delete-icon {
        .fill {
          fill: var(--icon-primary-color);
        }
      }
    }

    &:focus {
      outline: 2px auto var(--button-primary-color);
    }
  }

  &__delete-icon {
    width: var(--s5);
    height: var(--s5);

    .fill {
      fill: var(--button-primary-color);
    }
  }

  &__edit-button {
    padding: 0;
    background-color: transparent;

    &:hover,
    &:focus {
      background-color: transparent;

      .rich-text-card-inner__edit-icon {
        .fill {
          fill: var(--icon-primary-color);
        }
      }
    }

    &:focus {
      outline: 2px auto var(--button-primary-color);
    }
  }

  &__edit-icon {
    width: var(--s5);
    height: var(--s5);

    .fill {
      fill: var(--button-primary-color);
    }
  }
}
