@import 'src/styles/includes';

.analysis-card {
  display: block;
  min-height: 250px;
  border-radius: 2px;
  margin: 10px;
  width: 100%;
  padding: 0 var(--s7);

  .loader-container {
    height: auto;
    margin-left: rem(20);
  }

  .button-spinner-group {
    display: flex;
    align-items: center;

    .loading-spinner {
      margin-left: 20px;
    }
  }

  .analysis-card-header {
    padding: 12px 0;
  }

  .template-placeholder {
    background-color: var(--secondary);
    height: 6em;
    margin-bottom: var(--s2);
  }

  .status-container {
    display: flex;
    font-size: 0.825em;
    align-items: center;
    padding: 6px 0;
    svg {
      margin: 0 6px 0 2px;
    }
    .right {
      margin-left: auto;
      margin-right: 0px;
      margin-top: 0px;
    }
  }
}
