@import 'src/styles/includes';

:root {
  --all-projects-title-color: var(--color-yellow);
}

.theme-light {
  --all-projects-title-color: var(--color-orange-theme);
}

.menu-item.all-projects-menu-item.active {
  display: none;
  @media screen and (max-width: 767px) {
    display: flex;
  }

  .menu-item-arrow {
    display: none;
  }
}

.all-projects-link,
.all-projects-title {
  align-items: center;
  border-bottom: var(--s1px) solid var(--border-primary);
  color: var(--menuitem-color-label);
  display: flex;
  font-size: var(--font-size-medium);
  font-weight: var(--font-weight-bold);
  padding: var(--s5) var(--s4);

  @include query($md) {
    display: none;
  }

  span {
    color: var(--all-projects-title-color);
    font-weight: var(--font-weight-bold);
    padding-bottom: var(--s1px);
  }

  .menu-item-icon {
    margin-right: var(--s3);
  }

  &:hover {
    color: var(--menuitem-color-label);
  }
}

h4.all-projects-title {
  margin-bottom: 0;
}

.sidebar-project-name {
  @include query($md) {
    display: none;
  }

  h3 {
    @extend %clamp-1;
  }
}
